import { FC, useEffect } from "react";
import { Settings } from "@material-ui/icons";
import { IconButton } from "@mui/material";
import clsx from "clsx";

import { useApp } from "contexts/AppProvider";
import { useBusiness } from "contexts/BusinessProvider";

import classes from "./Header.module.css";

type HeaderProps = {
  selectedBusiness: {
    url: string;
    business_name: string;
    id: string;
    role: string;
  };
};

const Header: FC<HeaderProps> = ({ selectedBusiness }) => {
  const { permitSuccess, permission, doRefetchPermission } = useApp();
  const { allBusiness } = useBusiness();

  useEffect(() => {
    selectedBusiness?.id && !permitSuccess && doRefetchPermission();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedBusiness?.id]);

  return (
    <div className={clsx("mt-5", classes.wrapper)}>
      <div className={classes.header_top}>
        <div className="d-flex align-items-center">
          <div className={classes.BusinessDetail}>
            {selectedBusiness && selectedBusiness.url && (
              <img src={selectedBusiness.url} alt="profile" />
            )}
            {selectedBusiness &&
              selectedBusiness.id &&
              allBusiness &&
              allBusiness.length > 0 &&
              allBusiness
                .filter(({ business }, i) => {
                  return business.id === selectedBusiness.id;
                })
                .map(({ business }, i) =>
                  business.logo ? (
                    <img key={i} src={business.logo} alt={`profile`} />
                  ) : null
                )}
          </div>
          {selectedBusiness && (
            <h1 className={classes.SwitchBusiness}>
              {selectedBusiness.business_name}
            </h1>
          )}
        </div>
        <div className={classes.setting}>
          <IconButton
            className={classes.settingBtn}
            href="/business-settings"
            disabled={permission ? !permission.canViewAllSettings : false}
            // onClick={() => moveToSetting()}
          >
            <Settings />
          </IconButton>
        </div>
      </div>
    </div>
  );
};

export default Header;
