// imports
import { fetchBusinessTimeZone } from "../../../../api/Posts";

import calculatePostTime from "./CalculatePostTime";

// function
// const getBusinessPostTime = (scheduleDateTime, businessId) =>
const getBusinessPostTime = (scheduleDateTime) =>
  new Promise(async (resolve, reject) => {
    try {
      // fetch time zone value of business
      // const { timezone } = await fetchBusinessTimeZone(businessId);
      const res = await fetchBusinessTimeZone();
      const { timezone } = res.payload;
      const { value: offset } = timezone;
      console.log("offset", offset);
      const formattedPostTime = calculatePostTime(scheduleDateTime, offset);

      return resolve({ formattedPostTime });
    } catch (error) {
      return reject({ error });
    }
  });

// exports
export default getBusinessPostTime;
