import { createContext, useContext, useState } from "react";

const CurateContext = createContext();

const CurateProvider = ({ children }) => {
  const [postCreated, setPostCreate] = useState();

  return (
    <CurateContext.Provider value={{ postCreated, setPostCreate }}>
      {children}
    </CurateContext.Provider>
  );
};

export const useCurate = () => {
  const context = useContext(CurateContext);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of CurateProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export default CurateProvider;
