import { useEffect, useState } from "react";
import parse from "html-react-parser";
import "../../Styles/CSS/fontawesome.min.css";
import "../../Styles/CSS/solid.min.css";
import "../../Styles/CSS/custom.css";
import "../../Styles/CSS/menu.css";
// import HomeIcon from "../../assets/icons/home.svg";
import clsx from "clsx";
import classes from "./Sidebar.module.css";
//import classes from "./temp.module.css";
import Backdrop from "../UI/Backdrop/Backdrop";
import { Button } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import { useSidebar } from "../../contexts/SidebarProvider/SidebarProvider";
import { useBusiness } from "../../contexts/BusinessProvider";
import { MenuItem, Select } from "@material-ui/core";

import Setting from "../../assets/icons/settings.svg";
import { Tooltip } from "@mui/material";
import { useApp } from "contexts/AppProvider";

//const menuColor = { engage: "#F99D5B" };

const Sidebar = ({
  className,
  open,
  handleClose,
  business,
  selectedBusiness,
  location,
  switchBetweenBusiness,
}) => {
  const {
    activeMenu,
    activePlans,
    closeActiveMenu,
    openMenu,
    openSubMenu,
    catIcon,
    catColor,
    menuCategory,
    mainMenu,
    gradientColor,
    businessId,
    menuComponents,
  } = useSidebar();
  const { permitSuccess, permission, doRefetchPermission } = useApp();

  const history = useHistory();

  const { allBusiness } = useBusiness();

  const [currentPath] = useState(location.pathname);

  const urlSplit = location.pathname.split("/");

  const productName = urlSplit[1];

  const moveToSetting = () => {
    history.push(`../business/general-settings`);
  };

  const planCategory = activePlans.map(({ product }) => product);

  const switchWorkSpace = (catName) => {
    if (catName !== productName) {
      let switchPath;
      if (catName === "engage") {
        switchPath = "/engage/chat";
      }
      if (catName === "curate") {
        switchPath = "/curate/calendar";
      }
      history.push({
        pathname: switchPath,
      });
    }
  };

  const TikIcon = (color) => (
    <svg
      width="14"
      height="11"
      viewBox="0 0 14 11"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M13.0235 3.09661L5.57147 10.0519L0.976562 5.76328L3.02352 3.57011L5.57147 5.9482L10.9766 0.903442L13.0235 3.09661Z"
        fill={color.color}
      />
    </svg>
  );

  useEffect(() => {
    selectedBusiness?.id && !permitSuccess && doRefetchPermission();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedBusiness?.id]);

  const Navbar = () => {
    return (
      selectedBusiness && (
        <div
          id="sidebar"
          className={clsx(classes.sidebar, open && classes.open)}
        >
          <Backdrop
            show={(activeMenu && true) || false}
            clicked={() => {
              closeActiveMenu();
              openSubMenu(false);
            }}
          />
          <div className={classes.header_top}>
            <div className={clsx("d-flex", classes.header_child)}>
              <div className={classes.BusinessDetail}>
                {selectedBusiness &&
                  selectedBusiness.id &&
                  allBusiness &&
                  allBusiness.length > 0 &&
                  allBusiness
                    .filter(({ business }) => {
                      return business.id === selectedBusiness.id;
                    })
                    .map(({ business }, i) =>
                      business.logo ? (
                        <img
                          key={i}
                          src={business.logo}
                          alt={`Business: ${business.business_name}`}
                        />
                      ) : null
                    )}
              </div>
              {/* dropdown to switch between businesses */}
              {allBusiness && allBusiness.length > 0 && (
                <div
                  className={clsx(classes.SwitchBusiness, "switch_business")}
                >
                  <Select
                    variant="standard"
                    classes={{ root: classes.Selector }}
                    defaultValue={selectedBusiness.id}
                    inputProps={{ "aria-label": "Without label" }}
                    onChange={switchBetweenBusiness}
                    disabled={allBusiness && allBusiness.length < 1}
                  >
                    {allBusiness.map(({ business }, i) => {
                      return (
                        <MenuItem
                          key={i}
                          classes={{ root: classes.MenuItem }}
                          value={business.id}
                          selected={selectedBusiness?.id === business.id}
                        >
                          <Tooltip title={business.business_name}>
                            <h1 className={classes.h1}>
                              {business.business_name}
                            </h1>
                          </Tooltip>
                        </MenuItem>
                      );
                    })}
                  </Select>
                </div>
              )}
            </div>
            <div className={classes.setting}>
              <Button
                disabled={permission ? !permission.canViewAllSettings : false}
                className={classes.settingBtn}
                onClick={() => moveToSetting()}
              >
                <img src={Setting} alt="settings" />
              </Button>
            </div>
          </div>
          <div className={classes.innerContent}>
            <h2>Choose Workspace</h2>
            {menuCategory &&
              planCategory &&
              menuCategory.map((cat, i) =>
                planCategory.includes(cat.name) ? (
                  <div
                    key={i}
                    className={clsx(
                      classes.workspaceBtn,
                      productName === cat.name && classes.active
                    )}
                    onClick={() => switchWorkSpace(cat.name)}
                  >
                    <div className={classes.workspaceLink}>
                      {parse(cat.icon)}
                      <label style={{ color: cat.color }}>{cat.name}</label>
                      {productName === cat.name && (
                        <TikIcon color={cat.color} />
                      )}
                    </div>
                    <p>{cat.description}</p>
                  </div>
                ) : null
              )}
          </div>
          <nav className={classes.nav}>
            <ul>
              <li onClick={() => openMenu(3)}>
                <a onClick={(e) => e.preventDefault()} href="/user/profile">
                  My Profile
                </a>
              </li>
              <li>
                <a href="/signout">Sign Out</a>
              </li>
            </ul>
          </nav>
          {menuComponents.map(({ Component, id }, i) => {
            return id === activeMenu && <Component key={i} index={id} />;
          })}
        </div>
      )
    );
  };

  return (
    <>
      <Backdrop
        show={open}
        clicked={() => {
          handleClose();
          closeActiveMenu();
          openSubMenu(false);
        }}
      />
      <div
        id="page_container"
        className={`${classes.main_container} ${classes.top_bar_active} enquiry_page`}
      >
        {Navbar()}
        <div
          className={clsx(className, "menu_side_panel")}
          style={{
            background: `linear-gradient(to bottom,  ${gradientColor[productName]} 0%,#ffffff 17%, #ffffff 100%)`,
          }}
        >
          <div
            id="menu_icon"
            onClick={handleClose}
            className={`${productName}`}
            style={{ opacity: 1 }}
          >
            <span className="menu-bar menu-bar-1"></span>
            <span className="menu-bar menu-bar-2"></span>
            <span className="menu-bar menu-bar-3"></span>
            <label style={{ color: catColor[productName] }}>
              {catIcon[productName] && parse(catIcon[productName])}
              {productName}
            </label>
          </div>
          <nav
            className={clsx("nav", classes.Nav)}
            style={{ marginTop: "15px", opacity: 1 }}
          >
            <ul>
              {mainMenu &&
                mainMenu.map((menu, index) =>
                  (menu.published_business_ids.length === 0 ||
                    menu.published_business_ids.includes(Number(businessId))) &&
                  menu.published === true ? (
                    <li
                      key={index}
                      className={clsx(
                        classes.NavItem,
                        currentPath?.includes(menu?.slug) && classes.Active
                      )}
                    >
                      <a
                        href={`${menu.slug}`}
                        className={classes.anchor}
                        onClick={(e) => {
                          if (currentPath === menu.slug) {
                            return e.preventDefault();
                          }
                        }}
                      >
                        {parse(menu.icon)}
                        <p>{menu.name}</p>
                      </a>
                    </li>
                  ) : null
                )}
            </ul>
          </nav>
          <div className="left_btm_logo">
            <img src={"/images/small_logo_icon.png"} alt="easysocial.io" />
          </div>
        </div>
      </div>
    </>
  );
};

export default Sidebar;
