const findEvent = (month, date, eventList) => {
  const { events } = eventList;

  const monthList = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sept",
    "Oct",
    "Nov",
    "Dec",
  ];
  const findEventForDate = `${monthList[month - 1]}-${date}`;

  return events.filter((e) => e.day === findEventForDate);
};

const findUserCreatedEvent = (month, date, data) => {
  const monthList = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sept",
    "Oct",
    "Nov",
    "Dec",
  ];
  const matchDateFormat = String(date).length === 1 ? `0${date}` : date;
  const findEventForDate = `${monthList[month - 1]}-${matchDateFormat}`;
  const { postList } = data;

  return postList.filter((e) => e.event.day === findEventForDate);
};

export { findEvent, findUserCreatedEvent };
