/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import moment from "moment";

// dispatcher
import { useDispatch } from "react-redux";

import Header from "./Header";
import Day from "./Day";
import { useDate } from "hooks/useDate";

// request helpers
import { FetchEventsForMonth } from "api/Events/Business";

// reducers actions imports
import { addUserEvent } from "Store/calendarDashboard/events/eventSlice";

// date formatter utils imports
import { formatDate } from "utils/calendarDashboard";

// posts requests helpers
import { FetchBusinessPostsForMonth } from "api/Posts";

// strategy requests helpers
import { FetchStrategy } from "api/PostStrategy";

// posts actions imports
import { addPost } from "Store/calendarDashboard/posts/postSlice";

// contexts
import { useToastMessage } from "contexts/ToastMessageContext";
import { useBusiness } from "contexts/BusinessProvider";
import { useCurate } from "contexts/CurateProvider";

// styles
import useStyles from "../styles/muiStyles";

const Calendar = ({ setCreatePost }) => {
  const { postCreated } = useCurate();
  const classes = useStyles();
  const [nav, setNav] = useState(0);
  const [events] = useState(
    localStorage.getItem("events")
      ? JSON.parse(localStorage.getItem("events"))
      : []
  );
  const { setErrorMessage } = useToastMessage();
  const { selectedBusiness } = useBusiness();

  const { days, dateDisplay } = useDate(events, nav);
  // dispatcher
  const dispatch = useDispatch();

  useEffect(() => {
    if (selectedBusiness && selectedBusiness.id) {
      const monthNames = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];

      const [month, year] = dateDisplay.split(" ");

      let monthIndex = null;

      monthIndex = monthNames.findIndex((m) => m === month);

      if (monthIndex !== null && year) {
        // temp by nagendra
        // Fetch all the events and display in calendar + drawer
        /* FetchEventsForMonth(monthIndex, year)
          .then(data => {
            const eventsData = data.events.map(event => {
              const eventData = {};

              let { date, day } = getDayDate(event.eventdate);

              let [month, dayNum] = day.split("-");

              if (dayNum < 10) {
                dayNum = dayNum.replace(/0/g, "");
                day = `${month}-${dayNum}`;
              }

              if (event.isGlobal) {
                eventData.isGlobal = true;
              } else {
                eventData.isManual = true;
              }

              eventData.day = day;
              eventData.date = date;

              eventData.event = {
                name: event.eventName,
                desc: event.description,
              };

              return eventData;
            });

            dispatch(addUserEvent({ type: "FETCH", eventsData }));
          })
          .catch(error => {
            const { message, error: err } = error;

            if (message) {
              setErrorMessage(message);
            } else if (err) {
              setErrorMessage(err);
            } else {
              setErrorMessage(error);
            }
          }); */
        FetchBusinessPostsForMonth(monthIndex, year).then(async (res) => {
          const { payload: BusinessPosts } = res.data;
          if (BusinessPosts && BusinessPosts.length) {
            const postsListData = await Promise.all(
              BusinessPosts.map(async (postData) => {
                try {
                  const { post, type } = postData;
                  // const data = await FetchStrategy(businessID, post.strategyID);
                  const strategy = await FetchStrategy(post.post_strategy_id);
                  const [postDate] = post.schedule_date_time.split("T");

                  const dt = new Date(post.schedule_date_time);

                  /*                   const [postTime] = dt
                    .toLocaleTimeString("en-US", {
                      hour: "2-digit",
                      minute: "2-digit",
                    })
                    .split(" "); */

                  const [postTime] = moment(dt).format("hh:mm");

                  //const [postTime] = "10:00"

                  if (!strategy.success) {
                    setErrorMessage(strategy.message);
                  }

                  const strategyData = {
                    color: strategy.payload.color,
                    description: strategy.payload.description,
                    name: strategy.payload.name,
                  };

                  let postSubtitle = `Scheduled for ${postTime}`;
                  let showSuccessIcon = false;

                  if (post.platform_post_ids) {
                    const { fb, ig, tw, li } = post.platform_post_ids;

                    if (fb && ig && tw && li) {
                      postSubtitle = `Posted at ${postTime}`;
                      showSuccessIcon = true;
                    }
                  }

                  const dispatchPost = {
                    postID: post.id,
                    postDetails: {
                      ...post,
                      postImage: post.asset_url,
                      postThumbnail: post.thumbnail_url,
                      postDate,
                      postTime,
                      postDesc: post.content,
                      postSubtitle,
                      showSuccessIcon,
                    },

                    event: {
                      day: formatDate(post.schedule_date_time).split("T")[0],
                      strategy: strategyData,
                      isDraft: false,
                    },
                    post,
                  };

                  return dispatchPost;
                } catch (error) {
                  setErrorMessage(error.message);
                }
              })
            );

            if (postsListData.length && !postsListData.includes(undefined)) {
              dispatch(addPost({ type: "FETCH", posts: postsListData }));
            }
          }
        });
      }
    }
  }, [selectedBusiness, dateDisplay, postCreated]);

  // events hook
  useEffect(() => {
    localStorage.setItem("events", JSON.stringify(events));
  }, [events]);

  const WeekDays = () => (
    <div className="week-days">
      <div className="week-day">Sun</div>
      <div className="week-day">Mon</div>
      <div className="week-day">Tue</div>
      <div className="week-day">Wed</div>
      <div className="week-day">Thu</div>
      <div className="week-day">Fri</div>
      <div className="week-day">Sat</div>
    </div>
  );
  const Days = () => (
    <div className="days">
      {days.map((d, index) => (
        <Day key={index} day={d} index={index} />
      ))}
    </div>
  );
  const handleNext = () => {
    setNav((prev) => prev + 1);
  };
  const handlePrev = () => {
    setNav((prev) => prev - 1);
  };
  const getDayDate = (eventdate) => {
    const date = eventdate.toString().split("T");
    const dateFormat = formatDate(date[0]);
    return {
      date: date[0],
      day: dateFormat,
    };
  };

  return (
    <div className={classes.calendar}>
      <Header
        dateDisplay={dateDisplay}
        onNext={handleNext}
        onBack={handlePrev}
        setCreatePost={setCreatePost}
      />
      <div className="body">
        <WeekDays />
        <Days />
      </div>
    </div>
  );
};

export default Calendar;
