import { useState } from "react";
import { Drawer, IconButton } from "@material-ui/core";
import EventsCard from "../EventsCard/index";
import AddNewEventCreate from "../AddNewEventCreate/addNewEventCreate";
import classes from "./viewEvent.module.css";
import CustomIcon from "../../../shared-components/CustomIcon/CustomIcon";

const EventCreate = ({ open, onClose }) => {
  const [createNewEvent, setCreateNewEvent] = useState(false);
  return (
    <Drawer anchor="right" open={open} onClose={onClose}>
      <div role="presentation" className={classes["viewEventWrapper"]}>
        <div className={classes["viewEvent-sticky"]}>
          <div className={classes["viewEvent-Title-Wrapper"]}>
            <div className={classes["viewEvent-Title"]}>
              <div className={classes["side-close"]}>
                <IconButton onClick={onClose}>
                  <CustomIcon.BackBtnIcon />
                </IconButton>
              </div>
              <div className={classes["sideDrawer-Title"]}> Events</div>
            </div>
            <div>
              <div
                className={classes["add-new-event-button"]}
                onClick={() => setCreateNewEvent(true)}
              >
                <div className={classes["primaryButton-Text-Blue"]}>
                  Add new event
                </div>
              </div>
            </div>
          </div>
        </div>
        <EventsCard />
      </div>
      <AddNewEventCreate
        open={createNewEvent}
        onClose={() => setCreateNewEvent(false)}
      />
    </Drawer>
  );
};

export default EventCreate;
