/**
 * FB
 */

const fetchFBPostInsights = async (fbPostId) => {
  try {
    const response = await fetch(`${API_V2}/insights/fb/${fbPostId}`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-BusinessId": localStorage.getItem("businessId"),
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });
    return await response.json();
  } catch (error) {
    return { error: error.message };
  }
};

const fetchFBPostLikes = (postID, accessToken) =>
  new Promise(async (resolve, reject) => {
    try {
      const fbPostLikesRes = await axios({
        url: `${BE_URL}/fb/posts/likes`,
        method: "POST",
        data: {
          postID,
          accessToken,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: fbPostLikesData } = fbPostLikesRes;

      const { error, likesCount } = fbPostLikesData;

      if (error) {
        return reject({ error });
      }

      return resolve({ likesCount });
    } catch (error) {
      return reject({ error: error.message });
    }
  });

