const fetchFBPostInfo = (postID, accessToken) =>
  new Promise(async (resolve, reject) => {
    try {
      const fbPostInfoRes = await axios({
        url: `${BE_URL}/fb/post/info`,
        method: "POST",
        data: {
          postID,
          accessToken,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: fbPostInfoData } = fbPostInfoRes;

      const { error, reachCount, shareCount, likesCount, commentsCount } =
        fbPostInfoData;

      if (error) {
        return reject({ error });
      }

      return resolve({ reachCount, shareCount, likesCount, commentsCount });
    } catch (error) {
      return reject({ message: error.message });
    }
  });

