/**
 * TW
 */

const fetchTweetInsights = (tweetID, twToken, twTokenSecret) =>
  new Promise(async (resolve, reject) => {
    try {
      const tweetInsightsRes = await axios({
        url: `${BE_URL}/tw/tweet/insights`,
        method: "POST",
        data: {
          tweetID,
          twToken,
          twTokenSecret,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: tweetData } = tweetInsightsRes;

      const { error, tweetInfo, postURL, comments, commentsCount } = tweetData;

      if (error) {
        return reject({ error });
      }

      return resolve({ tweetInfo, postURL, comments, commentsCount });
    } catch (error) {
      return reject({ message: error.message });
    }
  });

const replyToTweet = (tweetID, twToken, twTokenSecret, comment) =>
  new Promise(async (resolve, reject) => {
    try {
      const tweetInsightsRes = await axios({
        url: `${BE_URL}/tw/tweet/reply`,
        method: "POST",
        data: {
          tweetID,
          twToken,
          twTokenSecret,
          comment,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: tweetData } = tweetInsightsRes;

      const { error, reply } = tweetData;

      if (error) {
        return reject({ message: error });
      }

      return resolve({ reply });
    } catch (error) {
      return reject({ message: error.message });
    }
  });

const fetchTweetData = (tweetID, twAccessToken, twTokenSecret) =>
  new Promise(async (resolve, reject) => {
    try {
      const fetchTweetRes = await axios({
        url: `${BE_URL}/tw/tweet/data`,
        method: "POST",
        data: {
          tweetID,
          twAccessToken,
          twTokenSecret,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: tweetsData } = fetchTweetRes;

      const { error, tweetData } = tweetsData;

      if (error) {
        return reject({ message: error });
      }

      if (tweetData) {
        return resolve({ tweetData });
      }
    } catch (error) {
      return reject({ message: error.message });
    }
  });

const fetchTweetComments = (esPostID, tweetID, twAccessToken, twTokenSecret) =>
  new Promise(async (resolve, reject) => {
    try {
      const fetchTweetRes = await axios({
        url: `${BE_URL}/tw/tweet/replies`,
        method: "POST",
        data: {
          esPostID,
          tweetID,
          twAccessToken,
          twTokenSecret,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: tweetData } = fetchTweetRes;

      const { error, success, commentsData, repliesData } = tweetData;

      if (error) {
        return reject({ message: error });
      }

      if (success === false) {
        return reject({ error: "Could not fetch tweet data!" });
      }

      if (commentsData || repliesData) {
        return resolve({ commentsData, repliesData });
      }
    } catch (error) {
      return reject({ message: error.message });
    }
  });

const fetchPostAssetType = (esPostID) =>
  new Promise(async (resolve, reject) => {
    try {
      const typeRes = await axios({
        url: `${BE_URL}/insights/asset-type`,
        method: "POST",
        data: {
          esPostID,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data } = typeRes;

      return resolve(data);
    } catch (error) {
      return reject({ message: error.message });
    }
  });

