import { useState } from "react";
import classes from "./calendarHeader.module.css";
import LeftArrow from "../../../../assets/icons/leftArrow.svg";
import RightArrow from "../../../../assets/icons/rightArrow.svg";
import PostCreate from "../../PostCreate/postCreate";
import CalendarButtonOption from "../CalendarButtonOption/calendarButtonOption";

export default function CalendarHeader({
  dateDisplay,
  handleNext,
  handlePrev,
}) {
  const [createPost, setCreatePost] = useState(false);
  const dateP = dateDisplay.split(" ");
  return (
    <div className={classes.mobileHeaderWrapper}>
      <div className={classes.calendarWrapper}>
        <img
          src={LeftArrow}
          alt="left"
          onClick={handlePrev}
          className="prevBtn"
        />

        <div className={classes.dateDisplayTitle}>
          {dateP[0].substr(0, 3)} {dateP[1]}
        </div>

        <img
          src={RightArrow}
          alt="right"
          onClick={handleNext}
          className="nextBtn"
        />
      </div>

      <div className={classes.headerButtonWrapper}>
        <div>
          <button
            className={classes.createPostButton}
            onClick={() => setCreatePost(true)}
          >
            Create Post
          </button>
        </div>
        <div className={classes.buttonOptions}>
          <CalendarButtonOption />
        </div>
      </div>

      <PostCreate open={createPost} onClose={() => setCreatePost(false)} />
    </div>
  );
}
