import { useEffect, useState } from "react";
import { findEvent, findUserCreatedEvent } from "./useEvent";
import { useSelector } from "react-redux";

export const useDate = (events, nav) => {
  const [dateDisplay, setDateDisplay] = useState("");
  const [days, setDays] = useState([]);
  const postState = useSelector((state) => state.postState);
  const eventsState = useSelector((state) => state.eventsState);
  const [paddingDays, setPaddingDays] = useState(null);

  useEffect(() => {
    const weekdays = [
      "Sunday",
      "Monday",
      "Tuesday",
      "Wednesday",
      "Thursday",
      "Friday",
      "Saturday",
    ];
    const dt = new Date();

    if (nav !== 0) {
      dt.setDate(1);
      dt.setMonth(dt.getMonth() + nav);

      // if (dt.getMonth() === 0) {
      //   dt.setFullYear(dt.getFullYear() - 1);
      //   dt.setMonth(11);
      // }
    }

    const day = dt.getDate();
    const month = dt.getMonth();
    const year = dt.getFullYear();

    const firstDayOfMonth = new Date(year, month, 1);
    const lastDayOfMonth = new Date(year, month + 1, 0);
    const daysInMonth = new Date(year, month + 1, 0).getDate();
    const dateString = firstDayOfMonth.toLocaleDateString("en-us", {
      weekday: "long",
      year: "numeric",
      month: "numeric",
      day: "numeric",
    });
    const lastDateString = lastDayOfMonth.toLocaleDateString("en-us", {
      weekday: "long",
      year: "numeric",
      month: "numeric",
      day: "numeric",
    });

    setDateDisplay(
      `${dt.toLocaleDateString("en-us", { month: "long" })} ${year}`
    );
    const paddingDays = weekdays.indexOf(dateString.split(", ")[0]);
    const marginDays =
      weekdays.length - (weekdays.indexOf(lastDateString.split(", ")[0]) + 1);

    setPaddingDays(paddingDays);

    const daysArr = [];
    let toIncrement = new Date(year, month, 0).getDate() - paddingDays; //starting date of prev month
    let toIncrementDiff = new Date(year, month, 0).getDate() - paddingDays;
    let toIncrementDiffn = new Date(year, month, 0).getDate() - paddingDays;

    for (let i = 1; i <= paddingDays + daysInMonth + marginDays; i++) {
      if (i <= paddingDays) {
        const prevString = `${month}/${
          new Date(year, month, 0).getDate() - i
        }/${year}`;

        daysArr.push({
          value: ++toIncrementDiffn,
          isPadding: true,
          globalEvent: findEvent(month, ++toIncrement, eventsState),
          event: findUserCreatedEvent(month, ++toIncrementDiff, postState),
          isCurrentDay: false,
          date: prevString,
        });
      } else if (i > paddingDays + daysInMonth) {
        const marginDay = new Date(
          year,
          month + 1,
          i - (daysInMonth + paddingDays)
        ).getDate();
        const nextString = `${month + 2}/${marginDay}/${year}`;

        daysArr.push({
          value: marginDay,
          isPadding: true,
          globalEvent: findEvent(month, marginDay, eventsState),
          event: findUserCreatedEvent(month, marginDay, postState),
          isCurrentDay: false,
          date: nextString,
        });
      } else {
        const dayString = `${month + 1}/${i - paddingDays}/${year}`;

        daysArr.push({
          value: i - paddingDays,
          isPadding: false,
          globalEvent: findEvent(month + 1, i - paddingDays, eventsState),
          event: findUserCreatedEvent(month + 1, i - paddingDays, postState),
          isCurrentDay: i - paddingDays === day && nav === 0,
          date: dayString,
        });
      }
    }

    setDays(daysArr);
  }, [events, nav, postState, eventsState]);

  return {
    paddingDays,
    days,
    dateDisplay,
  };
};
