import { makeStyles } from "@material-ui/core";

export default makeStyles((theme) => ({
  root: {
    padding: 7,
    margin: -7,
  },

  thumb: {
    width: 24,
    height: 24,
    backgroundColor: "#fff",
    boxShadow: "#000",
  },

  switchBase: {
    color: "#000 !important",
    padding: 7,
  },

  track: {
    borderRadius: 20,
    backgroundColor: "#000 !important",
  },

  checked: {
    "& $thumb": {
      backgroundColor: "#fff !important",
    },
    "& + $track": {
      opacity: "1 !important",
      backgroundColor: "#32C5FF !important",
    },
  },

  focusVisible: {},

  "new-post": {
    "& .MuiBackdrop-root": {
      backgroundColor: "#00000080",
    },

    "& .wrapper": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      width: "360px",

      "& > .header": {
        alignItems: "center",
        display: "flex",
        flex: "0 1 0",
        gap: "10px",
        padding: "36px 30px",
        width: "100%",

        "& .back-btn": {
          "&:hover": {
            backgroundColor: "transparent",
          },
        },

        "& .title": {
          color: "#24272c",
          flexGrow: 1,
          fontWeight: 500,
          fontSize: "24px",
          lineHeight: "28px",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        },
      },

      "& > form": {
        flex: "1 0 0",
        overflowY: "auto",
        padding: "0 40px",

        "& .form-title": {
          color: "#000000",
          fontSize: "16px",
          fontWeight: 500,
          lineHeight: "19px",
          marginBottom: "24px",
        },

        "& .field": {
          marginBottom: "16px",

          "&.flex": {
            display: "flex",
            justifyContent: "space-between",
            gap: "20px",

            "&.reverse": {
              justifyContent: "end",
            },
          },

          "& .field-title": {
            color: "#24272C",
            fontSize: "16px",
            fontWeight: 500,
            lineHeight: "19px",

            "&.dark": {
              color: "#000000",
            },
          },

          "& .allowed-platform": {
            display: "flex",
            gap: "9px",
            marginTop: "16px",
            marginBottom: "16px",

            "& img": {
              height: "11px",
              width: "auto",
            },
          },

          "& .sample": {
            alignItems: "center",
            display: "flex",
            justifyContent: "center",
            margin: "33px 0",
            textAlign: "center",

            "& .MuiLink-root": {
              color: "#12B0EE",
              display: "flex",
              fontSize: "16px",
              fontWeight: 500,
              gap: "16px",
              lineHeight: "19px",
            },
          },

          "& .MuiInputBase-root": {
            "&::before, &::after": {
              display: "none",
            },
          },

          "& .MuiInputBase-input": {
            color: "#24272C",
            fontSize: "15px",
            fontWeight: 400,
            lineHeight: "18px",

            "&.MuiInputBase-inputAdornedEnd": {
              textTransform: "uppercase",
            },
          },

          "& .MuiInput-root": {
            padding: "16px 14px",

            "&:not(.MuiInput-multiline)": {
              height: "52px",

              "&::before, &::after": {
                display: "none",
              },
            },
          },

          "& .MuiFormControl-root": {
            height: "52px",
            width: "100%",

            "& .MuiInputLabel-filled": {
              transform: "translate(14px, 20px) scale(1)",

              "&.MuiInputLabel-shrink": {
                transform: "translate(14px, 10px) scale(0.75)",
              },
            },
          },

          "& .MuiSelect-select": {
            paddingLeft: "14px",

            "&:focus": {
              backgroundColor: "transparent",
            },

            "& .MuiSelect-iconFilled": {
              color: "#24272C",
              right: "17px",
            },
          },

          "& .color-select": {
            "& .MuiSelect-root": {
              alignItems: "center",
              columnGap: "10px",
              display: "flex",
              justifyContent: "flex-start",

              "& .color": {
                borderRadius: "50%",
                display: "flex",
                height: "13px",
                width: "13px",
              },

              "& .label": {
                color: "#24272C",
                fontSize: "14px",
                fontWeight: 400,
                lineHeight: "21px",
              },
            },
          },

          "& .advanceSettingWrapper": {
            boxSizing: "border-box",
            borderRadius: "8px",
            backgroundColor: "#ffffff",
            padding: "9px 14px",
            border: "1px solid #ecedf2",

            "& .advanceTitle": {
              fontSize: "16px",
              lineHeight: "16px",
              textDecorationLine: "underline",
              color: "#000000",
            },

            "& .advanceTitleWrap": {
              alignItems: "center",
              display: "flex",
              columnGap: "4px",
              cursor: "pointer",
              marginTop: "9px",
              marginBottom: "11px",
            },

            ".advance-triangle": {
              position: "relative",
            },

            "& .custom-title": {
              fontSize: "12px",
              lineHeight: "12px",
              marginBottom: "5px",
              color: "#000000",
              opacity: 0.5,
            },

            "& .social-media-icons": {
              display: "flex",
              marginTop: "6px",
              columnGap: "4px",
            },
          },

          "& .append": {
            display: "flex",
            justifyContent: "space-between",
            marginTop: "9px",

            "& .label-error": {
              alignItems: "center",
              display: "flex",
              columnGap: "3px",

              "& .label": {
                color: "#F34B4B",
                fontSize: "10px",
                lineHeight: "16px",
              },
            },

            "& .label": {
              color: "#000000",
              fontSize: "14px",
              fontWeight: 400,
              lineHeight: "16px",
            },

            "& .platforms": {
              display: "flex",
              columnGap: "5px",
            },
          },

          "& .MuiLink-root": {
            color: "#282C32",
            fontSize: "14px",
            fontWeight: 400,
            lineHeight: "16px",
          },
        },

        "& .actions": {
          display: "flex",
          justifyContent: "flex-end",
          gap: "16px",
          padding: "20px 0",

          "& .MuiButton-root": {
            borderRadius: "8px",
            boxShadow: "none",
            height: "40px",
            minWidth: "84px",
            textTransform: "capitalize",

            "&.MuiButton-outlined": {
              borderColor: "#12B0EE",
              color: "#12B0EE",
            },

            "&.MuiButton-contained": {
              backgroundColor: "#12B0EE",
              color: "#ffffff",
            },
          },
        },
      },
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
}));
