import { makeStyles } from "@material-ui/core";

export default makeStyles((theme) => ({
  strategy: {
    "& .MuiBackdrop-root": {
      backgroundColor: "#00000080",
    },

    "& .wrapper": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      width: "360px",

      "& > .header": {
        alignItems: "center",
        display: "flex",
        flex: "0 1 0",
        justifyContent: "space-between",
        gap: "10px",
        padding: "36px 30px",
        width: "100%",

        "& .left": {
          display: "flex",
          flex: "1 0 0",

          "& .back-btn": {
            "&:hover": {
              backgroundColor: "transparent",
            },
          },

          "& .title": {
            color: "#24272c",
            flexGrow: 1,
            fontWeight: 500,
            fontSize: "24px",
            lineHeight: "28px",
            overflow: "hidden",
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
          },
        },

        "& .right": {
          flex: "0 1 auto",

          "& .MuiButton-root": {
            backgroundColor: "#ffffff",
            border: "1px solid #24272c33",
            borderRadius: "4px",
            color: "#12B0EE",
            height: "40px",
            textTransform: "capitalize",
          },
        },
      },

      "& > .body": {
        display: "flex",
        flexDirection: "column",
        flex: "1 0 0",

        "& .strategy-head": {
          borderBottom: "1px solid #0000001A",
          padding: "0 30px",
          flex: "0 1 0",
        },

        "& .strategy-body": {
          padding: "0 30px",
          flex: "1 0 0",
          overflowY: "auto",

          "& .MuiList-root": {
            "& .MuiListItem-root": {
              "& .MuiListItemIcon-root": {
                minWidth: "unset",
              },

              "& .info": {
                display: "flex",
                flexDirection: "column",
                flex: "1 1 0",
                justifyContent: "space-evenly",
                gap: "4px",
                padding: "12px 0",
                maxWidth: "calc(100% - 127px)",

                "& .strategy-name": {
                  borderLeft: "4px solid transparent",
                  color: "#24272C",
                  paddingLeft: "4px",
                  fontSize: "15px",
                  fontWeight: 500,
                  lineHeight: "18px",
                  textOverflow: "ellipsis",
                  overflow: "hidden",
                  whiteSpace: "nowrap",
                },

                "& .details": {
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "space-evenly",
                  gap: "4px",
                  paddingLeft: "8px",

                  "& .type": {
                    color: "#00000080",
                    fontSize: "12px",
                    fontWeight: 700,
                    lineHeight: "16px",
                  },

                  "& .desc": {
                    color: "#00000080",
                    fontSize: "12px",
                    fontWeight: 400,
                    lineHeight: "16px",
                  },

                  "& .social-platform": {
                    display: "flex",
                    gap: "9px",

                    "& img": {
                      height: "11px",
                      width: "auto",
                    },
                  },
                },
              },

              "& .actions": {
                display: "flex",
                justifyContent: "flex-end",
                gap: "20px",
                padding: "12px 0 0 10px",

                "& .MuiLink-root": {
                  color: "#24272C",
                  fontSize: "12px",
                  fontWeight: 500,
                  lineHeight: "14px",
                },
              },
            },
          },
        },
      },
    },
  },

  "new-strategy": {
    "& .MuiBackdrop-root": {
      backgroundColor: "#00000080",
    },

    "& .wrapper": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      width: "360px",

      "& > .header": {
        alignItems: "center",
        display: "flex",
        flex: "0 1 0",
        gap: "10px",
        padding: "36px 30px",
        width: "100%",

        "& .back-btn": {
          "&:hover": {
            backgroundColor: "transparent",
          },
        },

        "& .title": {
          color: "#24272c",
          flexGrow: 1,
          fontWeight: 500,
          fontSize: "24px",
          lineHeight: "28px",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        },
      },

      "& > form": {
        flex: "1 0 0",
        overflowY: "auto",
        padding: "0 40px",

        "& .form-title": {
          color: "#000000",
          fontSize: "16px",
          fontWeight: 500,
          lineHeight: "19px",
          marginBottom: "24px",
        },

        "& .field": {
          marginBottom: "16px",

          "& .social-platform": {
            display: "flex",
            gap: "9px",

            "& img": {
              height: "11px",
              width: "auto",
            },
          },

          "&.flex": {
            display: "flex",
            justifyContent: "space-between",
            gap: "20px",
          },

          "& .field-title": {
            color: "#24272C",
            fontSize: "16px",
            fontWeight: 500,
            lineHeight: "19px",

            "&.dark": {
              color: "#000000",
            },
          },

          "& .allowed-platform": {
            display: "flex",
            gap: "9px",
            marginTop: "16px",
            marginBottom: "16px",

            "& img": {
              height: "11px",
              width: "auto",
            },
          },

          "& .sample": {
            alignItems: "center",
            display: "flex",
            justifyContent: "center",
            margin: "33px 0",
            textAlign: "center",

            "& .MuiLink-root": {
              color: "#12B0EE",
              display: "flex",
              fontSize: "16px",
              fontWeight: 500,
              gap: "16px",
              lineHeight: "19px",
            },
          },

          "& .MuiInputBase-root": {
            "&::before, &::after": {
              display: "none",
            },
          },

          "& .MuiInputBase-input": {
            color: "#24272C",
            fontSize: "15px",
            fontWeight: 400,
            lineHeight: "18px",
          },

          "& .MuiInput-root": {
            padding: "16px 14px",

            "&:not(.MuiInput-multiline)": {
              height: "52px",

              "&::before, &::after": {
                display: "none",
              },
            },
          },

          "& .MuiFormControl-root": {
            height: "52px",
            width: "100%",

            "& .MuiInputLabel-filled": {
              transform: "translate(14px, 20px) scale(1)",

              "&.MuiInputLabel-shrink": {
                transform: "translate(14px, 10px) scale(0.75)",
              },
            },
          },

          "& .MuiSelect-select": {
            paddingLeft: "14px",

            "&:focus": {
              backgroundColor: "transparent",
            },

            "& .MuiSelect-iconFilled": {
              color: "#24272C",
              right: "17px",
            },
          },

          "& .color-select": {
            "& .MuiSelect-root": {
              alignItems: "center",
              columnGap: "10px",
              display: "flex",
              justifyContent: "flex-start",

              "& .color": {
                borderRadius: "50%",
                display: "flex",
                height: "13px",
                width: "13px",
              },

              "& .label": {
                color: "#24272C",
                fontSize: "14px",
                fontWeight: 400,
                lineHeight: "21px",
              },
            },
          },
        },

        "& .actions": {
          display: "flex",
          justifyContent: "flex-end",
          gap: "16px",
          padding: "20px 0",

          "& .MuiButton-root": {
            backgroundColor: "#12B0EE",
            borderRadius: "8px",
            boxShadow: "none",
            color: "#ffffff",
            height: "40px",
            minWidth: "84px",
            textTransform: "capitalize",
          },
        },
      },
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
}));
