/* eslint-disable react-hooks/exhaustive-deps */
import React, { Fragment, useState, useEffect } from "react";
// dispatcher
import { useDispatch } from "react-redux";
import { useMediaQuery } from "@material-ui/core";
import { useTheme } from "@material-ui/core/styles";

import CalendarDesktop from "../Calendar/Desktop";
import CalendarMobile from "../Mobile/CalendarMobile/calendarMobile";
import PostCreate from "../PostCreate";
// strategy request helper
import { FetchBusinessStrategiesAll } from "../../../api/PostStrategy";
// actions
import { fetchStrategies } from "../../../Store/calendarDashboard/strategy/strategySlice.js";
// contexts
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import { useBusiness } from "../../../contexts/BusinessProvider";

const Scheduler = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
  const [createPost, setCreatePost] = useState(false);
  const dispatch = useDispatch();
  const { setErrorMessage } = useToastMessage();
  const { selectedBusiness } = useBusiness();

  useEffect(() => {
    if (selectedBusiness && selectedBusiness.id) {
      FetchBusinessStrategiesAll()
        .then((res) => {
          if (!res.success) {
            return console.log("Fetch strategy error: ", res.message);
          }

          const toDispatchStrategies = res.payload.map((strategy) => {
            return {
              name: strategy.name,
              strategyID: strategy.id,
              color: strategy.color,
              desc: strategy.description,
              businessID: strategy.business_id,
              type: strategy.strategy_type,
              isDefault: false,
            };
          });

          dispatch(fetchStrategies({ data: toDispatchStrategies }));
        })
        .catch((err) => {
          const { message, error } = err;

          if (message) {
            setErrorMessage(message);
          } else if (error) {
            setErrorMessage(error);
          } else {
            setErrorMessage(err);
          }
        });
    }
  }, [selectedBusiness]);

  // useEffect(() => {
  // const businessID = "6064538f2f94727c34a4d6a9";
  // FetchBusinessStrategiesAll(businessID)
  // }, []);

  return (
    <>
      {!isMobile && (
        <Fragment>
          <CalendarDesktop setCreatePost={setCreatePost} />
          <PostCreate open={createPost} onClose={() => setCreatePost(false)} />
        </Fragment>
      )}
      {isMobile && <CalendarMobile />}
    </>
  );
};

export default Scheduler;
