// request helpers definitions
const FetchBusinessStrategiesAll = async () => {
  try {
    const res = await fetch(`${API_V2}/strategies/fetchAll`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
        "X-BusinessId": localStorage.getItem("businessId"),
      },
    });

    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

