  const FetchStrategy = async (strategyID) => {
    try {
      const res = await fetch(`${API_V2}/strategies/${strategyID}`, {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: `Bearer ${localStorage.getItem("token")}`,
          "X-BusinessId": localStorage.getItem("businessId"),
        },
      });
  
      return await res.json();
    } catch (err) {
      return { message: err.message };
    }
  };

const CreateNewPostStrategy = async (StrategyData) => {
  try {
    const response = await fetch(`${API_V2}/strategies`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "X-BusinessId": localStorage.getItem("businessId"),
        Authorization: `bearer ${localStorage.getItem("token")}`,
      },
      body: JSON.stringify(StrategyData),
    });
    return await response.json();
  } catch (err) {
    return { message: err.message };
  }
};

const EditPostStrategy = async (StrategyData, strategyID) => {
  try {
    const response = await fetch(`${API_V2}/strategies/edit/${strategyID}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        "X-BusinessId": localStorage.getItem("businessId"),
        Authorization: `bearer ${localStorage.getItem("token")}`,
      },
      body: JSON.stringify(StrategyData),
    });
    return await response.json();
  } catch (err) {
    return { message: err.message };
  }
};

const DeletePostStrategy = async (strategyID) => {
  try {
    const response = await fetch(`${API_V2}/strategies/delete/${strategyID}`, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        "X-BusinessId": localStorage.getItem("businessId"),
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });
    return await response.json();
  } catch (err) {
    return { message: err.message };
  }
};
// request helpers exports
export {
  FetchBusinessStrategiesAll,
  FetchStrategy,
  CreateNewPostStrategy,
  EditPostStrategy,
  DeletePostStrategy,
};
