import React, { useState, useEffect } from "react";
import classes from "./post.module.css";

import Facebook from "../../../assets/icons/facebookPost.svg";
import Instagram from "../../../assets/icons/instagramPost.svg";
import InfoIconLocal from "../../../assets/icons/infoIcon.svg";
import Twitter from "../../../assets/icons/twitterPost.svg";
import VisibilityOutlinedIcon from "@material-ui/icons/VisibilityOutlined";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import DoneAllIcon from "@material-ui/icons/DoneAllOutlined";
import DeleteOutlineOutlinedIcon from "@material-ui/icons/DeleteOutlineOutlined";
import PostCreate from "../PostCreate/postCreate";
import ViewPost from "../ViewPost/viewPost";
import DeletePost from "./PostDelete/postDelete";

export default function Posts({
  isMobile = false,
  day,
  postID,
  eventSubTitle,
  eventTitle,
  eventDesc = "",
  eventColor,
  eventImage = [],
  isFacebook = "",
  isTwitter = "",
  isInstagram = "",
  isLinkedin = "",
  isDraft,
  postDetails,
  imgURL,
  post,
}) {
  const [editPost, setEditPost] = useState(false);
  const [viewPost, setViewPost] = useState(false);
  const [deletePost, setDeletePost] = useState(false);
  const [mouseOver, setMouseOver] = useState(false);
  const [renderImgURL, setRenderImgURL] = useState(null);

  useEffect(() => {
    if (postDetails) setRenderImgURL(postDetails.postThumbnail);
  }, [postDetails]);

  const HoverComponent = () => {
    return (
      <div
        className={classes["hover-container"]}
        onMouseLeave={() => setMouseOver(false)}
        style={{
          width: "100%",
          display: "flex",
          justifyContent: "space-evenly",
        }}
      >
        <div
          className={classes["hover-buttons"]}
          onClick={() => {
            setViewPost(true);
            setMouseOver(false);
          }}
        >
          <VisibilityOutlinedIcon />
          <div className={classes["hover-button-text"]}>View</div>
        </div>

        {/* <div
          className={classes["hover-buttons"]}
          onClick={() => {
            setEditPost(true);
            setMouseOver(false);
          }}
        >
          <EditOutlinedIcon />
          <div className={classes["hover-button-text"]}>Edit</div>
        </div> */}
        <div className={classes["hover-buttons"]}>
          <DeleteOutlineOutlinedIcon
            onClick={() => {
              setDeletePost(true);
              setMouseOver(false);
            }}
          />
          <div className={classes["hover-button-text"]}>Delete</div>
        </div>
      </div>
    );
  };
  const eventDescToDisplay =
    eventDesc.length > 18 ? `${eventDesc.substr(0, 18)} ...` : eventDesc;

  const data = {
    ...postDetails,
  };
  return (
    <div
      className={classes["eventParentWrapper"]}
      onMouseEnter={() => setMouseOver(true)}
      onMouseLeave={() => setMouseOver(false)}
      onClick={() => setMouseOver(true)}
    >
      <div
        className={classes["eventWrapper"]}
        style={{
          borderLeft: mouseOver ? "none" : `3px solid ${eventColor.value}`,
          // display: mouseOver ? "none" : "block",
        }}
      >
        {!isMobile && (
          <div className={classes["eventTitleWrapper"]}>
            <h6
              className={
                isMobile ? classes["eventTitleMobile"] : classes["eventTitle"]
              }
            >
              {isDraft ? eventTitle : eventDescToDisplay}
            </h6>
            {isDraft && <img src={InfoIconLocal} alt="info" />}
          </div>
        )}

        <div
          className={
            Array.isArray(eventImage)
              ? !isDraft
                ? classes["post-wrapper-with-gap"]
                : classes["post-wrapper-without-gap"]
              : isMobile
              ? classes["post-image-wrapper-expand"]
              : classes["post-image-wrapper"]
          }
        >
          <div className={classes["post-image"]}>
            {/* {imgURL && (
              <img
                src={imgURL}
                alt="event"
                className={
                  isMobile
                    ? classes["event-medium-image"]
                    : classes["event-small-image"]
                }
              />
            )} */}
            {renderImgURL && (
              <img
                src={renderImgURL}
                alt="event"
                className={
                  isMobile
                    ? classes["event-medium-image"]
                    : classes["event-small-image"]
                }
              />
            )}
          </div>
          {/* <div className={classes["post-image"]}>
            {eventImage && eventImage.preview && (
              <img
                src={eventImage.preview}
                alt="event"
                className={
                  isMobile
                    ? classes["event-medium-image"]
                    : classes["event-small-image"]
                }
              />
            )}
          </div> */}
          <div className={classes["post-right-part-wrapper"]}>
            {isMobile && (
              <div
                className={classes["eventTitleWrapper"]}
                style={{ marginBottom: "5px" }}
              >
                <h6 className="eventTitle">
                  {isDraft ? eventTitle : eventDescToDisplay}
                </h6>
                {isDraft && <img src={InfoIconLocal} alt="info icon" />}
              </div>
            )}
            <div className={classes["social-available"]}>
              <div className={classes["social-icons-posted"]}>
                {isFacebook && (
                  <img
                    src={Facebook}
                    className={classes["social-av-icons"]}
                    alt="social-icons"
                  />
                )}
                {isInstagram && (
                  <img
                    src={Instagram}
                    className={classes["social-av-icons"]}
                    alt="social-icons"
                  />
                )}
                {isTwitter && (
                  <img
                    src={Twitter}
                    className={classes["social-av-icons"]}
                    alt="social-icons"
                  />
                )}
              </div>
              <div>
                {isFacebook && isInstagram && isTwitter && isLinkedin && (
                  <div className={classes["plus-social-text"]}>+1</div>
                )}
              </div>
            </div>
            {isMobile && (
              <div
                className={classes["eventSubTitleWrapper"]}
                style={{ marginTop: "2px" }}
              >
                <div className={classes["eventSubTitle"]}>{eventSubTitle}</div>

                {!isDraft && (
                  <DoneAllIcon style={{ fontSize: "14px", color: "#12B0EE" }} />
                )}
              </div>
            )}
            {/* {!isDraft && !isMobile && !Array.isArray(eventImage) && (
              <div className={classes["comment-div"]}>New 12</div>
            )} */}
          </div>
        </div>

        {!isMobile && (
          <div className={classes["eventSubTitleWrapper"]}>
            <div className={classes["eventSubTitle"]}>
              {postDetails.postSubtitle}
            </div>

            {/* TODO: Only show the icon if all platform post ids are available */}

            {postDetails.showSuccessIcon && (
              <DoneAllIcon style={{ fontSize: "14px", color: "#12B0EE" }} />
            )}
          </div>
        )}
      </div>
      {mouseOver && <HoverComponent />}
      {editPost && (
        <PostCreate
          strategy={eventTitle}
          isPopulated
          isEdit
          eventImage={eventImage}
          currentPostID={postID}
          populatingData={data}
          open={editPost}
          onClose={() => setEditPost(false)}
        />
      )}
      {viewPost && (
        <ViewPost
          open={viewPost}
          onClose={() => setViewPost(false)}
          day={day}
          eventImage={eventImage}
          isFacebook={isFacebook}
          isInstagram={isInstagram}
          isTwitter={isTwitter}
          isLinkedin={isLinkedin}
          postDetails={postDetails}
          imgURL={imgURL}
          post={post}
        />
      )}
      {deletePost && (
        <DeletePost
          open={deletePost}
          postID={postID}
          onClose={() => setDeletePost(false)}
          day={day}
          eventImage={eventImage}
          postDetails={postDetails}
          eventDesc={eventDesc}
          isFacebook={isFacebook}
          isInstagram={isInstagram}
          isTwitter={isTwitter}
          isLinkedin={isLinkedin}
          imgURL={imgURL}
        />
      )}
    </div>
  );
}
