import React, { useState } from "react";
import { Button, Grow, Link } from "@mui/material";
import GoldStar from "../../../../assets/icons/star.svg";
import CreatePost from "../../PostCreate";
export default function UserEvent({
  title,
  desc,
  date,
  isMultiple = false,
  isNotImportant = false,
}) {
  const [createPost, setCreatPost] = useState(false);
  const [showButton, setShowButton] = useState(false);

  return (
    <div
      className="userEvent"
      style={{ marginLeft: `${isMultiple ? "10px" : "18px"}` }}
    >
      <div className="titleWrapper">
        <div style={{ visibility: isNotImportant ? "hidden" : "visible" }}>
          <img src={GoldStar} alt="gold star" />
        </div>
        <Link
          component="button"
          className="eventTitle"
          onClick={() => setShowButton((prevState) => !prevState)}
          underline="none"
        >
          {title}
        </Link>
      </div>
      <Grow in={showButton}>
        <Button
          className="themeButton"
          variant="contained"
          onClick={() => setCreatPost(true)}
          style={{
            ...(!showButton ? { display: "none" } : {}),
          }}
        >
          {"Create a post"}
        </Button>
      </Grow>
      {showButton && (
        <CreatePost
          open={createPost}
          onClose={() => setCreatPost(false)}
          isPopulated
          hideBackdrop={true}
          strategy="Event"
          populatingData={{
            postDesc: desc,
            postInstruction: title,
            postDate: date,
          }}
        />
      )}
    </div>
  );
}
