import { useState, useEffect, useCallback } from "react";
import moment from "moment";

import { Drawer } from "@material-ui/core";
import { Link } from "@mui/material";
import classes from "./viewPost.module.css";
import LeftArrow from "../../../assets/icons/leftArrow.svg";
import DownloadIcon from "../../../assets/icons/Download.svg";

// requests imports
import {
  fetchFBPostInsights,
  fetchFBPostInfo,
  fetchIGPostInsights,
  fetchIGPostStats,
  // fetchTweetInsights,
  fetchTweetData,
  fetchPostAssetType,
  fetchLIPostInsights,
} from "../../../api/PostInsights";

import { fetchCredentials } from "../../../api/SocialMediaSetup";

import { fetchPlatformPostIDs } from "../../../api/Posts";

import ShowPostComment from "../ShowPostComment/showPostComment";

// contexts import
import { useToastMessage } from "../../../contexts/ToastMessageContext";

export default function ViewPost ({
  open,
  onClose,
  eventImage,
  isInstagram,
  isLinkedin,
  isTwitter,
  isFacebook,
  day,
  postDetails,
  imgURL,
  post,
}) {
  const SocialMediaStats = ({
    name,
    IGEngCount,
    IGLikesCount,
    IGCommentsCount,
    IGPermalink,
    IGReachCount,
    IGSavedCount,
    IGImpressionCount,
    FBLikesCount,
    FBCommentsCount,
    FBReachCount,
    FBShareCount,
    FBPostURL,
    retweetsCount,
    favoritesCount,
    TWPostURL,
    TWCommentsCount,
    TWQuotesCount,
    showPostComment,
    setShowPostComment,
    setSelectedPlatform,

    LILikesCount,
    LICommentsCount,
    LIPostURL,

    // mediaInfo,
  }) => {
    const handlePlatformSelect = platform => {
      setShowPostComment(false);
      setTimeout(() => {
        setShowPostComment(true);
        setSelectedPlatform(platform);
      }, 1);
    };

    if (name === "Facebook") {
      return (
        <div
          className={`${classes.cardWrapper} ${
            selectedPlatform === "facebook" ? classes.active : ""
          }`}
          onClick={() => handlePlatformSelect("facebook")}
        >
          <div className={classes.cardHeadingWrapper}>
            <div className={classes.cardTitle}>{name}</div>
            <div className={classes.cardSubTitle}>
              <a href={FBPostURL} target='_blank' rel='noreferrer'>
                View Live Post
              </a>
            </div>
          </div>
          <div className={classes.cardFooter}>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {FBLikesCount || 0}
              </span>{" "}
              Likes
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {FBCommentsCount || 0}
              </span>{" "}
              Comments
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {FBShareCount || 0}
              </span>{" "}
              Shares
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {FBReachCount || 0}
              </span>{" "}
              Reaches
            </div>
            {/* <div className={classes.cardStats} />
            <div className={classes.cardStatsLast}>More...</div> */}
          </div>
        </div>
      );
    }

    if (name === "Instagram") {
      return (
        <div
          className={`${classes.cardWrapper} ${
            selectedPlatform === "instagram" ? classes.active : ""
          }`}
          onClick={() => handlePlatformSelect("instagram")}
        >
          <div className={classes.cardHeadingWrapper}>
            <div className={classes.cardTitle}>{name}</div>
            {/* <div className={classes.cardSubTitle}>View live post</div> */}
            <div className={classes.cardSubTitle}>
              <a href={IGPermalink} target='_blank' rel='noreferrer'>
                View Live Post
              </a>
            </div>
          </div>
          <div className={classes.cardFooter}>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>{IGEngCount || 0}</span>{" "}
              Engagements
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {IGLikesCount || 0}
              </span>{" "}
              Likes
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {IGCommentsCount || 0}
              </span>{" "}
              Comments
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {IGReachCount || 0}
              </span>{" "}
              Reaches
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {IGSavedCount || 0}
              </span>{" "}
              Saves
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {IGImpressionCount || 0}
              </span>{" "}
              Impressions
            </div>
            {/* <div className={classes.cardStats} />
            <div className={classes.cardStatsLast}>More...</div> */}
          </div>
        </div>
      );
    }

    if (name === "Linkedin") {
      return (
        <div
          className={`${classes.cardWrapper} ${
            selectedPlatform === "linkedin" ? classes.active : ""
          }`}
          onClick={() => handlePlatformSelect("linkedin")}
        >
          <div className={classes.cardHeadingWrapper}>
            <div className={classes.cardTitle}>{name}</div>
            <div className={classes.cardSubTitle}>
              <a href={LIPostURL} target='_blank' rel='noreferrer'>
                View Live Post
              </a>
            </div>
          </div>
          <div className={classes.cardFooter}>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {LILikesCount || 0}
              </span>{" "}
              Likes
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {LICommentsCount || 0}
              </span>{" "}
              Comments
            </div>
          </div>
        </div>
      );
    }

    if (name === "Twitter") {
      return (
        <div
          className={`${classes.cardWrapper} ${
            selectedPlatform === "twitter" ? classes.active : ""
          }`}
          onClick={() => handlePlatformSelect("twitter")}
        >
          <div className={classes.cardHeadingWrapper}>
            <div className={classes.cardTitle}>{name}</div>
            <div className={classes.cardSubTitle}>
              <a href={TWPostURL} target='_blank' rel='noreferrer'>
                View Live Post
              </a>
            </div>
          </div>
          <div className={classes.cardFooter}>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {favoritesCount || 0}
              </span>{" "}
              Favorites
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {retweetsCount || 0}
              </span>{" "}
              Retweets
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {TWCommentsCount || 0}
              </span>{" "}
              Comments
            </div>
            <div className={classes.cardStats}>
              <span className={classes.cardStatsNumber}>
                {TWQuotesCount || 0}
              </span>{" "}
              Shares
            </div>
            {/* <div className={classes.cardStats} />
            <div className={classes.cardStatsLast}>More...</div> */}
          </div>
        </div>
      );
    }

    return <></>;
  };

  // view post states

  // FB
  const [FBLikesCount, setFBLikesCount] = useState(null);
  const [FBCommentsCount, setFBCommentsCount] = useState(null);
  const [FBReachCount, setFBReachCount] = useState(null);
  const [FBShareCount, setFBShareCount] = useState(null);
  const [FBPostURL, setFBPostURL] = useState(null);

  // IG
  const [IGEngCount, setIGEngCount] = useState(null);
  const [IGReachCount, setIGReachCount] = useState(null);
  const [IGSavedCount, setIGSavedCount] = useState(null);
  const [IGImpressionCount, setIGImpressionCount] = useState(null);

  const [IGLikesCount, setIGLikesCount] = useState(null);
  const [IGCommentsCount, setIGCommentsCount] = useState(null);
  const [IGPermalink, setIGPermalink] = useState(null);

  // TW
  const [TWFavoritesCount, setTWFavoritesCount] = useState(null);
  const [TWRetweetsCount, setTWRetweetsCount] = useState(null);
  const [TWCommentsCount, setTWCommentsCount] = useState(null);
  const [TWQuotesCount, setTWQuotesCount] = useState(null);
  const [TWPostURL, setTWPostURL] = useState(null);

  // LI
  const [LILikesCount, setLILikesCount] = useState(null);
  const [LICommentsCount, setLICommentsCount] = useState(null);
  const [LIPostURL, setLIPostURL] = useState(null);

  // comments component
  const [showPostComment, setShowPostComment] = useState(false);
  const [platformData, setPlatformData] = useState(null);
  const [selectedPlatform, setSelectedPlatform] = useState(null);
  const [credentials, setCredentials] = useState(null);
  const [platformIDs, setPlatformIDs] = useState(null);

  // image render
  const [renderImgURL, setRenderImgURL] = useState(null);

  // contexts
  const { setErrorMessage } = useToastMessage();

  useEffect(() => {}, []);

  /*   useEffect(() => {
    if (selectedPlatform) {
      const platformData = {
        platform: selectedPlatform,
        credentials: credentials[selectedPlatform],
      };

      setPlatformData(platformData);
    }
  }, [credentials, selectedPlatform]); */

  useEffect(() => {
    if (selectedPlatform) {
      const platformData = {
        platform: selectedPlatform,
        //credentials: credentials[selectedPlatform],
      };
      setPlatformData(platformData);
    }
  }, [selectedPlatform]);

  const loadInsights = async platformsIds => {
    const {
      facebook_post_id,
      instagram_post_id,
      linkedin_post_id,
    } = platformsIds;

    // facebook insights
    const fbInsights = await fetchFBPostInsights(facebook_post_id);
    if (fbInsights.success) {
      const {
        reach_count,
        likes_count,
        comments_count,
        shares_count,
        post_url,
      } = fbInsights.payload;

      setFBLikesCount(likes_count);
      setFBCommentsCount(comments_count);
      setFBShareCount(shares_count);
      setFBReachCount(reach_count);
      setFBPostURL(post_url);
    } else {
      console.log("fbInsights", fbInsights);
    }

    if (instagram_post_id) {
      const igInsights = await fetchIGPostInsights(instagram_post_id);
      if (igInsights.success) {
        const {
          likes_count: IGLikes,
          comments_count: IGCommentsCount,
          post_url: IGPostUrl,
          engagement_count,
          reach_count: IGReachCount,
          saved_count,
          impressions_count,
        } = igInsights.payload;
        setIGLikesCount(IGLikes);
        setIGCommentsCount(IGCommentsCount);
        setIGPermalink(IGPostUrl);
        setIGEngCount(engagement_count);
        setIGReachCount(IGReachCount);
        setIGSavedCount(saved_count);
        setIGImpressionCount(impressions_count);
      }
    }

    // linkedin insights
    if (linkedin_post_id) {
      const liInsights = await fetchLIPostInsights(linkedin_post_id);
      if (liInsights.success) {
        const {
          total_likes,
          total_comments,
          post_url: li_post_url,
        } = liInsights.payload;

        setLILikesCount(total_likes);
        setLICommentsCount(total_comments);
        setLIPostURL(li_post_url);
      }
    }
  };

  useEffect(() => {
    if (open && post) {
      // fetch platform post ids
      const postID = post.id;
      if (post.platform_post_ids) {
        setPlatformIDs(post.platform_post_ids);
        loadInsights(post.platform_post_ids);
      }

      /*   if (tw) {
                if (!twitter) {
                  return console.log("Linked Twitter account info not found!");
                }

               fetchTweetData(tw, twAccessToken, twTokenSecret)
                  .then(({ tweetData }) => {
                    const {
                      public_metrics: {
                        retweet_count,
                        reply_count,
                        like_count,
                        quote_count,
                      },
                      tweetURL,
                    } = tweetData;

                    setTWFavoritesCount(like_count);
                    setTWRetweetsCount(retweet_count);
                    setTWPostURL(tweetURL);
                    setTWCommentsCount(reply_count);
                    setTWQuotesCount(quote_count);
                  })
                  .catch(({ error }) => {
                    console.log("Fetch tweet data error: ", error);
                  }); 
              } */
    }
  }, [open, post]);

  const checkPostType = useCallback(async () => {
    if (postDetails) {
      try {
        setRenderImgURL(postDetails.thumbnail_url);

        /* const { assetType } = await fetchPostAssetType(postDetails._id);
        switch (assetType) {
          case "IMAGE":
            setRenderImgURL(postDetails.postImage);
            break;

          default:
            setRenderImgURL(postDetails.postThumbnail);
        } */ // by nagendra
      } catch (error) {
        const { message, error: err } = error;

        if (message) {
          setErrorMessage(message);
        } else if (err) {
          setErrorMessage(err);
        } else {
          setErrorMessage(error);
        }
      }
    }
  }, [postDetails]);

  useEffect(() => {
    checkPostType();
  }, [checkPostType]);

  return (
    <Drawer
      anchor='right'
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") && onClose()
      }
    >
      <div className={classes.drawerMain}>
        <div role='presentation' className={classes.drawerWrapper}>
          <div className={classes.drawerHeaderFix}>
            <div className={classes.drawerHeadingWrapper}>
              <div className={classes.drawerHeading}>
                <Link
                  className='side-close'
                  underline='none'
                  component='button'
                  onClick={onClose}
                >
                  <img src={LeftArrow} alt='close side drawer' />
                </Link>
                <div className='sideDrawer-Title'>{day} Post</div>
              </div>
              {/* <div>
                <div className={classes.viewPostButtonWrapper}>
                  <img src={DownloadIcon} alt="download icon" />
                  <div className={classes.blackTextButton}>Report</div>
                </div>
              </div> */}
            </div>
          </div>

          {/* post section */}

          {/* <div className={classes.postWrapper}>
            <div className={classes.imageWrapper}>
              {imgURL && (
                <img src={imgURL} alt="post" className={classes.postImage} />
              )}
            </div> */}
          <div className={classes.postWrapper}>
            <div className={classes.imageWrapper}>
              {renderImgURL && (
                <img
                  src={renderImgURL}
                  alt='post'
                  className={classes.postImage}
                />
              )}
            </div>
            {/* <div className={classes.imageWrapper}>
            {eventImage && eventImage.preview && (
              <img
                src={eventImage.preview}
                alt="post"
                className={classes.postImage}
              />
            )}
          </div> */}
            <div className={classes.postDescription}>{postDetails.content}</div>

            <div className={classes.postTiming}>
              {day} @ {moment(postDetails.schedule_date_time).format("hh:mm")}
            </div>
            {FBLikesCount !== null && FBCommentsCount !== null && (
              <hr className={classes.divider} />
            )}
            {/* {isFacebook && <SocialMediaStats name="Facebook" />}
        {isInstagram && <SocialMediaStats name="Instagram" />}
        {isTwitter && <SocialMediaStats name="Twitter" />}
        {isLinkedin && <SocialMediaStats name="Linkedin" />} */}

            {FBLikesCount !== null && FBCommentsCount !== null && (
              <SocialMediaStats
                name='Facebook'
                FBLikesCount={FBLikesCount}
                FBCommentsCount={FBCommentsCount}
                FBShareCount={FBShareCount}
                FBReachCount={FBReachCount}
                FBPostURL={FBPostURL}
                showPostComment={showPostComment}
                setShowPostComment={setShowPostComment}
                setSelectedPlatform={setSelectedPlatform}
              />
            )}
            {IGEngCount !== null && <hr className={classes.divider} />}
            {IGEngCount !== null && (
              <SocialMediaStats
                name='Instagram'
                IGEngCount={IGEngCount}
                IGLikesCount={IGLikesCount}
                IGCommentsCount={IGCommentsCount}
                IGPermalink={IGPermalink}
                IGReachCount={IGReachCount}
                IGSavedCount={IGSavedCount}
                showPostComment={showPostComment}
                setShowPostComment={setShowPostComment}
                setSelectedPlatform={setSelectedPlatform}
              />
            )}
            {TWFavoritesCount !== null && TWRetweetsCount !== null && (
              <hr className={classes.divider} />
            )}
            {TWFavoritesCount !== null && TWRetweetsCount !== null && (
              <SocialMediaStats
                name='Twitter'
                favoritesCount={TWFavoritesCount}
                retweetsCount={TWRetweetsCount}
                TWPostURL={TWPostURL}
                TWCommentsCount={TWCommentsCount}
                TWQuotesCount={TWQuotesCount}
                showPostComment={showPostComment}
                setShowPostComment={setShowPostComment}
                setSelectedPlatform={setSelectedPlatform}
              />
            )}
            {LILikesCount !== null && LICommentsCount !== null && (
              <hr className={classes.divider} />
            )}
            {LILikesCount !== null && LICommentsCount !== null && (
              <SocialMediaStats
                name='Linkedin'
                LILikesCount={LILikesCount}
                LICommentsCount={LICommentsCount}
                LIPostURL={LIPostURL}
                showPostComment={showPostComment}
                setShowPostComment={setShowPostComment}
                setSelectedPlatform={setSelectedPlatform}
              />
            )}
          </div>
        </div>
        {platformIDs && platformData && (
          <ShowPostComment
            open={showPostComment}
            onClose={() => setShowPostComment(false)}
            platformData={platformData}
            setPlatformData={setPlatformData}
            platformIDs={platformIDs}
            esPostID={post.id}
            setSelectedPlatform={setSelectedPlatform}
          />
        )}
      </div>
    </Drawer>
  );
}
