import React, { useState, useEffect } from "react";
import classes from "./advanceSettingCreate.module.css";
import { Drawer, IconButton, MenuItem, Switch } from "@material-ui/core";
import {
  CustomInputField,
  CustomTextFieldFilled,
} from "../../UI/CustomTextField/CustomTextField";
import CustomIcon from "../../../shared-components/CustomIcon/CustomIcon";

// requests imports
import { fetchSetupInfo } from "../../../api/SocialMediaSetup";

const allowedCTATypes = [
  "BOOK_TRAVEL",
  "BUY_NOW",
  "CALL_NOW",
  "DOWNLOAD",
  "GET_DIRECTIONS",
  "GET_QUOTE",
  "INSTALL_APP",
  "INSTALL_MOBILE_APP",
  "LEARN_MORE",
  "LIKE_PAGE",
  "LISTEN_MUSIC",
  "MESSAGE_PAGE",
  "NO_BUTTON",
  "OPEN_LINK",
  "PLAY_GAME",
  "SHOP_NOW",
  "SIGN_UP",
  "SUBSCRIBE",
  "USE_APP",
  "USE_MOBILE_APP",
  "WATCH_MORE",
  "WATCH_VIDEO",
];

export default function AdvanceSettingCreate({
  onClose,
  open,
  postData,
  handleInputChange,
  setPostData,
}) {
  const [enableCTA, setEnableCTA] = useState(false);
  const [enableTagging, setEnableTagging] = useState(false);
  const [connectedAccounts, setConnectedAccounts] = useState([]);
  const [platforms, setPlatforms] = useState([]);

  useEffect(() => {
    if (open) {
      fetchSetupInfo()
        .then(({ connectedAccounts }) => {
          setConnectedAccounts(connectedAccounts);

          const connectedPlatforms = connectedAccounts.map(
            (account) => account.platform
          );

          setPlatforms(connectedPlatforms);
        })
        .catch((error) => {
          console.log("error fetching setup info: ", error.error);
        });
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [open]);

  useEffect(() => {
    if (open && connectedAccounts.length) {
      const postDataObject = {
        ...postData,
      };

      connectedAccounts.forEach((account) => {
        postDataObject[`${account.platform.toLowerCase()}Date`] =
          postData.postDate;
        postDataObject[`${account.platform.toLowerCase()}Time`] =
          postData.postTime;
      });

      setPostData(postDataObject);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [open, connectedAccounts]);

  const handleChange = (e, name) => {
    if (name === "tag") {
      setEnableTagging(e.target.checked);
    } else {
      setEnableCTA(e.target.checked);
    }
  };

  return (
    <Drawer anchor={"right"} open={open} onClose={onClose} hideBackdrop={true}>
      <div className={classes["advance-create-wrap"]}>
        <div className={classes["advance-title"]}>
          <IconButton onClick={onClose}>
            <CustomIcon.BackBtnIcon />
          </IconButton>
          <div className={classes["sideDrawer-Title"]}>Advance Settings </div>
        </div>
        <div className={classes["advance-title-description"]}></div>
        <div className={classes["sideDrawerForm"]}>
          {/* FB */}
          {platforms.includes("facebook") && (
            <>
              <div className={classes["form-label"]}>Facebook</div>
              <CustomInputField
                type="date"
                className={classes["form-input"]}
                placeholder="Date"
                value={postData.facebookDate}
                onChange={(e) =>
                  handleInputChange("facebookDate", e.target.value)
                }
              />
              <CustomInputField
                type="time"
                className={classes["form-input"]}
                placeholder="Time"
                value={postData.facebookTime}
                onChange={(e) =>
                  handleInputChange("facebookTime", e.target.value)
                }
              />
              <div className={classes["form-field-combine"]}>
                <input
                  type="checkbox"
                  value={enableTagging}
                  onChange={(e) => handleChange(e, "ctaType")}
                  className={classes["input-checkbox"]}
                />
                <div className={classes["form-field-label"]}>Enable CTA</div>
              </div>
              {enableCTA && (
                <>
                  <CustomTextFieldFilled
                    select
                    label="Select CTA"
                    placeholder="Select CTA"
                    variant="filled"
                    type="select"
                    fullWidth
                    onChange={(e) =>
                      handleInputChange("ctaType", e.target.value)
                    }
                    inputProps={{ "aria-label": "Without label" }}
                    name="type"
                    style={{ marginBottom: "16px" }}
                  >
                    {allowedCTATypes.map((ctaType, index) => (
                      <MenuItem value={ctaType} key={index}>
                        {ctaType.replace("_", " ")}
                      </MenuItem>
                    ))}
                  </CustomTextFieldFilled>

                  <CustomInputField
                    type="text"
                    className={classes["form-input"]}
                    placeholder="CTA URL"
                    value={postData.ctaURL}
                    onChange={(e) =>
                      handleInputChange("ctaURL", e.target.value)
                    }
                  />

                  {/* <div className={classes["form-field-combine-end"]}>
                    <div className={classes["form-field-label"]}>
                      Save CTA for future posts
                    </div>
                    <div>
                      <Switch />
                    </div>
                  </div> */}
                </>
              )}
            </>
          )}
          {platforms.includes("instagram") && (
            <>
              <div className={classes["form-label"]}>Instagram</div>
              <CustomInputField
                type="date"
                className={classes["form-input"]}
                value={postData.instagramDate}
                onChange={(e) =>
                  handleInputChange("instagramDate", e.target.value)
                }
                placeholder="Date"
              />
              <CustomInputField
                type="time"
                className={classes["form-input"]}
                value={postData.instagramTime}
                onChange={(e) =>
                  handleInputChange("instagramTime", e.target.value)
                }
                placeholder="Time"
              />

              <div className={classes["form-field-combine"]}>
                <input
                  type="checkbox"
                  value={enableTagging}
                  onChange={(e) => handleChange(e, "tag")}
                  className={classes["input-checkbox"]}
                />
                <div className={classes["form-field-label"]}>
                  Enable Tagging
                </div>
              </div>

              {enableTagging && (
                <>
                  <CustomInputField
                    type="text"
                    className={classes["form-input"]}
                    placeholder="Tag People"
                    value={postData.tagPeople}
                    onChange={(e) =>
                      handleInputChange("tagPeople", e.target.value)
                    }
                  />
                  {postData.tagPeople !== "" && (
                    <CustomInputField
                      type="text"
                      className={classes["form-input"]}
                      placeholder="Tag Place"
                      value={postData.tagPlace}
                      onChange={(e) =>
                        handleInputChange("tagPlace", e.target.value)
                      }
                    />
                  )}
                  <div className={classes["form-field-combine-end]"]}>
                    <div className={classes["form-field-label"]}>
                      Save tagging for future posts
                    </div>
                    <div>
                      <Switch />
                    </div>
                  </div>
                </>
              )}
            </>
          )}
          {platforms.includes("linkedin") && (
            <>
              <div className={classes["form-label"]}>Linkedin</div>
              <CustomInputField
                type="date"
                className={classes["form-input"]}
                value={postData.linkedinDate}
                onChange={(e) =>
                  handleInputChange("linkedinDate", e.target.value)
                }
                placeholder="Date"
              />

              <CustomInputField
                value={postData.linkedinTime}
                onChange={(e) =>
                  handleInputChange("linkedinTime", e.target.value)
                }
                type="time"
                className={classes["form-input"]}
                placeholder="Time"
              />
            </>
          )}
          {platforms.includes("twitter") && (
            <>
              <div className={classes["form-label"]}>Twitter</div>
              <CustomInputField
                type="date"
                className={classes["form-input"]}
                value={postData.twitterDate}
                onChange={(e) =>
                  handleInputChange("twitterDate", e.target.value)
                }
                placeholder="Date"
              />

              <CustomInputField
                type="time"
                className={classes["form-input"]}
                value={postData.twitterTime}
                onChange={(e) =>
                  handleInputChange("twitterTime", e.target.value)
                }
                placeholder="Time"
              />
            </>
          )}
        </div>
        <div className={classes.buttonFlexWrapper}>
          <button className={classes["save-setting-button"]} onClick={onClose}>
            Save the settings
          </button>
        </div>
      </div>
    </Drawer>
  );
}
