import { makeStyles } from "@material-ui/core";

export default makeStyles((theme) => ({
  container: {
    height: "100vh",
    display: "flex",

    "& .sidebar": {
      marginLeft: "15%",
      borderRadius: "8px",
      height: "100%",
    },

    "& .inner": {
      width: "100%",
    },

    "& .content": {
      backgroundColor: "#fff",
      height: "calc(100vh - 65px)",
      overflow: "hidden",
      overflowY: "auto",
      width: "100%",
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    container: {
      backgroundColor: "#f6f8fb",

      "& .inner": {
        alignItems: "center",
        display: "flex",
        height: "100vh",
        margin: "auto",
      },

      "& .content ": {
        borderRadius: "8px",
        height: "calc(100% - 20px)",
        userSelect: "none",
        margin: "10px 10px 10px 20px",
        width: "100%",
      },

      "& .grid-ontainer": {
        width: "95%",
        height: "calc(100% - 200px)",
        position: "relative",
        margin: "auto",
        backgroundColor: "#fff",
        paddingTop: "0px",

        "& .add": {
          display: "none",
        },
      },
    },
  },
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
}));
