import { makeStyles } from "@material-ui/core";

export default makeStyles((theme) => ({
  calendar: {},

  deletePostWrapper: {
    padding: "20px",

    "& .cardDescription": {
      display: "flex",
      justifyContent: "space-between",
      marginTop: "13px",
      marginBottom: "9px",
    },

    "& .cardTitle": {
      fontSize: "13px",
      lineHeight: "15px",
      color: "#000000",
    },

    "& .cardSubTitle": {
      fontSize: "10px",
      lineHeight: "12px",
      textAlign: "right",
      color: "#000000",
      opacity: 0.5,
    },

    "& .cardSocialWrapper": {
      display: "flex",
      columnGap: "6px",
      alignItems: "center",
    },

    "& .footer": {
      marginBottom: "12px",
    },

    "& .footerText": {
      fontWeight: "500",
      fontSize: "17px",
      lineHeight: "20px",
      color: "#000000",
    },

    "& .footerButton": {
      display: "flex",
      columnGap: "4px",
      marginBottom: "8px",
      alignItems: "center",
    },

    "& .deletePostButton": {
      background: "#32c5ff",
      borderRadius: "6px",
      padding: "10px 21px",
      cursor: "pointer",
      fontWeight: "bold",
      outline: "none",
      border: "1px solid #32c5ff",
      fontSize: "14px",
      lineHeight: "16px",
      textAlign: "center",
      color: "#ffffff",
    },

    "& .cancelPostButton": {
      backgroundColor: "#ffffff",
      border: "1px solid #ffffff",
      cursor: "pointer",
      padding: "10px 21px",
      fontWeight: 500,
      fontSize: "14px",
      lineHeight: "16px",
      textAlign: "center",
      color: "#24272c",
      opacity: 0.7,
    },

    "& .socialIcons": {
      width: "20px",
      height: "20px",
    },

    "& .plusIconText": {
      fontSize: "14px",
      lineHeight: "11px",
      textDecorationLine: "underline",
      color: "#000000",
    },

    "& .postImage": {
      height: "220px",
      width: "100%",
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    calendar: {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      paddingBottom: "5px",

      "& .date-bar": {
        borderBottom: "1px solid #24272C1A",
        display: "flex",
        justifyContent: "space-between",
        padding: "20px 30px",
        flex: "0 1 0",

        "& .header": {
          alignItems: "center",
          display: "flex",
          gap: "10px",

          "& .date-title": {
            color: "#24272C",
            fontSize: "20px",
            fontWeight: 700,
            lineHeight: "23px",
          },

          "& .MuiIconButton-root": {
            height: "32px",
            width: "32px",
          },
        },

        "& .actions": {
          alignItems: "center",
          columnGap: "60px",
          display: "flex",

          "& .MuiLink-root": {
            alignItems: "center",
            color: "#12B0EE",
            columnGap: "10px",
            fontSize: "16px",
            fontWeight: 500,
            lineHeight: "19px",
          },

          "& .MuiButton-root": {
            alignItems: "center",
            border: "1px solid #12B0EE",
            borderRadius: "8px",
            color: "#12B0EE",
            height: "40px",
          },
        },
      },

      "& .body": {
        flex: "1 1 0",
        overflow: "auto",
        padding: "20px 30px",

        "& .week-days": {
          color: "#24272C",
          display: "grid",
          gridTemplateColumns: "repeat(7, minmax(0, 1fr))",
          gridGap: "16px",
          marginBottom: "10px",
          width: "100%",

          "& .week-day": {
            fontSize: "18px",
            fontWeight: 500,
            lineHeight: "21px",
            marginBottom: "5px",
          },
        },

        "& .days": {
          display: "grid",
          gridTemplateColumns: "repeat(7, minmax(0, 1fr))",
          gridGap: "13px",

          "& .day": {
            borderTop: "3px solid #24272c",
            minHeight: "150px",
            padding: "0 3px",
            width: "100%",

            "&:hover": {
              backgroundColor: "#f4f4f5",
            },

            "&.padding": {
              borderTopColor: "rgba(36, 39, 44, 0.3)",

              "& .date": {
                "&.paddingDate": {
                  opacity: 0.3,
                },
              },
            },

            "& .date": {
              alignItems: "flex-start",
              color: "#24272c",
              display: "flex",
              gap: "6px",
              marginTop: "14px",

              "&.currentDay": {
                color: "#12B0EE",
              },

              "& h4": {
                fontSize: "20px",
                fontWeight: 500,
                lineHeight: "23px",
              },
            },

            "& .eventParentWrapper": {
              borderLeft: `3px solid transparent`,
              cursor: "pointer",
              marginTop: "4px",
              marginBottom: "20px",
              position: "relative",

              "& .eventTitleMobile": {
                fontWeight: 500,
                fontSize: "12px",
                lineHeight: "14px",
                textAlign: "center",
                color: "#000000",
              },

              "& .eventWrapper": {
                padding: "6px 4px",
                cursor: "pointer",
                boxSizing: "content-box",
                display: "flex",
                flexDirection: "column",
              },

              "& .eventWrapperGlobal": {
                marginTop: "16px",
                marginBottom: "16px",
                padding: "6px",

                boxSizing: "border-box",
                display: "flex",
                flexDirection: "column",
              },

              "& .eventTitle": {
                color: "#24272c",
                fontWeight: "900",
                fontSize: "11px",
                lineHeight: "13px",
                margin: 0,
              },

              "& .eventSubTitle": {
                fontSize: "9px",
                fontWeight: 500,
                lineHeight: "11px",
                color: "#24272C",

                "&.half-tone": {
                  color: "#24272C80",
                },
              },

              "& .event-image": {
                width: "36px",
                height: "36px",
                borderRadius: "2px",
              },

              "& .event-image-md": {
                width: "100%",
                height: "auto",
                borderRadius: "2px",
              },

              "& .post-desc": {
                color: "#24272C",
                fontSize: "12px",
                fontWeight: 500,
                lineHeight: "14px",
                textOverflow: "ellipsis",
                overflow: "hidden",
                whiteSpace: "nowrap",
                maxWidth: "calc(100% - 8px)",
              },

              "& .post-image-wrapper": {
                display: "flex",
                margin: "6px 0px",
                columnGap: "6px",
                position: "relative",
              },

              "& .post-image-wrapper-expand": {
                display: "flex",
                margin: "6px 0px",
                columnGap: "10px",
                position: "relative",
              },

              "& .post-image": {
                display: "flex",
                borderRadius: "5px",
                overflow: "hidden",
              },

              "& .hover-container": {
                alignItems: "center",
                backgroundColor: "#00000080",
                borderRadius: "5px",
                color: "#ffffff",
                columnGap: "2px",
                display: "none",
                height: "100%",
                justifyContent: "space-evenly",
                left: 0,
                paddingTop: "24px",
                paddingBottom: "17px",
                paddingLeft: "9px",
                paddingRight: "10px",
                position: "absolute",
                top: 0,
                width: "100%",
                zIndex: 111,
              },

              "&:hover": {
                "& .hover-container": {
                  display: "flex",
                },
              },

              "& .hover-buttons": {
                alignItems: "center",
                display: "flex",
                flexDirection: "column",
                rowGap: "6px",
              },

              "& .hover-button-text": {
                fontWeight: 500,
                fontSize: "14px",
                lineHeight: "16px",
                textAlign: "center",
              },

              "& .hover-buttons:hover": {
                color: "#12b0ee",
              },

              "& .social-av-icons": {
                width: "11.6px",
                height: "11.6px",
              },

              "& .eventSubTitleWrapper": {
                alignItems: "center",
                columnGap: "4px",
                display: "flex",
                marginTop: "6px",

                "& .social-available": {
                  flex: "1 1 0",
                },
              },

              "& .eventTitleWrapper": {
                display: "flex",
                columnGap: "4px",
                alignItems: "center",
                justifyContent: "space-between",
              },

              "& .plus-social-text": {
                fontWeight: "normal",
                fontSize: "9px",
                lineHeight: "11px",
                textDecorationLine: "underline",
                color: "#000000",
              },

              "& .social-available": {
                display: "flex",
                alignItems: "flex-start",
                columnGap: "4px",
              },

              "& .social-icons-posted": {
                display: "flex",
                gap: "5px",
              },

              "& .post-wrapper-with-gap": {
                display: "flex",

                flexDirection: "column",
                rowGap: "27px",
              },

              "& .post-wrapper-without-gap": {
                display: "flex",

                flexDirection: "column",
              },

              "& .post-right-part-wrapper": {
                display: "flex",
                flexDirection: "column",
              },

              "& .comment-div": {
                backgroundColor: "#12b0ee",
                padding: "2px 5px",
                borderRadius: "10px",

                textAlign: "center",
                fontWeight: 500,
                fontSize: "8px",
                lineHeight: "9px",
                boxSizing: "border-box",
                color: "#ffffff",
                marginTop: "8px",
                width: "37px",
                marginBottom: "4px",
                position: "relative",
              },

              "& .comment-div:after": {
                content: "''",
                position: "absolute",
                left: "28px",
                bottom: "-4px",
                right: "0px",
                margin: "0 auto",
                width: 0,
                height: 0,
                borderTop: "7px solid #12b0ee",
                borderRight: "7px solid transparent",
              },
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
}));
