import { useState, useEffect } from "react";
import {
  Drawer,
  FormControl,
  IconButton,
  Select,
  MenuItem,
} from "@material-ui/core";
import Instagram from "assets/icons/instagram.svg";
import { useSelector } from "react-redux";
import Facebook from "assets/icons/facebook.svg";
import Twitter from "assets/icons/twitter.svg";
import Triangle from "assets/icons/Triangle.svg";
import Linkedin from "assets/icons/linkedin.svg";
import { addPost, updatePost } from "Store/calendarDashboard/posts/postSlice";
import { useDispatch } from "react-redux";
import AdvanceSettingCreate from "../AdvanceSettingCreate/advanceSettingCreate";
import classes from "./postCreate.module.css";
import Warn from "assets/icons/warn.svg";
import { formatDate } from "utils/calendarDashboard";
import Drag from "./Drag/drag";
import { v4 as uuid } from "uuid";
import CustomIcon from "shared-components/CustomIcon/CustomIcon";
import { CustomInputField } from "../../UI/CustomTextField/CustomTextField";

// request helper imports
import { FetchBusinessStrategiesAll } from "api/PostStrategy";

// reducer imports
import { fetchStrategies } from "Store/calendarDashboard/strategy/strategySlice";

import { useToastMessage } from "contexts/ToastMessageContext";
import { useBusiness } from "contexts/BusinessProvider";
import { useCurate } from "contexts/CurateProvider";

// posts request helpers
import {
  UpdateScheduledAssetPost,
  ScheduleNewImagePost,
  ScheduleNewVideoPost,
  ScheduleNewGifPost,
} from "api/Posts";

// utils imports
import validatePostAsset from "./utils/ValidatePostAsset";
import getBusinessPostTime from "./utils/GetBusinessPostTime";
import validatePostDateTime from "./utils/ValidatePostDateTime";

const PostCreate = ({
  open,
  onClose,
  isEdit = false,
  isPopulated = false,
  eventImage,
  currentPostID,
  populatingData = {},
  strategy = "",
}) => {
  const { setPostCreate } = useCurate();
  const dispatch = useDispatch();
  const [advanceSetting, setAdvanceSetting] = useState(false);
  const [, setShowDropDown] = useState(false);
  const strategyState = useSelector((state) => state.strategyState);

  const [showTwitterInputBox, setShowTwitterInputBox] = useState(false);

  const initialStrategy = strategy ? strategyState.strategyList[0] : "";
  const [selectedStrategy, setSelectedStrategy] = useState(initialStrategy);
  const [defaultStrategy, setDefaultStrategy] = useState(null);

  const [allStrategies, setAllStrategies] = useState([]);
  const [submitDisable, setSubmitDisable] = useState(false);

  // const [selectedStrategy, setSelectedStrategy] = useState(null);

  const [assetType, setAssetType] = useState(null);

  const [files, setFiles] = useState([]);

  const { setErrorMessage, setSuccessMessage } = useToastMessage();

  const { selectedBusiness } = useBusiness();

  const initialData = isPopulated
    ? { ...populatingData }
    : {
        postInstruction: "",
        postDesc: "",
        twitterDesc: "",
        postDate: "",
        postTime: "",
        isAdvanceActivated: advanceSetting,
        facebookDate: "",
        facebookTime: "",
        instagramTime: "",
        instagramDate: "",
        twitterTime: "",
        twitterDate: "",
        linkedinDate: "",
        ctaType: "",
        ctaURL: "",
        tagPeople: "",
        tagPlace: "",
        linkedinTime: "",
        postID: "",
      };
  const [postData, setPostData] = useState({
    ...initialData,
  });

  const showTwitterButtonHandler = () => {
    const text = postData.postDesc.substr(0, 180);
    setPostData({
      ...postData,
      twitterDesc: text,
    });
    setShowTwitterInputBox(true);
  };

  // fetch all the strategies

  useEffect(() => {
    if (open) {
      setSubmitDisable(false);
    }
  }, [open]);

  useEffect(() => {
    console.log("hii2");
    if (selectedBusiness && selectedBusiness.id) {
      FetchBusinessStrategiesAll()
        .then((data) => {
          const { error } = data;

          if (error) {
            return console.log("Fetch strategy error: ", error);
          }

          const toDispatchStrategies = data.strategies.map((strategy) => {
            return {
              name: strategy.name,
              strategyID: strategy.id,
              color: strategy.color,
              desc: strategy.description,
              businessID: strategy.business_id,
              isDefault: strategy.is_default || false,
              type: strategy.strategy_type,
            };
          });

          const eventStrategy = toDispatchStrategies.find(
            (s) => s.name === "Event"
          );

          setDefaultStrategy(eventStrategy);

          setAllStrategies(toDispatchStrategies);

          dispatch(fetchStrategies({ data: toDispatchStrategies }));
        })
        .catch((err) => {
          console.log("Fetch strategy err: ", err);
        });
    }
  }, [selectedBusiness]);

  useEffect(() => {
    let [month, day, year] = new Date().toLocaleDateString().split("/");

    month = month.length < 2 ? `0${month}` : month;
    day = day.length < 2 ? `0${day}` : day;

    const requiredDate = `${year}-${month}-${day}`;

    let [hour, min, sec] = new Date(Date.now() + 1000 * 60 * 10)
      .toLocaleTimeString()
      .split(":");

    const meridian = sec.split(" ").pop();

    // hour = meridian === "PM" ? parseInt(hour) + 12 + "" : hour;

    if (meridian === "PM") {
      if (parseInt(hour) !== 12) {
        hour = parseInt(hour) + 12 + "";
      }
    }

    hour = hour.length < 2 ? `0${hour}` : hour;
    min = min.length < 2 ? `0${min}` : min;

    const requiredTime = `${hour}:${min}`;

    setPostData({
      ...postData,
      postDate: requiredDate,
      postTime: requiredTime,
    });

    // }, [open, postData]);
    // eslint-disable-next-line
  }, [open]);

  const handleFinalSubmit = async (event, action) => {
    event.preventDefault();

    setSubmitDisable(true);

    const businessID = localStorage.getItem("businessId");

    // const businessID = "6064538f2f94727c34a4d6a9";

    if (
      postData.postDate === "" ||
      postData.postTime === "" ||
      postData.postDesc === ""
      // Object.keys(selectedStrategy).length === 0
      // selectedStrategy.length === 0
    ) {
      setSubmitDisable(false);
      setErrorMessage("fields can not be empty");
    } else {
      if (isEdit) {
        const fbScheduleDateTime =
          postData.facebookDate && postData.facebookTime
            ? `${postData.facebookDate}T${postData.facebookTime}:00`
            : "";

        const igScheduleDateTime =
          postData.instagramDate && postData.instagramTime
            ? `${postData.instagramDate}T${postData.instagramTime}:00`
            : "";

        const twScheduleDateTime =
          postData.twitterDate && postData.twitterTime
            ? `${postData.twitterDate}T${postData.twitterTime}:00`
            : "";

        const liScheduleDateTime =
          postData.linkedinDate && postData.linkedinTime
            ? `${postData.linkedinDate}T${postData.linkedinTime}:00`
            : "";

        const formData = new FormData();

        formData.append("asset", files[0]);
        formData.append("postID", currentPostID);
        formData.append("action", "update");
        formData.append("businessID", businessID);
        // formData.append("strategyID", "617bb84fda289f37b08a995a");
        formData.append("strategyID", selectedStrategy.strategyID);
        formData.append("instructions", postData.postInstruction);
        formData.append("content", postData.postDesc);
        formData.append(
          "scheduleDateTime",
          `${postData.postDate}T${postData.postTime}:00`
        );
        formData.append("fbScheduleDateTime", fbScheduleDateTime);
        formData.append("igScheduleDateTime", igScheduleDateTime);
        formData.append("twScheduleDateTime", twScheduleDateTime);
        formData.append("liScheduleDateTime", liScheduleDateTime);
        formData.append("cta_type", postData.ctaType);
        formData.append("cta_value", postData.ctaURL);
        formData.append("igUsernames", postData.tagPeople);

        try {
          const { UpdatePostData } = await UpdateScheduledAssetPost(formData);

          console.log("Update post data: ", UpdatePostData);

          setSuccessMessage(`Scheduled post updated!`);
        } catch (error) {
          console.log("Update post error: ", error);

          const { message, error: err } = error;

          if (message) {
            setErrorMessage(message);
          } else if (err) {
            setErrorMessage(err);
          } else {
            setErrorMessage(error);
          }
        }

        dispatch(
          updatePost({
            postID: currentPostID,
            postDetails: { ...postData, postImage: files[0] },
            event: {
              day: formatDate(postData.postDate),
              strategy: selectedStrategy,
              isDraft: false,
            },
          })
        );

        setSubmitDisable(false);
        setPostCreate(Date.now());
      } else {
        // Send create post request

        const fbScheduleDateTime =
          postData.facebookDate && postData.facebookTime
            ? `${postData.facebookDate}T${postData.facebookTime}:00`
            : "";

        const igScheduleDateTime =
          postData.instagramDate && postData.instagramTime
            ? `${postData.instagramDate}T${postData.instagramTime}:00`
            : "";

        const twScheduleDateTime =
          postData.twitterDate && postData.twitterTime
            ? `${postData.twitterDate}T${postData.twitterTime}:00`
            : "";

        const liScheduleDateTime =
          postData.linkedinDate && postData.linkedinTime
            ? `${postData.linkedinDate}T${postData.linkedinTime}:00`
            : "";

        const formData = new FormData();

        const scheduleDateTime = `${postData.postDate}T${postData.postTime}:00`;

        // console.log("scheduleDateTime: ", scheduleDateTime);

        // convert to business post time
        let scheduledFormattedTime;
        let fbFormattedTime;
        let igFormattedTime;
        let twFormattedTime;
        let liFormattedTime;

        const timeConversionArray = [];

        timeConversionArray.push(
          getBusinessPostTime(scheduleDateTime)
            .then((scheduledTimeData) => {
              scheduledFormattedTime = scheduledTimeData.formattedPostTime;
            })
            .catch((error) => {
              console.log("Business post time format error: ", error);
            })
        );

        if (fbScheduleDateTime) {
          timeConversionArray.push(
            getBusinessPostTime(fbScheduleDateTime)
              .then((scheduledTimeData) => {
                fbFormattedTime = scheduledTimeData.formattedPostTime;
              })
              .catch((error) => {
                console.log("Business post time format error: ", error);
              })
          );
        }

        if (igScheduleDateTime) {
          timeConversionArray.push(
            getBusinessPostTime(igScheduleDateTime)
              .then((scheduledTimeData) => {
                igFormattedTime = scheduledTimeData.formattedPostTime;
              })
              .catch((error) => {
                console.log("Business post time format error: ", error);
              })
          );
        }

        if (twScheduleDateTime) {
          timeConversionArray.push(
            getBusinessPostTime(twScheduleDateTime)
              .then((scheduledTimeData) => {
                twFormattedTime = scheduledTimeData.formattedPostTime;
              })
              .catch((error) => {
                console.log("Business post time format error: ", error);
              })
          );
        }

        if (liScheduleDateTime) {
          timeConversionArray.push(
            getBusinessPostTime(liScheduleDateTime)
              .then((scheduledTimeData) => {
                liFormattedTime = scheduledTimeData.formattedPostTime;
              })
              .catch((error) => {
                console.log("Business post time format error: ", error);
              })
          );
        }

        try {
          await Promise.all(timeConversionArray);
        } catch (error) {
          console.log("Timezone formatting error: ", error);
        }

        // validate times
        if (
          scheduledFormattedTime &&
          !validatePostDateTime(scheduledFormattedTime)
        ) {
          setSubmitDisable(false);
          return setErrorMessage(
            `Post schedule time should be at least 10 mins from now!`
          );
        }

        if (fbFormattedTime && !validatePostDateTime(fbFormattedTime)) {
          setSubmitDisable(false);
          return setErrorMessage(
            `Facebook schedule time should be at least 10 mins from now!`
          );
        }

        if (igFormattedTime && !validatePostDateTime(igFormattedTime)) {
          setSubmitDisable(false);
          return setErrorMessage(
            `Instagram schedule time should be at least 10 mins from now!`
          );
        }

        if (twFormattedTime && !validatePostDateTime(twFormattedTime)) {
          setSubmitDisable(false);
          return setErrorMessage(
            `Twitter schedule time should be at least 10 mins from now!`
          );
        }

        if (liFormattedTime && !validatePostDateTime(liFormattedTime)) {
          setSubmitDisable(false);
          return setErrorMessage(
            `LinkedIn schedule time should be at least 10 mins from now!`
          );
        }

        formData.append("asset", files[0]);
        // formData.append("strategyID", selectedStrategy);
        // formData.append("strategyID", "621f40b2f004b8397418c407");
        formData.append("strategyID", selectedStrategy);
        // formData.append("action", "create");
        formData.append("action", action);
        formData.append("instructions", postData.postInstruction);
        formData.append("content", postData.postDesc);
        formData.append("scheduleDateTime", scheduledFormattedTime);

        if (fbFormattedTime) {
          formData.append("fbScheduleDateTime", fbFormattedTime);
        }

        if (igFormattedTime) {
          formData.append("igScheduleDateTime", igFormattedTime);
        }

        if (twFormattedTime) {
          formData.append("twScheduleDateTime", twFormattedTime);
        }

        if (liFormattedTime) {
          formData.append("liScheduleDateTime", liFormattedTime);
        }

        formData.append("cta_type", postData.ctaType);
        formData.append("cta_value", postData.ctaURL);
        formData.append("igUsernames", postData.tagPeople);

        formData.append("platforms", "facebook");
        formData.append("platforms", "instagram");
        formData.append("platforms", "twitter");
        formData.append("platforms", "linkedin");

        try {
          let schedulePostData;

          if (assetType && assetType === "image") {
            const { AssetPostData } = await ScheduleNewImagePost(formData);
            schedulePostData = AssetPostData;
          } else if (assetType && assetType === "video") {
            const { AssetPostData } = await ScheduleNewVideoPost(formData);
            schedulePostData = AssetPostData;
          } else if (assetType && assetType === "gif") {
            const { AssetPostData } = await ScheduleNewGifPost(formData);
            schedulePostData = AssetPostData;
          } else {
            setSubmitDisable(false);
            if (files.length === 0) {
              return setErrorMessage("Please provide a post asset!");
            }

            return setErrorMessage("Invalid asset type!");
          }

          const { error, PSQDocs, postDraft, NewScheduledAssetPost } =
            schedulePostData;

          if (error) {
            setSubmitDisable(false);
            return setErrorMessage(error);
          }

          let addPostData = {};

          if (action === "draft" && postDraft) {
            const dt = new Date(postDraft.scheduleDateTime);

            const [postTime] = dt
              .toLocaleTimeString("en-US", {
                hour: "2-digit",
                minute: "2-digit",
              })
              .split(" ");

            let postSubtitle = `Drafted for ${postTime}`;

            // find the strategy data using strategy ID
            // const strategy = allStrategies.find(
            //   (s) => s.strategyID === selectedStrategy
            // );

            addPostData = {
              postID: postDraft._id,
              postDetails: {
                ...postData,
                postImage: postDraft.thumbnailURL || postDraft.assetURL,
                postSubtitle,
                showSuccessIcon: false,
              },
              // event: {
              //   day: formatDate(postData.postDate),
              //   strategy: selectedStrategy,
              //   isDraft: false,
              // },
              event: {
                day: formatDate(postData.postDate),
                // strategy: {
                //   strategyID: selectedStrategy,
                // },
                // strategy: defaultStrategy,
                strategy: selectedStrategy,
                isDraft: true,
              },
              post: {
                _id: postDraft._id,
              },
            };
          }

          if (action === "create" && NewScheduledAssetPost) {
            // const dt = new Date(schedulePostData.PSQDocs[0].scheduleDateTime);
            const dt = new Date(
              schedulePostData.NewScheduledAssetPost.scheduleDateTime
            );

            const [postDate] =
              NewScheduledAssetPost.scheduleDateTime.split("T");

            const [postTime] = dt
              .toLocaleTimeString("en-US", {
                hour: "2-digit",
                minute: "2-digit",
              })
              .split(" ");

            let postSubtitle = `Scheduled for ${postTime}`;

            // find the strategy data using strategy ID
            const postStrategy = allStrategies.find(
              (s) => s.strategyID === selectedStrategy
            );

            addPostData = {
              postID: schedulePostData.NewScheduledAssetPost._id,
              postDetails: {
                ...NewScheduledAssetPost,
                postDate,
                postTime,
                postImage: schedulePostData.NewScheduledAssetPost.assetURL,
                postThumbnail:
                  schedulePostData.NewScheduledAssetPost.thumbnailURL,
                postDesc: schedulePostData.NewScheduledAssetPost.content,
                postSubtitle,
                showSuccessIcon: false,
              },
              event: {
                day: formatDate(postData.postDate),
                strategy: postStrategy,
                isDraft: false,
              },
              post: {
                _id: schedulePostData.NewScheduledAssetPost._id,
              },
            };

            // if (action === "create" && schedulePostData.PSQDocs.length) {
            if (action === "create" && schedulePostData.NewScheduledAssetPost) {
              setSuccessMessage(`New ${assetType} post scheduled!`);
              setPostCreate(Date.now());
            }

            if (action === "draft" && postDraft) {
              setSuccessMessage(`New ${assetType} post drafted!`);
            }

            dispatch(addPost({ type: "ADD", post: addPostData }));

            setSubmitDisable(false);
          }
        } catch (error) {
          const { message, error: err } = error;

          if (message) {
            setErrorMessage(message);
          } else if (err) {
            setErrorMessage(err);
          } else {
            setErrorMessage("Something went wrong. Please try again later.");
          }

          setSubmitDisable(false);
        }
      }
      onClose();
      handleClearState();
    }
  };

  const handleClearState = () => {
    setPostData({
      postInstruction: "",
      postDesc: "",
      twitterDesc: "",
      postDate: "",
      postTime: "",
      isAdvanceActivated: advanceSetting,
      facebookDate: "",
      facebookTime: "",
      instagramTime: "",
      instagramDate: "",
      twitterTime: "",
      twitterDate: "",
      linkedinDate: "",
      ctaType: "",
      ctaURL: "",
      tagPeople: "",
      tagPlace: "",
      linkedinTime: "",
    });
    setSelectedStrategy("");
    setFiles([]);
    setSubmitDisable(false);
  };
  const handleSaveAsDraft = (event) => {
    event.preventDefault();

    if (
      postData.postDate === "" ||
      postData.postTime === "" ||
      Object.keys(selectedStrategy).length === 0
    ) {
      setErrorMessage("fields can not be empty");
    } else {
      dispatch(
        addPost({
          postID: uuid(),
          postDetails: {
            postDate: postData.postDate,
            postTime: postData.postTime,
            postDesc: postData.postDesc,
            postImage: files[0],
          },

          event: {
            day: formatDate(postData.postDate),
            strategy: selectedStrategy,
            isDraft: true,
          },
        })
      );
    }
    handleClearState();
    onClose();
  };
  const handleInputChange = (field, value) => {
    return setPostData((prevState) => ({
      ...prevState,
      [field]: value,
    }));
  };

  const handleStrategySelect = (s) => {
    const { name, color, desc, strategyID } = s;
    // setSelectedStrategy({
    //   name,
    //   color,
    //   desc,
    //   strategyID,
    // });

    setSelectedStrategy(strategyID);

    setShowDropDown(false);
  };

  const handleInputChangeSpecial = (field, value) => {
    return setPostData((prevState) => ({
      ...prevState,
      [field]: value,
    }));
  };

  const handleClose = () => {
    onClose();
    handleClearState();
  };

  useEffect(() => {
    if (files.length) {
      // validate post asset
      const { error, isAssetValid, type } = validatePostAsset(files[0]);

      if (error) {
        setSubmitDisable(false);
        // handleClose();
        setFiles([]);
        return setErrorMessage(error);
      } else if (isAssetValid) {
        setSubmitDisable(false);
        setAssetType(type);
      }
    }
  }, [files]);

  // console.log(selectedStrategy, strategyState.strategyList);

  const newHandleStrategySelect = (event) => {
    event.preventDefault();

    setSelectedStrategy(event.target.value);
  };

  return (
    <Drawer anchor={"right"} open={open} onClose={onClose}>
      <div className={classes["create-post-wrapper"]} role="presentation">
        <div className={classes["sideDrawerTitleSticky"]}>
          <div className={classes["sideDrawerTitleWrapper"]}>
            <IconButton onClick={handleClose}>
              <CustomIcon.BackBtnIcon />
            </IconButton>
            <div className={classes["sideDrawerTitle"]}>
              {isEdit ? "Edit Post" : "Create a Post"}
            </div>
          </div>
        </div>

        <div className={classes["sideDrawerForm"]}>
          <form className={classes.Form}>
            <FormControl className={classes.FormControl}>
              <Select
                defaultValue={defaultStrategy ? defaultStrategy.strategyID : ""}
                placeholder="Select Strategy"
                className={classes.Select_Dropdown}
                displayEmpty
                inputProps={{ "aria-label": "Without label" }}
                onChange={newHandleStrategySelect}
              >
                <MenuItem className={classes.Option_TOWhom} disabled>
                  Select Strategy
                </MenuItem>
                {strategyState.strategyList.map((s) => (
                  <MenuItem
                    key={s.strategyID ?? s.name}
                    value={s.strategyID ?? s.name}
                    className={classes["option-atom"]}
                    onClick={() => handleStrategySelect(s)}
                    displayEmpty={s.name === "Event"}
                  >
                    {s.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>

            <FormControl className={classes.FormControl}>
              <CustomInputField
                placeholder="Post Instruction (Optional)"
                rows={4}
                multiline
                value={postData.postInstruction}
                onChange={(e) =>
                  handleInputChange("postInstruction", e.target.value)
                }
                style={{ resize: "no" }}
              />
            </FormControl>

            {/* Image Upload */}
            <Drag files={files} setFiles={setFiles} />

            {/*Post Description */}
            <FormControl className={classes.FormControl}>
              <CustomInputField
                placeholder="Add Description"
                rows={3}
                value={postData.postDesc}
                onChange={(e) => handleInputChange("postDesc", e.target.value)}
                style={{ resize: "no" }}
                multiline
              />
            </FormControl>

            {/* Twitter text limit error */}
            {postData.postDesc.length >= 280 && (
              <div className={classes["twitter-button-wrapper"]}>
                <div className={classes["word-limit-error"]}>
                  <img src={Warn} alt="warn" />
                  <div className={classes["word-error-text"]}>
                    Word limit {postData.postDesc.length}/280
                  </div>
                </div>
                <div
                  className={classes["edit-twitter-button"]}
                  onClick={showTwitterButtonHandler}
                >
                  Edit Twitter Text
                </div>
              </div>
            )}

            {/* If twitter text limit reached */}
            {showTwitterInputBox && (
              <FormControl className={classes.FormControl}>
                <CustomInputField
                  placeholder="Twitter Description"
                  rows="3"
                  multiline
                  value={postData.twitterDesc}
                  onChange={(e) =>
                    handleInputChangeSpecial("twitterDesc", e.target.value)
                  }
                  style={{ resize: "no" }}
                />
              </FormControl>
            )}

            <h6 className={classes.postedAtHeading}>To be Posted at:</h6>

            {/* Post date */}
            <FormControl className={classes.FormControl}>
              <CustomInputField
                type="date"
                value={postData.postDate}
                onChange={(e) => handleInputChange("postDate", e.target.value)}
                placeholder="select strategy"
              />
            </FormControl>

            {/* Post Time */}
            <FormControl className={classes.FormControl}>
              <CustomInputField
                type="time"
                value={postData.postTime}
                onChange={(e) => handleInputChange("postTime", e.target.value)}
                placeholder="select strategy"
              />
            </FormControl>

            {/* Advance Setting  Option */}
            <div className={classes.advanceSettingWrapper}>
              <div
                className={classes.advanceTitleWrap}
                onClick={() => setAdvanceSetting(true)}
              >
                <p className={classes.advanceTitle}>
                  Advance Social media settings
                </p>
                <img
                  src={Triangle}
                  alt="triangle"
                  className={classes["advance-triangle"]}
                />
              </div>

              {(postData.facebookDate ||
                postData.facebookTime ||
                postData.instagramDate ||
                postData.instagramTime ||
                postData.twitterDate ||
                postData.twitterTime) && (
                <div className={classes["custom-title"]}>
                  Custom settings applied on:
                </div>
              )}

              <div className={classes["social-media-icons"]}>
                {(postData.facebookDate || postData.facebookTime) && (
                  <img src={Facebook} alt="facebook icon" />
                )}
                {(postData.instagramDate || postData.instagramTime) && (
                  <img src={Instagram} alt="instagram-icon" />
                )}
                {(postData.twitterDate || postData.twitterTime) && (
                  <img src={Twitter} alt="twitter-icon" />
                )}
                {(postData.linkedinDate || postData.linkedinTime) && (
                  <img
                    src={Linkedin}
                    alt="linkedin-icon"
                    style={{ width: "5%" }}
                  />
                )}
              </div>
            </div>
            <div
              className={classes.buttonFlexWrapper}
              style={{ display: "flex", justifyContent: "right" }}
            >
              <button
                className={classes["create-post-buttons"]}
                onClick={(event) => handleFinalSubmit(event, "create")}
                disabled={submitDisable}
              >
                Submit the post
              </button>
              {/* <button
                className={classes["create-post-buttons-draft"]}
                // onClick={handleSaveAsDraft}
                onClick={(event) => handleFinalSubmit(event, "draft")}
              >
                Save as draft
              </button> */}
            </div>
          </form>
        </div>

        <AdvanceSettingCreate
          open={advanceSetting}
          onClose={() => setAdvanceSetting(false)}
          handleInputChange={handleInputChange}
          postData={postData}
          setPostData={setPostData}
        />
      </div>
    </Drawer>
  );
};

export default PostCreate;
