import { makeStyles } from "@material-ui/core";

export default makeStyles((theme) => ({
  events: {
    "& .MuiBackdrop-root": {
      backgroundColor: "#00000080",
    },

    "& .wrapper": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      width: "360px",

      "& > .header": {
        alignItems: "center",
        display: "flex",
        flex: "0 1 0",
        justifyContent: "space-between",
        gap: "10px",
        padding: "36px 30px",
        width: "100%",

        "& .left": {
          display: "flex",
          flex: "1 0 0",

          "& .back-btn": {
            "&:hover": {
              backgroundColor: "transparent",
            },
          },

          "& .title": {
            color: "#24272c",
            flexGrow: 1,
            fontWeight: 500,
            fontSize: "24px",
            lineHeight: "28px",
            overflow: "hidden",
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
          },
        },

        "& .right": {
          flex: "0 1 auto",

          "& .MuiButton-root": {
            backgroundColor: "#ffffff",
            border: "1px solid #24272c33",
            borderRadius: "4px",
            color: "#12B0EE",
            height: "40px",
            textTransform: "capitalize",
          },
        },
      },

      "& > .body": {
        display: "flex",
        flexDirection: "column",
        flex: "1 0 0",
        overflowY: "auto",

        "& .events-body": {
          padding: "0 30px",

          "& .eventCard": {
            "& .event-date": {
              color: "#000000",
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "19px",
            },

            "& .userEvent": {
              marginTop: "18px",
              marginBottom: "24px",

              "& .titleWrapper": {
                alignItems: "center",
                display: "flex",
                columnGap: "7px",
                marginBottom: "6px",

                "& .eventTitle": {
                  color: "#24272c",
                  fontSize: "14px",
                  fontWeight: 400,
                  lineHeight: "16px",
                },
              },

              "& .MuiButton-root": {
                color: "#ffffff",
                backgroundColor: "#12b0ee",
                boxShadow: "none",
                borderRadius: "8px",
                fontSize: "14px",
                fontWeight: 500,
                lineHeight: "16px",
                height: "40px",
                textTransform: "initial",
              },
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
}));
