import React, { Fragment, useEffect, useState } from "react";
import { useSelector } from "react-redux";
import GlobalEvent from "./GlobalEvent";
import UserEvent from "./UserEvent";

export default function EventCard() {
  const eventsState = useSelector((state) => state.eventsState);
  const getDate = (date) => {
    const splitDate = date.split("-");
    return splitDate.reverse().join(" ");
  };
  const calculateLines = (index) => {
    if (index < eventsState.events.length) {
      const object = {
        Jan: 31,
        Feb: 28,
        Mar: 31,
        Apr: 30,
        May: 31,
        Jun: 30,
        Jul: 31,
        Aug: 31,
        Sept: 30,
        Oct: 31,
        Nov: 30,
        Dec: 31,
      };
      const previousDate = eventsState.events[index - 1].day;
      const currentDate = eventsState.events[index].day;
      const [pMonth, pDay] = previousDate.split("-");
      const [cMonth, cDay] = currentDate.split("-");
      if (pMonth === cMonth) {
        return Math.abs(Number(cDay) - Number(pDay));
      } else {
        if (pDay <= 25) {
          return 3;
        } else {
          return object[pMonth] - Math.abs(Number(pDay) - Number(cDay));
        }
      }
    }
  };

  const LineToDisplay = ({ index }) => {
    const [linesToShow, setLinesToShow] = useState(0);
    useEffect(() => {
      const result = calculateLines(index);
      if (result >= 3) {
        setLinesToShow(3);
      } else {
        setLinesToShow(result);
      }
    }, [index]);

    return (
      <div className="lines-wrapper">
        {new Array(linesToShow).fill("random").map((e, index) => (
          <hr className="lines" key={index} />
        ))}
      </div>
    );
  };

  return (
    <Fragment>
      {eventsState.events.map((e, index, allEvents) => (
        <div className="eventCard" key={index}>
          {(index === 0 || allEvents[index - 1]?.day !== e.day) && (
            <h4 className="event-date">{getDate(e.day)}</h4>
          )}
          {e.isGlobal && (
            <GlobalEvent
              title={e.event.name}
              desc={e.event.desc}
              date={e.date}
            />
          )}
          {e.isManual && (
            <UserEvent title={e.event.name} desc={e.event.desc} date={e.date} />
          )}
          {Object.prototype.hasOwnProperty.call(e, "isMultipleEvent") &&
            e.multipleEvents.map((event) => {
              return (
                <UserEvent
                  title={event.name}
                  date={event.date}
                  desc={event.desc}
                  isMultiple
                  isNotImportant={e.hasOwnProperty("isContainNonImpEvent")}
                />
              );
            })}
        </div>
      ))}
    </Fragment>
  );
}
