import React, { useState, useEffect, useCallback } from "react";
import { Drawer } from "@material-ui/core";
import classes from "./showPostComment.module.scss";
import LeftArrow from "../../../assets/icons/leftArrow.svg";
import { Close } from "@mui/icons-material";
import { Input, Link } from "@material-ui/core";
import { Collapse } from "@mui/material";

// api imports
import {
  fetchFBPostComments,
  postFBComment,
  commentOnFBComment,
  fetchIGPostComments,
  postIGComment,
  commentOnIGComment,
  fetchFBCommentReplies,
  fetchIGCommentReplies,
  postLIComment,
  replyToTweet,
  fetchTweetComments,
  fetchPostAssetType,
  fetchLIPostComments,
  fetchLICommentReplies,
  commentOnLIComment,
} from "../../../api/PostInsights";

import {
  fetchBusinessSMAccountInfo,
  fetchBusinessLogo,
} from "../../../api/SocialMediaSetup";

// contexts imports
import { useToastMessage } from "../../../contexts/ToastMessageContext";

// utils imports
import formatCommentTime from "./utils/formatCommentTime";
import { get } from "http";

export default function ShowPostComment({
  open,
  onClose,
  eventImage,
  platformData,
  setPlatformData,
  platformIDs,
  esPostID,
  setSelectedPlatform,
}) {
  // component states
  const [comments, setComments] = useState(null);
  const [platformName, setPlatformName] = useState(null);
  const [postID, setPostID] = useState(null);
  const [platformCredentials, setPlatformCredentials] = useState(null);
  const [showReplyForm, setShowReplyForm] = useState(false);
  const [replyCommentID, setReplyCommentID] = useState(null);
  const [commentText, setCommentText] = useState("");
  const [replyCommentText, setReplyCommentText] = useState("");
  const [commentReplies, setCommentReplies] = useState([]);
  const [businessAccountData, setBusinessAccountData] = useState(null);
  const [logoURL, setLogoURL] = useState(null);
  const [preventFetchReplies, setPreventFetchReplies] = useState(false);
  const [showReply, setShowReply] = useState(true);

  // contexts
  const { setErrorMessage, setSuccessMessage } = useToastMessage();

  // component effects
  useEffect(() => {
    if (open) {
      const { platform, credentials } = platformData;

      setPlatformName(platform);
      //setPlatformCredentials(credentials);

      let postID = "";

      switch (platform) {
        case "facebook":
          postID = platformIDs.facebook_post_id;
          break;

        case "instagram":
          postID = platformIDs.instagram_post_id;
          break;

        case "linkedin":
          postID = platformIDs.linkedin_post_id;
          break;

        case "twitter":
          postID = platformIDs.tw;
          break;

        default:
          postID = null;
      }

      setPostID(postID);

      if (platform === "facebook") {
        fetchFBPostComments(postID)
          .then((response) => {
            setComments(response.payload);
          })
          .catch((error) => {
            setErrorMessage(error.error);
          });
      }

      if (platform === "instagram") {
        fetchIGPostComments(postID)
          .then((response) => {
            setComments(response.payload);
          })
          .catch((error) => {
            setErrorMessage(error.error);
          });
      }

      if (platform === "linkedin") {
        fetchLIPostComments(postID)
          .then((response) => {
            if (response.message === "success") {
              console.log("response", response);
              setComments(response.payload);
            } else {
              setErrorMessage(response.error);
            }
          })
          .catch((error) => {
            setErrorMessage(error.error);
          });
      }

      if (platform === "twitter") {
        const { twAccessToken, twTokenSecret } = credentials;

        fetchTweetComments(esPostID, postID, twAccessToken, twTokenSecret)
          .then(({ commentsData, repliesData }) => {
            setComments(commentsData);
            setCommentReplies(repliesData);
          })
          .catch(({ error }) => {
            setErrorMessage(error);
          });
      }
    }
  }, [esPostID, open, platformData, platformIDs, setErrorMessage]);

  useEffect(() => {
    if (open && esPostID) {
      fetchPostAssetType(esPostID)
        .then(({ assetType }) => {
          if (
            platformName === "facebook" &&
            (assetType === "GIF" || assetType === "VIDEO")
          ) {
            setShowReply(false);
          }
        })
        .catch(({ error }) => {
          console.log("fetch asset type error: ", error);
        });
    }
  }, [esPostID, open, platformName]);

  const getPlatformDetail = async () => {
    if (platformName) {
      const SMInfo = await fetchBusinessSMAccountInfo({
        platform: platformName.toUpperCase(),
        product: "curate",
      });
      const { facebook, instagram, linkedin } = SMInfo.payload;
      if (platformName === "facebook") {
        setBusinessAccountData(facebook);
      }
      if (platformName === "instagram") {
        setBusinessAccountData(instagram);
      }
      if (platformName === "linkedin") {
        setBusinessAccountData(linkedin);
      }

      fetchBusinessLogo()
        .then(({ logoURL }) => {
          setLogoURL(logoURL);
        })
        .catch(({ error }) => {
          console.log("account info error: ", error);
        });
    }
  };

  useEffect(() => {
    getPlatformDetail();
  }, [platformName]);

  useEffect(() => {
    if (!open) {
      setComments([]);
      setCommentReplies([]);
      setPlatformName(null);
      setPlatformCredentials(null);
      setPlatformData(null);
      setSelectedPlatform(null);
      setPreventFetchReplies(false);
    }
    // eslint-disable-next-line
  }, [open]);

  // useEffect(() => {
  //   if (open && platformName) {
  //     setComments([]);
  //     setCommentReplies([]);
  //     setPlatformCredentials(null);
  //     setPlatformData(null);
  //     // setSelectedPlatform(null);
  //   }
  // }, [open, platformName]);

  // callback functions def
  const fetchFBRepliesCB = useCallback(async () => {
    if (comments && comments.length && platformName) {
      if (platformName === "facebook") {
        const fbCommentsReplies = await Promise.all(
          comments.map(async (comment) => {
            try {
              const data = await fetchFBCommentReplies(comment.comment_id);

              return {
                commentID: comment.comment_id,
                replies: data.payload,
              };
            } catch (error) {
              setErrorMessage(error.message || error.error);
            }
          })
        );

        setCommentReplies(fbCommentsReplies);
      } else if (platformName === "instagram") {
        const igCommentsReplies = await Promise.all(
          comments.map(async (comment) => {
            try {
              const res = await fetchIGCommentReplies(comment.comment_id);
              if (res.errors) {
                return setErrorMessage(res.errors[0].message);
              }
              return {
                commentID: comment.comment_id,
                replies: res.payload,
              };
            } catch (error) {
              setErrorMessage(error.message || error.error);
            }
          })
        );

        setCommentReplies(igCommentsReplies);
      } else if (platformName === "linkedin") {
        const liCommentsReplies = await Promise.all(
          comments.map(async (comment) => {
            try {
              const data = await fetchLICommentReplies(comment.urn);
              console.log("li data", data);
              return {
                commentID: comment.comment_id,
                replies: data.payload,
              };
            } catch (error) {
              setErrorMessage(error.message || error.error);
            }
          })
        );

        setCommentReplies(liCommentsReplies);
      }
    }
  }, [comments, platformName, platformCredentials, setErrorMessage]);

  useEffect(() => {
    if (!preventFetchReplies) {
      fetchFBRepliesCB();
    }
  }, [fetchFBRepliesCB, preventFetchReplies]);

  // event handlers
  const handleReplyLabelClick = (commentID) => {
    setShowReplyForm(true);
    console.log("commentID", commentID);
    setReplyCommentID(commentID);
  };

  const handleCancelReply = () => {
    setShowReplyForm(false);
    setReplyCommentID(null);
  };

  const handleCommentOnPost = async (event) => {
    event.preventDefault();
    const eventTime = Date.now();

    if (!commentText) {
      return setErrorMessage("Please enter a comment!");
    }

    try {
      let commentResponse = null;

      if (platformName === "facebook") {
        commentResponse = await postFBComment(postID, commentText);

        if (commentResponse.errors) {
          return setErrorMessage(commentResponse.errors[0].message);
        }
        if (commentResponse.id) {
          setPreventFetchReplies(true);
          setComments((prevComments) => {
            return [
              {
                comment: commentText,
                comment_id: commentResponse.id,
                time: eventTime,
                userID: businessAccountData.page_id,
                username: businessAccountData.page_name,
              },
              ...prevComments,
            ];
          });
          setCommentText("");
          // setSuccessMessage("commented on the facebook post!");
        }
      }

      if (platformName === "instagram") {
        commentResponse = await postIGComment(postID, commentText);
        if (commentResponse.errors) {
          return setErrorMessage(commentResponse.errors[0].message);
        }
        if (commentResponse && commentResponse.id.length) {
          setPreventFetchReplies(true);
          setComments((prevComments) => {
            return [
              {
                comment: commentText,
                comment_id: commentResponse.id,
                time: eventTime,
                userID: businessAccountData.account_id,
                username: businessAccountData.username,
              },
              ...prevComments,
            ];
          });
          setCommentText("");

          // setSuccessMessage("commented on the instagram post!");
        }
      }

      if (platformName === "linkedin") {
        commentResponse = await postLIComment(postID, commentText);
        if (commentResponse.errors) {
          return setErrorMessage(commentResponse.errors[0].message);
        }
        if (commentResponse.message === "success") {
          setPreventFetchReplies(true);
          setComments((prevComments) => {
            return [
              {
                comment: commentText,
                comment_id: commentResponse.payload.comment_id,
                time: eventTime,
                userID: businessAccountData.org_id,
                username: businessAccountData.org_name,
              },
              ...prevComments,
            ];
          });
          setCommentText("");
          // setSuccessMessage("commented on the facebook post!");
        }
      }

      if (platformName === "twitter") {
        // tweetID, twToken, twTokenSecret, comment

        const { twAccessToken, twTokenSecret } = platformCredentials;

        commentResponse = await replyToTweet(
          postID,
          twAccessToken,
          twTokenSecret,
          commentText
        );

        const { reply } = commentResponse;

        if (reply && reply.id_str) {
          setPreventFetchReplies(true);
          setComments((prevComments) => {
            return [
              {
                comment: commentText,
                commentID: reply.id_str,
                time: eventTime,
                profileIMG: businessAccountData.imgURL,
                userID: businessAccountData.accountID,
                username: businessAccountData.screen_name,
              },
              ...prevComments,
            ];
          });
          setCommentText("");
          // setSuccessMessage("Replied to tweet!");
        }
      }
    } catch (error) {
      setErrorMessage(error.error);
    }
  };

  const handleCommentOnComment = async (event) => {
    event.preventDefault();
    const eventTime = Date.now();
    if (!replyCommentText) {
      return setErrorMessage("Please enter a comment!");
    }

    try {
      let commentResponse = null;

      if (platformName === "facebook") {
        commentResponse = await commentOnFBComment(
          replyCommentID,
          replyCommentText
        );
        if (commentResponse.errors) {
          return setErrorMessage(commentResponse.errors[0].message);
        }

        if (commentResponse.id) {
          setCommentReplies((prevReplies) => {
            const commentIndex = prevReplies.findIndex(
              (prevReply) => prevReply.commentID === replyCommentID
            );

            if (commentIndex < 0) {
              prevReplies.unshift({
                commentID: replyCommentID,
                replies: [
                  {
                    reply: replyCommentText,
                    replyID: commentResponse.id,
                    time: eventTime,
                    userID: businessAccountData.page_id,
                    username: businessAccountData.page_name,
                  },
                ],
              });
            } else {
              prevReplies[commentIndex].replies.push({
                reply: replyCommentText,
                replyID: commentResponse.id,
                time: eventTime,
                userID: businessAccountData.page_id,
                username: businessAccountData.page_name,
              });
            }
            return prevReplies;
          });
          setReplyCommentText("");

          // setSuccessMessage("commented on the facebook comment!");
        }
      }

      if (platformName === "instagram") {
        commentResponse = await commentOnIGComment(
          replyCommentID,
          replyCommentText
        );

        if (commentResponse.errors) {
          return setErrorMessage(commentResponse.errors[0].message);
        }
        if (commentResponse && commentResponse.id.length) {
          setCommentReplies((prevReplies) => {
            const commentIndex = prevReplies.findIndex(
              (prevReply) => prevReply.commentID === replyCommentID
            );
            if (commentIndex < 0) {
              prevReplies.unshift({
                commentID: replyCommentID,
                replies: [
                  {
                    reply: replyCommentText,
                    replyID: commentResponse.id,
                    time: eventTime,
                    userID: businessAccountData.account_id,
                    username: businessAccountData.username,
                  },
                ],
              });
            } else {
              prevReplies[commentIndex].replies.unshift({
                reply: replyCommentText,
                replyID: commentResponse.id,
                time: eventTime,
                userID: businessAccountData.account_id,
                username: businessAccountData.username,
              });
            }

            return prevReplies;
          });
          setReplyCommentText("");

          // setSuccessMessage("commented on the instagram comment!");
        }
      }

      if (platformName === "linkedin") {
        commentResponse = await commentOnLIComment(
          replyCommentID,
          replyCommentText
        );

        if (commentResponse.errors) {
          return setErrorMessage(commentResponse.errors[0].message);
        }

        if (commentResponse.message === "success") {
          setCommentReplies((prevReplies) => {
            const commentIndex = prevReplies.findIndex(
              (prevReply) => prevReply.commentID === replyCommentID
            );

            if (commentIndex < 0) {
              prevReplies.unshift({
                commentID: replyCommentID,
                replies: [
                  {
                    reply: replyCommentText,
                    replyID: commentResponse.payload,
                    time: eventTime,
                    userID: businessAccountData.org_id,
                    username: businessAccountData.org_name,
                  },
                ],
              });
            } else {
              prevReplies[commentIndex].replies.push({
                reply: replyCommentText,
                replyID: commentResponse.id,
                time: eventTime,
                userID: businessAccountData.org_id,
                username: businessAccountData.org_name,
              });
            }
            return prevReplies;
          });
          setReplyCommentText("");

          // setSuccessMessage("commented on the facebook comment!");
        }
      }

      if (platformName === "twitter") {
        // tweetID, twToken, twTokenSecret, comment

        const { twAccessToken, twTokenSecret } = platformCredentials;

        commentResponse = await replyToTweet(
          replyCommentID,
          twAccessToken,
          twTokenSecret,
          replyCommentText
        );

        const { reply } = commentResponse;

        if (reply && reply.id_str) {
          setCommentReplies((prevReplies) => {
            const commentIndex = prevReplies.findIndex(
              (prevReply) => prevReply.commentID === replyCommentID
            );
            if (commentIndex < 0) {
              prevReplies.unshift({
                commentID: replyCommentID,
                replies: [
                  {
                    reply: replyCommentText,
                    replyID: reply.id_str,
                    time: eventTime,
                    profileIMG: businessAccountData.imgURL,
                    userID: businessAccountData.accountID,
                    username: businessAccountData.screen_name,
                  },
                ],
              });
            } else {
              prevReplies[commentIndex].replies.unshift({
                reply: replyCommentText,
                replyID: reply.id,
                time: eventTime,
                profileIMG: businessAccountData.imgURL,
                userID: businessAccountData.accountID,
                username: businessAccountData.screen_name,
              });
            }
            return prevReplies;
          });
          setReplyCommentText("");
          // setSuccessMessage("Replied to tweet!");
        }
      }
    } catch (error) {
      setErrorMessage(error.error);
    }
  };

  return (
    <Collapse orientation="horizontal" in={open}>
      <div role="presentation" className={classes.drawerWrapper}>
        <div className={classes.drawerHeaderFix}>
          <div className={classes.drawerHeadingWrapper}>
            <div className={classes.drawerHeading}>
              {platformName && (
                <div className={classes["sideDrawer-Title"]}>
                  {platformName}
                </div>
              )}
            </div>
            <Link underline="none" component="button" onClick={() => onClose()}>
              <Close />
            </Link>
          </div>
        </div>
        <div className={classes.commentsWrapper}>
          {comments &&
            comments.length > 0 &&
            comments.map((comment) => (
              <div className={classes.postWrapper} key={comment.commentID}>
                <div className={classes.userHeader}>
                  <div className={classes.imageWrapper}>
                    {/* {eventImage && eventImage.preview && (
                    <img
                      src={eventImage.preview}
                      alt="post"
                      className={classes.postImage}
                    />
                  )} */}
                    {comment.profileIMG ? (
                      <img
                        src={comment.profileIMG}
                        alt="post"
                        style={{ borderRadius: "50%" }}
                        // className={classes.postImage}
                      />
                    ) : (
                      comment.username.slice(0, 2).toUpperCase()
                    )}
                  </div>
                  <div style={{ marginLeft: "10px" }}>
                    <div className={classes.userName}>{comment.username}</div>
                    {/* <div className={classes.commentTime}>
                    November 05, 2021 05:31 PM
                  </div> */}
                    {/* <div className={classes.commentTime}>{comment.time}</div> */}
                    <div className={classes.commentTime}>
                      {formatCommentTime(platformName, comment.time)}
                    </div>
                  </div>
                </div>
                <div className={classes.postComment}>{comment.comment}</div>
                <Link
                  underline="none"
                  component="button"
                  className={classes.commentReply}
                  onClick={() => handleReplyLabelClick(comment.comment_id)}
                >
                  {showReply && (
                    <>
                      <svg
                        width="12"
                        height="11"
                        viewBox="0 0 12 11"
                        fill="none"
                        xmlns="//www.w3.org/2000/svg"
                      >
                        <path d="M4.656 3.4C6.81067 3.69867 8.53333 4.584 9.824 6.056C10.9013 7.29333 11.6267 8.856 12 10.744C11.1787 9.57067 10.1707 8.712 8.976 8.168C7.78133 7.61333 6.34133 7.336 4.656 7.336V10.056L0 5.4L4.656 0.743999V3.4Z" />
                      </svg>
                      {" Reply"}
                    </>
                  )}
                </Link>
                {commentReplies.length > 0 &&
                  commentReplies.map((reply) =>
                    comment.comment_id === reply.commentID &&
                    reply.replies.length > 0
                      ? reply.replies.map((cmtReply) => (
                          <div
                            style={{ marginLeft: 25, marginTop: 20 }}
                            key={cmtReply.reply_id}
                          >
                            <div className={classes.postWrapper}>
                              <div className={classes.userHeader}>
                                <div className={classes.imageWrapper}>
                                  {cmtReply.profileIMG ? (
                                    <img
                                      src={cmtReply.profileIMG}
                                      alt="post"
                                      style={{ borderRadius: "50%" }}
                                    />
                                  ) : (
                                    cmtReply.username.slice(0, 2).toUpperCase()
                                  )}
                                </div>
                                <div style={{ marginLeft: "10px" }}>
                                  <div className={classes.userName}>
                                    {cmtReply.username}
                                  </div>
                                  {/* <div className={classes.commentTime}>
                                  {cmtReply.time}
                                </div> */}
                                  <div className={classes.commentTime}>
                                    {formatCommentTime(
                                      platformName,
                                      cmtReply.time
                                    )}
                                  </div>
                                </div>
                              </div>
                              <div className={classes.postComment}>
                                {cmtReply.reply}
                              </div>
                            </div>
                          </div>
                        ))
                      : null
                  )}
                {showReplyForm && replyCommentID === comment.comment_id && (
                  <div className={classes.postWrapper}>
                    <div className={classes.userHeader}>
                      <div className={classes.imageWrapper}>
                        {businessAccountData && businessAccountData.imgURL ? (
                          <img
                            src={businessAccountData.imgURL}
                            alt="post"
                            className={classes.postImage}
                          />
                        ) : (
                          logoURL && (
                            <img
                              src={logoURL}
                              alt="post"
                              className={classes.postImage}
                            />
                          )
                        )}
                      </div>
                      <div style={{ marginLeft: "10px", width: "85%" }}>
                        <form onSubmit={handleCommentOnComment}>
                          <div style={{ width: "100%" }}>
                            <Input
                              minRows={1}
                              maxRows={3}
                              multiline
                              className={classes.input}
                              placeholder="Write a Message..."
                              value={replyCommentText}
                              onChange={(e) =>
                                setReplyCommentText(e.target.value)
                              }
                            />
                          </div>
                          <div className={classes.commentActionBtn}>
                            <button
                              className={classes.commentCancel}
                              onClick={handleCancelReply}
                            >
                              Cancel
                            </button>
                            <button className={classes.commentReplyBtn}>
                              Reply
                            </button>
                          </div>
                        </form>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            ))}
        </div>
        <div className={classes.postNewComment}>
          <div className={classes.userHeader}>
            <div className={classes.imageWrapper}>
              {businessAccountData && businessAccountData.imgURL ? (
                <img
                  src={businessAccountData.imgURL}
                  alt="post"
                  className={classes.postImage}
                />
              ) : (
                logoURL && (
                  <img src={logoURL} alt="post" className={classes.postImage} />
                )
              )}
            </div>
            <div style={{ marginLeft: "10px", width: "85%" }}>
              <form onSubmit={handleCommentOnPost}>
                <div style={{ width: "100%" }}>
                  <Input
                    minRows={1}
                    maxRows={3}
                    multiline
                    className={classes.input}
                    placeholder="Write a Message..."
                    value={commentText}
                    onChange={(e) => setCommentText(e.target.value)}
                  />
                </div>
                <div className={classes.commentActionBtn}>
                  {/* <button className={classes.commentCancel}>Cancel</button> */}
                  <button className={classes.commentBtn}>Comment</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Collapse>
  );
}
