import { useState } from "react";
import classes from "./addNewEventCreate.module.css";
import { Drawer, IconButton } from "@material-ui/core";
import { useDispatch } from "react-redux";
import { addUserEvent } from "../../../Store/calendarDashboard/events/eventSlice";
import { formatDate } from "../../../utils/calendarDashboard";
import CustomIcon from "../../../shared-components/CustomIcon/CustomIcon";
import { CustomInputField } from "../../UI/CustomTextField/CustomTextField";

// request helpers
import { CreateBusinessEvent } from "../../../api/Events/Business";

// contexts
import { useToastMessage } from "../../../contexts/ToastMessageContext";

export default function EventCreate({ open, onClose }) {
  const [eventCreateData, setEventCreateData] = useState({
    eventName: "",
    eventDate: "",
    eventDesc: "",
  });
  const dispatch = useDispatch();

  const { setErrorMessage, setSuccessMessage } = useToastMessage();

  const handleInputChange = (field, value) => {
    setEventCreateData((prevState) => ({
      ...prevState,
      [field]: value,
    }));
  };

  const clearFieldsFunc = () => {
    setEventCreateData({ eventName: "", eventDate: "", eventDesc: "" });
  };

  const handleSubmitForAddNewEvent = async () => {
    const { eventDesc, eventName, eventDate } = eventCreateData;
    if (eventDate === "" || eventDesc === "" || eventName === "") {
      setErrorMessage("Filled cannot be empty");
    } else {
      const businessID = localStorage.getItem("businessId");

      // const businessID = "6064538f2f94727c34a4d6a9";

      const eventData = {
        eventdate: eventDate,
        eventName,
        description: eventDesc,
      };

      try {
        await CreateBusinessEvent(businessID, eventData);

        const eventsData = [
          {
            day: formatDate(eventDate),
            isGlobal: false,
            isManual: true,
            date: eventDate,
            event: {
              name: eventName,
              desc: eventDesc,
            },
          },
        ];

        setSuccessMessage("New event created!");

        dispatch(addUserEvent({ type: "ADD", eventsData }));
      } catch (error) {
        const { message, error: err } = error;

        if (message) {
          setErrorMessage(message);
        } else if (err) {
          setErrorMessage(err);
        } else {
          setErrorMessage(error);
        }
      }
      clearFieldsFunc();
      onClose();
    }
  };

  return (
    <Drawer anchor={"right"} open={open} onClose={onClose}>
      <div className={classes["strategy-create-wrap"]}>
        <div className={classes["strategy-Title"]}>
          <IconButton onClick={onClose}>
            <CustomIcon.BackBtnIcon />
          </IconButton>
          <div className={classes["sideDrawer-Title"]}>Add a new event </div>
        </div>
        <div className={classes.sideDrawerForm}>
          {/* Event Name */}
          <CustomInputField
            type="text"
            className={classes["form-input"]}
            onChange={(e) => handleInputChange("eventName", e.target.value)}
            value={eventCreateData.eventName}
            placeholder="Event Name"
          />

          {/* Event Date */}
          <CustomInputField
            type="date"
            onChange={(e) => handleInputChange("eventDate", e.target.value)}
            value={eventCreateData.eventDate}
            className={classes["form-input"]}
            placeholder="Event Date"
          />

          {/* Description */}
          <CustomInputField
            value={eventCreateData.eventDesc}
            onChange={(e) => handleInputChange("eventDesc", e.target.value)}
            placeholder="Description"
            rows="3"
            multiline
            className={classes["form-input"]}
            style={{ resize: "no" }}
          />
        </div>
        <div className={classes.buttonFlexWrapper}>
          <div
            className={classes["event-create-button"]}
            onClick={handleSubmitForAddNewEvent}
          >
            Save event to calendar
          </div>
        </div>
      </div>
    </Drawer>
  );
}
