import React, { useState } from "react";
import GrayStar from "../../../../assets/icons/GrayStar.svg";
import classes from "./globalEvent.module.css";
import CreatePost from "../../PostCreate";
export default function GlobalEvent({ title, desc, date }) {
  const [createPost, setCreatPost] = useState(false);
  const [showButton, setShowButton] = useState(false);
  const data = {
    postDesc: desc,
    postInstruction: title,
    postDate: date,
  };
  return (
    <div className={classes.globalEventWrapper}>
      <div
        className={classes.globalEventChildWrapper}
        onClick={() => setShowButton((prevState) => !prevState)}
      >
        <img src={GrayStar} alt="global Events" />
        <div className={classes.globalEventTitel}>{title}</div>
      </div>

      {showButton && (
        <div
          className={classes.primaryButtonWrapperBlue}
          onClick={() => setCreatPost(true)}
        >
          Create a post
        </div>
      )}
      {showButton && (
        <CreatePost
          open={createPost}
          onClose={() => setCreatPost(false)}
          hideBackdrop={true}
          isPopulated
          strategy="Event"
          populatingData={data}
        />
      )}
    </div>
  );
}
