// function def
const calculatePostTime = (scheduleDateTime, offset) => {
  // check the sign
  const isBehind = offset.toString().startsWith("-");

  const offsetSign = isBehind ? "-" : "+";

  let [hours, mins] = offset.toString().split(".");

  hours = hours.replace(offsetSign, "");

  const formattedHours = hours.length === 1 ? `0${hours}` : hours;

  mins = mins ? `.${mins}` : "00";

  const formattedMins = mins === "00" ? mins : 60 * parseFloat(mins);

  const formattedTimeZone = `${offsetSign}${formattedHours}:${formattedMins}`;

  const formattedPostDate = new Date(
    `${scheduleDateTime}${formattedTimeZone}`
  ).toISOString();

  return formattedPostDate;
};

// exports
export default calculatePostTime;
