import { Hidden } from "@material-ui/core";
import { useBusiness } from "../../../contexts/BusinessProvider";
import Navbar from "./Navbar/Navbar";
import Sidebar from "./Sidebar/Sidebar";

const NavbarContainer = ({ isLoading, switchBusiness, open, toggleBar }) => {
  const { allBusiness, selectedBusiness } = useBusiness();
  return (
    <Hidden smUp implementation="js">
      <Navbar
        isLoading={isLoading}
        switchBetweenBusiness={switchBusiness}
        allBusiness={allBusiness}
        selectedBusiness={selectedBusiness}
        open={open}
        toggleBar={toggleBar}
      />
      <Sidebar
        selectedBusiness={selectedBusiness}
        business={allBusiness}
        open={open}
        closed={toggleBar}
      />
    </Hidden>
  );
};

export default NavbarContainer;
