// const FetchBusinessEventsAll = (businessID) =>
const FetchBusinessEventsAll = () =>
  new Promise(async (resolve, reject) => {
    try {
      // const FetchBusinessEventsRes = await axios({
      //   url: `${BE_URL}/events/business/fetchAll/${businessID}`,
      // });
      const FetchBusinessEventsRes = await axios({
        url: `${BE_URL}/events/business/fetchAll`,
        headers: {
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data } = FetchBusinessEventsRes;

      if (data.error) {
        return reject(data.error);
      }

      return resolve(data);
    } catch (error) {
      return reject(error);
    }
  });

const CreateBusinessEvent = (businessID, EventData) =>
  new Promise(async (resolve, reject) => {
    try {
      const data = {
        businessID,
        ...EventData,
      };

      const CreateBusinessEventRes = await axios({
        url: `${BE_URL}/events/business/create`,
        method: "POST",
        data,
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: CreateEventData } = CreateBusinessEventRes;

      if (CreateEventData.error) {
        return reject(CreateEventData.error);
      }

      return resolve(CreateEventData);
    } catch (error) {
      return reject(error);
    }
  });

const EditBusinessEvent = (EventData) =>
  new Promise(async (resolve, reject) => {
    const { eventID } = EventData;

    try {
      const EditBusinessEventRes = await axios({
        // url: `${BE_URL}/events/business/edit/${businessID}/${eventID}`,
        url: `${BE_URL}/events/business/edit/${eventID}`,
        method: "PUT",
        data: EventData,
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: EditBusinessEventData } = EditBusinessEventRes;

      if (EditBusinessEventData.error) {
        return reject(EditBusinessEventData.error);
      }

      return resolve(EditBusinessEventData);
    } catch (error) {
      return reject(error);
    }
  });

const DeleteBusinessEvent = (eventID) =>
  new Promise(async (resolve, reject) => {
    try {
      const DeleteBusinessEventRes = await axios({
        // url: `${BE_URL}/events/business/delete/${businessID}/${eventID}`,
        url: `${BE_URL}/events/business/delete/${eventID}`,
        method: "DELETE",
        headers: {
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: DeleteBusinessEventData } = DeleteBusinessEventRes;

      if (DeleteBusinessEventData.error) {
        return reject(DeleteBusinessEventData.error);
      }

      return resolve(DeleteBusinessEventData);
    } catch (error) {
      return reject(error);
    }
  });

