const FetchEventsForMonth = (monthIndex, year, paddingDays) =>
  new Promise(async (resolve, reject) => {
    try {
      const FetchEventsRes = await axios({
        // url: `${BE_URL}/events/business/fetchAll/${businessID}`,
        url: `${BE_URL}/events/business/fetchAll`,
        method: "POST",
        data: {
          monthIndex,
          year,
          paddingDays,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: fetchEventsData } = FetchEventsRes;

      if (fetchEventsData.error) {
        return reject(fetchEventsData.error);
      }

      return resolve(fetchEventsData);
    } catch (error) {
      return reject(error);
    }
  });

