import React, { useEffect } from "react";
import { useSelector } from "react-redux";
// import classes from "./day.module.css";
// import Post from "../../../Post/post";

import GrayStar from "../../../../../assets/icons/GrayStar.svg";

import GoldStar from "../../../../../assets/icons/star.svg";
import Post from "../Post";

const Day = ({ day, index }) => {
  const strategyState = useSelector((state) => state.strategyState);

  useEffect(() => {}, []);

  let dayValue;

  if (day.value) {
    if (String(day.value).length === 1) {
      dayValue = "0" + day.value;
    } else if (String(day.value).length > 1) {
      dayValue = day.value;
    } else {
      dayValue = "";
    }
  }

  return (
    <div className={["day", day.isPadding ? "padding" : undefined].join(" ")}>
      <div
        className={[
          "date",
          day.isPadding
            ? "paddingDate"
            : day.isCurrentDay
            ? "currentDay"
            : undefined,
        ].join(" ")}
      >
        <h4>{dayValue}</h4>
        {day.globalEvent.length > 0 &&
          day.globalEvent[0].isGlobal &&
          !day.globalEvent[0].isManual && (
            <img
              src={GrayStar}
              alt="global event indicator"
              className="grayStar"
            />
          )}
        {day.globalEvent.length > 0 && day.globalEvent[0].isManual && (
          <img
            src={GoldStar}
            alt="User important event indicator"
            className="grayStar"
          />
        )}
      </div>
      {day.event.length > 0 &&
        day.event.map((e) => {
          return (
            strategyState.strategyStatus[e.event.strategy.name] && (
              <Post
                key={e.postID}
                day={e.event.day}
                postID={e.postID}
                eventTitle={e.event.strategy.name}
                eventColor={e.event.strategy.color}
                eventImage={e.postDetails.postImage}
                isDraft={e.event.isDraft}
                eventDesc={e.postDetails.postDesc}
                imgURL={e.postDetails.postImage}
                isLinkedin={Boolean(e.postDetails.li_schedule_date_time)}
                isFacebook={
                  Boolean(e.postDetails.fb_schedule_date_time) ||
                  Boolean(e.postDetails.schedule_date_time)
                }
                isInstagram={Boolean(e.postDetails.ig_schedule_date_time)}
                isTwitter={Boolean(e.postDetails.tw_schedule_date_time)}
                postDetails={e.postDetails}
                eventSubTitle={"This is sample subtitle"}
                post={e.post}
              />
            )
          );
        })}
      {day.event && <div className="event">{day.event.title}</div>}
    </div>
  );
};

export default Day;
