import React, { useState } from "react";
import IconButton from "@material-ui/core/IconButton";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import MoreVertIcon from "@material-ui/icons/MoreVert";

import ViewEvent from "../../ViewEvent/viewEvent";
import Strategy from "../../Strategy/index";

export default function LongMenu() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [strategy, setStrategy] = useState(false);
  const [event, setEvent] = useState(false);
  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleItemClick = (event) => {
    if (event === "strategy") {
      setStrategy(true);
    } else {
      setEvent(true);
    }
    handleClose();
  };

  return (
    <div>
      <IconButton
        aria-label="more"
        aria-controls="long-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        <MoreVertIcon />
      </IconButton>
      <Menu
        id="long-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        PaperProps={{
          style: {
            width: "20ch",
          },
        }}
      >
        <MenuItem onClick={() => handleItemClick("strategy")}>
          {" "}
          View Strategy
        </MenuItem>
        <MenuItem onClick={() => handleItemClick("events")}>
          {" "}
          View Events
        </MenuItem>
      </Menu>

      {event && <ViewEvent open={event} onClose={() => setEvent(false)} />}

      {strategy && (
        <Strategy open={strategy} onClose={() => setStrategy(false)} />
      )}
    </div>
  );
}
