import React, { useState, useEffect } from "react";

import Dialog from "@material-ui/core/Dialog";

import DialogContent from "@material-ui/core/DialogContent";
import Facebook from "../../../../assets/icons/facebook.svg";
import Instagram from "../../../../assets/icons/instagram.svg";
import Twitter from "../../../../assets/icons/twitter.svg";
import Slide from "@material-ui/core/Slide";
import classes from "./postDelete.module.css";
import { useDispatch } from "react-redux";

import { deletePost } from "../../../../Store/calendarDashboard/posts/postSlice";

// request helpers imports
import {
  DeleteScheduledPost,
  deletePlatformPosts,
  isPlatformPosted,
} from "../../../../api/Posts";

// contexts
import { useToastMessage } from "../../../../contexts/ToastMessageContext";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function PostDelete({
  postID,
  eventImage,
  open,
  onClose,
  day,
  postDetails,
  eventDesc,
  isFacebook,
  isInstagram,
  isLinkedin,
  isTwitter,
  imgURL,
}) {
  const eventDescToDisplay =
    eventDesc.length > 18 ? `${eventDesc.substr(0, 18)} ...` : eventDesc;

  const dispatch = useDispatch();

  // contexts
  const { setErrorMessage, setSuccessMessage } = useToastMessage();

  // component states
  const [renderImgURL, setRenderImgURL] = useState(null);

  // component effects
  useEffect(() => {
    if (postDetails) setRenderImgURL(postDetails.postThumbnail);
  }, [postDetails]);

  // event handlers
  const handlePostDelete = async () => {
    try {
      const { isPosted } = await isPlatformPosted(postID);

      if (!isPosted) {
        const businessID = localStorage.getItem("businessId");
        // const businessID = "6064538f2f94727c34a4d6a9";
        // await DeleteScheduledPost(businessID, postID);
        await DeleteScheduledPost(postID);

        dispatch(
          deletePost({
            postID: postID,
          })
        );

        setSuccessMessage("Scheduled post deleted!");
      } else {
        // delete platform post
        await deletePlatformPosts(postID, ["fb", "tw", "li"]);

        setSuccessMessage("Post deleted from platforms!");
      }
    } catch (error) {
      console.log("Delete post error: ", error);

      const { message, error: err } = error;

      if (message) {
        setErrorMessage(message);
      } else if (err) {
        setErrorMessage(err);
      } else {
        setErrorMessage(error);
      }
    }
  };

  return (
    <div>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={onClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogContent className={classes.deletePostWrapper}>
          {/* <div className={classes.cardImage}>
            {imgURL && (
              <img src={imgURL} alt="post" className={classes.postImage} />
            )}
          </div> */}
          <div className={classes.cardImage}>
            {renderImgURL && (
              <img
                src={renderImgURL}
                alt="post"
                className={classes.postImage}
              />
            )}
          </div>
          {/* <div className={classes.cardImage}>
            {eventImage && eventImage.preview && (
              <img
                src={eventImage.preview}
                alt="post"
                className={classes.postImage}
              />
            )}
          </div> */}
          <div className={classes.cardDescription}>
            <div className={classes.cardTitle}>{eventDescToDisplay}</div>
            <div className={classes.cardSubTitle}>
              {day} @ {postDetails.postTime}
            </div>
          </div>

          <div className={classes.cardSocialWrapper}>
            <div className="social-icons-posted">
              {isFacebook && (
                <img
                  src={Facebook}
                  className={classes.socialIcons}
                  alt="social-icons"
                />
              )}
              {isInstagram && (
                <img
                  src={Instagram}
                  className={classes.socialIcons}
                  alt="social-icons"
                />
              )}
              {isTwitter && (
                <img
                  src={Twitter}
                  className={classes.socialIcons}
                  alt="social-icons"
                />
              )}
            </div>
            <div>
              {isFacebook && isInstagram && isTwitter && isLinkedin && (
                <div className={classes.plusIconText}>+1</div>
              )}
            </div>
          </div>
          <hr />

          <div className={classes.footer}>
            <div className={classes.footerText}>
              Are you sure,you want to delete the post ?
            </div>
          </div>
          <div className={classes.footerButton}>
            <button
              className={classes.deletePostButton}
              onClick={handlePostDelete}
            >
              Delete Now
            </button>
            <button className={classes.cancelPostButton} onClick={onClose}>
              Cancel
            </button>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
}
