// date.js import
const date = require("date.js");

const ValidateScheduleTime = (postDate) => {
  const schedule = new Date(postDate);

  const date10MinsFromNow = date("14 minutes from now");

  const date6MonthsFromNow = date("6 months from now");

  // if (schedule < date10MinsFromNow || schedule > date6MonthsFromNow) {
  //     return false;
  // }

  // return true;

  return !(schedule < date10MinsFromNow || schedule > date6MonthsFromNow);
};

module.exports = ValidateScheduleTime;
