import { useState } from "react";
import { Drawer, IconButton, Button } from "@material-ui/core";
import EventsCard from "../EventsCard";
import AddNewEventCreate from "../AddNewEventCreate";
import CustomIcon from "../../../shared-components/CustomIcon/CustomIcon";

// styles
import useStyles from "./styles/muiStyles";

const EventCreate = ({ open, onClose }) => {
  const classes = useStyles();
  const [createNewEvent, setCreateNewEvent] = useState(false);
  return (
    <Drawer
      anchor="right"
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") && onClose()
      }
      className={classes.events}
    >
      <div role="presentation" className="wrapper">
        <div className="header">
          <div className="left">
            <IconButton
              aria-label="Back"
              className="back-btn"
              onClick={() => {
                onClose();
              }}
              style={{ color: "transparent", padding: 0 }}
              disableFocusRipple={true}
              disableRipple={true}
            >
              <CustomIcon.BackBtnIcon />
            </IconButton>
            <h1 className="title">{"Events"}</h1>
          </div>
          <div className="right">
            <Button
              aria-label="Add new event"
              variant="outlined"
              onClick={() => setCreateNewEvent(true)}
            >
              {"Add new event"}
            </Button>
          </div>
        </div>
        <div className="body">
          <div className="events-body">
            <EventsCard />
          </div>
        </div>
      </div>
      <AddNewEventCreate
        open={createNewEvent}
        onClose={() => setCreateNewEvent(false)}
      />
    </Drawer>
  );
};

export default EventCreate;
