import React, { useState } from "react";
import { Drawer } from "@material-ui/core";
import { Link } from "@mui/material";
import classes from "./showPost.module.css";
import LeftArrow from "../../../assets/icons/leftArrow.svg";
import DownloadIcon from "../../../assets/icons/Download.svg";
import ShowPostComment from "../ShowPostComment/showPostComment";

export default function ShowPost({ open, onClose, eventImage }) {
  const [showPostComment, setShowPostComment] = useState(false);

  const SocialMediaStats = ({ name }) => {
    return (
      <div
        className={classes.cardWrapper}
        onClick={() => setShowPostComment(true)}
      >
        <div className={classes.cardHeadingWrapper}>
          <div className={classes.cardTitle}>{name}</div>
          <div className={classes.cardSubTitle}>View live post</div>
        </div>
        <div className={classes.cardFooter}>
          <div className={classes.cardStats}>
            <span className={classes.cardStatsNumber}>1.3k</span> Reactions
          </div>
          <div className={classes.cardStats}>
            <span className={classes.cardStatsNumber}>109</span> Comments
          </div>
          <div className={classes.cardStats} />
          <div className={classes.cardStatsLast}>More...</div>
        </div>
      </div>
    );
  };

  return (
    <Drawer anchor="right" open={open} onClose={onClose}>
      <div role="presentation" className={classes.drawerWrapper}>
        <div className={classes.drawerHeaderFix}>
          <div className={classes.drawerHeadingWrapper}>
            <div className={classes.drawerHeading}>
              <Link
                className="side-close"
                underline="none"
                component="button"
                onClick={onClose}
              >
                <img src={LeftArrow} alt="close side drawer" />
              </Link>
              <div className={classes["sideDrawer-Title"]}>12 Aug Post</div>
            </div>
            <div>
              <div className={classes.viewPostButtonWrapper}>
                <img src={DownloadIcon} alt="download icon" />
                <div className={classes.blackTextButton}>Report</div>
              </div>
            </div>
          </div>
        </div>

        {/* post section */}

        <div className={classes.postWrapper}>
          <div className={classes.imageWrapper}>
            {eventImage && eventImage.preview && (
              <img
                src={eventImage.preview}
                alt="post"
                className={classes.postImage}
              />
            )}
          </div>
          <div className={classes.postDescription}>
            Lorem Ipsum is simply dummy text of the printing and typesetting
            industry. Lorem Ipsum has been the industry's standard dummy text
            ever since the 1500s
          </div>

          <div className={classes.postTiming}>12 Aug @ 2:00 PM</div>
          <hr className={classes.divider} />
        </div>
        <SocialMediaStats name="Facebook" />
        <hr className={classes.divider} />
        <SocialMediaStats name="Instagram" />
        <hr className={classes.divider} />
        <SocialMediaStats name="Twitter" />
        <hr className={classes.divider} />
        <SocialMediaStats name="Linkedin" />
      </div>

      <ShowPostComment
        open={showPostComment}
        onClose={() => setShowPostComment(false)}
      />
    </Drawer>
  );
}
