import { Hidden } from "@material-ui/core";
import React, { useEffect, useRef, useState } from "react";

import Scheduler from "../../Components/CalendarDashboard/Scheduler";
import Sidebar from "../../Components/Sidebar/Sidebar";
import BottomNavigation from "../../shared-components/mobile/BottomNavigation/BottomNavigation";
import { NavbarContainer } from "../../shared-components/mobile";
import { useBusiness } from "../../contexts/BusinessProvider";
import { useSidebar } from "../../contexts/SidebarProvider/SidebarProvider";
import CurateProvider from 'contexts/CurateProvider'

// styles
import useStyles from "./styles/muiStyles";

const CalendarDashboard = ({ location, history, ...rest }) => {
  const classes = useStyles();
  const {
    switchBusiness,
    selectedBusiness,
    allBusiness,
    getAllUserBusiness,
    fetchDefaultBusiness,
  } = useBusiness();

  const { menuCategory } = useSidebar();
  const [state, setState] = useState({
    toggled: location.hash === "#open" ? true : false,
    selectedLead: null,
    toggle_DropDown: false,
  });

  const toggleBar = () => {
    if (state.toggled) {
      history.push("#close");
    }
    if (!state.toggled) {
      history.push("#open");
    }
    return setState({ ...state, toggled: !state.toggled });
  };

  const switchBetweenBusiness = (e) => {
    if (selectedBusiness.id === e.target.value) {
      return;
    }

    const business = allBusiness.find(
      (data) => data.businessId === e.target.value
    );

    if (business) {
      switchBusiness(e.target.value, business);
      const urlSplit = location.pathname.split("/");
      const productName = urlSplit[1];
      if (business.plan_category.includes(productName)) {
        const menu = menuCategory.filter(
          (menu) => menu.productName === productName
        );
        const submenu = menu[0]["menu"].filter(
          (sub) => sub.url === "/curate/calendar"
        );
        if (
          (submenu[0].publishOnBusiness.length === 0 ||
            submenu[0].publishOnBusiness.includes(business.businessId)) &&
          submenu[0].published === true
        ) {
          window.location.reload();
        } else {
          window.location.href = business.redirectUrl;
        }
      } else {
        window.location.href = business.redirectUrl;
      }
    }
  };

  const loadDataRef = useRef(() => {
    !selectedBusiness && fetchDefaultBusiness();
    allBusiness.length < 1 && getAllUserBusiness();
  });

  useEffect(() => {
    loadDataRef.current();
  }, []);

  return (
    <CurateProvider>
      <Hidden smUp implementation="js">
        <NavbarContainer
          isLoading={false}
          open={state.toggled}
          switchBusiness={switchBetweenBusiness}
          toggleBar={toggleBar}
        />
      </Hidden>
      <div className={classes.container}>
        <div className="inner">
          <Hidden xsDown implementation="js">
            <Sidebar
              switchBetweenBusiness={switchBetweenBusiness}
              location={location}
              selectedBusiness={selectedBusiness}
              business={allBusiness}
              open={state.toggled}
              handleClose={toggleBar}
              className="sidebar"
            />
          </Hidden>
          <div className="content">
            <Scheduler />
          </div>
        </div>
        <Hidden implementation="js" smUp>
          <BottomNavigation />
        </Hidden>
      </div>
    </CurateProvider>
  );
};

export default CalendarDashboard;
