const ScheduleNewAssetPost = (formData) =>
  new Promise(async (resolve, reject) => {
    try {
      const AssetPostRes = await axios({
        url: `${BE_URL}/posts/schedule-new-asset-post`,
        method: "POST",
        data: formData,
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: AssetPostData } = AssetPostRes;

      return resolve({ AssetPostData });
    } catch (error) {
      return reject({ error });
    }
  });

