const FetchBusinessPostsForMonth = (monthIndex, year, paddingDays) =>
  new Promise(async (resolve, reject) => {
    try {
      const month_index = monthIndex;
      const padding_days = paddingDays;
      const res = await axios({
        url: `${API_V2}/business/posts/fetch-posts-month/${padding_days}/${month_index}/${year}`,
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          Authorization: `bearer ${localStorage.getItem("token")}`,
        },
      });

      // console.log('res', res);

      // const { data: BusinessPosts } = res;
      return resolve(res);
    } catch (error) {
      return reject({ message: error.message });
    }
  });

const UpdateScheduledAssetPost = (formData) =>
  new Promise(async (resolve, reject) => {
    try {
      const UpdateAssetPost = await axios({
        url: `${BE_URL}/posts/update-asset-post`,
        method: "PUT",
        data: formData,
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: UpdatePostData } = UpdateAssetPost;

      return resolve({ UpdatePostData });
    } catch (error) {
      return reject({ error: error.message });
    }
  });

  const DeleteScheduledPost = async (postID) => {
    try {
      const response = await fetch(`${API_V2}/business/posts/${postID}`, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          Authorization: `bearer ${localStorage.getItem("token")}`,
        },
      });
      return await response.json();
    } catch (error) {
      return { message: error.message };
    }
  };

