const fetchBusinessTimeZone = async () => {
  try {
    const response = await fetch(`${API_V2}/business/posts/timezone`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "X-BusinessId": localStorage.getItem("businessId"),
        Authorization: `bearer ${localStorage.getItem("token")}`,
      },
    });
    return await response.json();
  } catch (error) {
    return { message: error.message };
  }
};

const fetchPlatformPostIDs = (postID) =>
  new Promise(async (resolve, reject) => {
    try {
      const fetchIDsRes = await axios({
        url: `${BE_URL}/posts/platform-post-id`,
        method: "POST",
        data: {
          postID,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: postIDData } = fetchIDsRes;

      const { error, platform_post_ids } = postIDData;

      if (error) {
        return reject({ error });
      }

      return resolve({ platform_post_ids });
    } catch (error) {
      return reject({ error: error.message });
    }
  });

const deletePlatformPosts = (esPostID, platforms) =>
  new Promise(async (resolve, reject) => {
    try {
      const deletePostsRes = await axios({
        url: `${BE_URL}/posts/platform/delete`,
        method: "DELETE",
        data: {
          esPostID,
          platforms,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: deleteData } = deletePostsRes;

      // const { error, arePostsDeleted } = deleteData;
      const { error, success } = deleteData;

      if (error) {
        return reject({ error });
      }

      // return resolve({ arePostsDeleted });
      return resolve({ success });
    } catch (error) {
      return reject({ error: error.message });
    }
  });

const isPlatformPosted = (esPostID) =>
  new Promise(async (resolve, reject) => {
    try {
      const checkRes = await axios({
        url: `${BE_URL}/posts/check-platform-posted`,
        method: "POST",
        data: {
          esPostID,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: checkData } = checkRes;

      // const { error, arePostsDeleted } = deleteData;
      const { error, isPosted } = checkData;

      if (error) {
        return reject({ error });
      }

      // return resolve({ arePostsDeleted });
      return resolve({ isPosted });
    } catch (error) {
      return reject({ error: error.message });
    }
  });

export {
  ScheduleNewAssetPost,
  ScheduleNewTextPost,
  FetchBusinessPostsForMonth,
  UpdateScheduledAssetPost,
  DeleteScheduledPost,
  FetchPostData,
  ScheduleNewImagePost,
  ScheduleNewVideoPost,
  ScheduleNewGifPost,
  fetchBusinessTimeZone,
  fetchPlatformPostIDs,
  deletePlatformPosts,
  isPlatformPosted,
};
