import { MenuItem, Select } from "@material-ui/core";
import classes from "./Navbar.module.css";

const Navbar = ({
  open,
  isLoading,
  toggleBar,
  selectedBusiness,
  allBusiness,
  switchBetweenBusiness,
  shadow,
}) => {
  let attachedClasses1 = open
    ? [classes.toggles, classes.line1]
    : [classes.line1];
  let attachedClasses2 = open
    ? [classes.toggles, classes.line2]
    : [classes.line2];
  let attachedClasses3 = open
    ? [classes.toggles, classes.line3]
    : [classes.line3];

  console.log(selectedBusiness, allBusiness);

  return (
    <nav className={`${classes.Navbar} ${!shadow ? classes["no-shadow"] : ""}`}>
      {/* Burger Menu */}
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <div
          style={isLoading ? { zIndex: "unset" } : { zIndex: 201 }}
          className={classes.burger}
          onClick={toggleBar}
        >
          <div className={attachedClasses1.join(" ")}></div>
          <div className={attachedClasses2.join(" ")}></div>
          <div className={attachedClasses3.join(" ")}></div>
        </div>

        <div className={classes.Brand_Logo}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            xmlnsXlink="http://www.w3.org/1999/xlink"
            width="35px"
            height="35px"
            viewBox="0 0 35 38"
            version="1.1"
          >
            <title>Easy Social</title>
            <g
              id="Artboard"
              stroke="none"
              strokeWidth="1"
              fill="none"
              fillRule="evenodd"
            >
              <g id="Group-4" fillRule="nonzero">
                <g id="Group-5" fill="#12B0EE">
                  <path
                    d="M19.9620133,6.15384615 C27.5880534,6.15384615 33.7701804,12.3359732 33.7701804,19.9620133 C33.7701804,27.4821362 27.7585866,33.5981762 20.2789167,33.7666155 L19.9620133,33.7701804 L19.23804,33.7701804 L19.2026846,30.6932574 L19.9620133,30.6932574 C25.8887157,30.6932574 30.6932574,25.8887157 30.6932574,19.9620133 C30.6932574,14.0353109 25.8887157,9.23076923 19.9620133,9.23076923 C14.1324699,9.23076923 9.38859676,13.8790761 9.2346266,19.6714571 L9.23076923,19.9620133 L9.23076923,20.733685 L6.15384615,20.7353081 C6.15384615,20.5939938 6.15384615,20.3362288 6.15384615,19.9620133 C6.15384615,12.3359732 12.3359732,6.15384615 19.9620133,6.15384615 Z"
                    id="Oval"
                    transform="translate(19.962013, 19.962013) rotate(-135.000000) translate(-19.962013, -19.962013) "
                  />
                  <polygon
                    id="Path-3"
                    points="26.5562696 14.4 28.8 16.5979034 18.303427 27.2 12.8 21.6412462 15.0437304 19.4433428 18.3027745 22.7355558"
                  />
                </g>
              </g>
            </g>
          </svg>
        </div>
      </div>

      {/* Current Business Profile */}
      {allBusiness && allBusiness.length > 0 && (
        <div className={classes.Select_Business}>
          <Select
            classes={{ root: classes.Selector, select: classes.Select }}
            value={selectedBusiness.id}
            inputProps={{ "aria-label": "Without label" }}
            onChange={switchBetweenBusiness}
          >
            {allBusiness.map(({ business }, i) => {
              return (
                <MenuItem
                  key={i}
                  classes={{ root: classes.MenuItem }}
                  value={business.id}
                >
                  <p>{business.business_name}</p>
                </MenuItem>
              );
            })}
          </Select>
          <div className={classes.Business_Img}>
            {selectedBusiness && selectedBusiness.url && (
              <img loading="lazy" src={selectedBusiness.url} alt="profile" />
            )}
            {selectedBusiness &&
              selectedBusiness.id &&
              allBusiness &&
              allBusiness.length > 0 &&
              allBusiness
                .filter(({ business }) => {
                  return business.id === selectedBusiness.id;
                })
                .map(({ business }, i) => (
                  <img
                    key={i}
                    loading="lazy"
                    src={business.logo}
                    alt={
                      business.logo ? `Business: ${business.business_name}` : ""
                    }
                  />
                ))}
          </div>
        </div>
      )}
    </nav>
  );
};

export default Navbar;
