const formatCommentTime: Function = (
  platformName: string,
  commentDateTime: string
): string => {
  let outputDateTimeString: string = "";

  // NOTE: Check date time format for linkedin and parse it accordingly

  const time: string = new Date(commentDateTime).toLocaleTimeString([], {
    hour: "2-digit",
    minute: "2-digit",
    hour12: true,
  });

  const date: string = new Date(commentDateTime).toLocaleDateString([], {
    year: "numeric",
    month: "long",
    day: "numeric",
  });

  outputDateTimeString = `${date}, ${time.toUpperCase()}`;

  return outputDateTimeString;
};

export default formatCommentTime;
