const validatePostAsset = (asset) => {
  const { type, size } = asset;

  // type limits
  const IMG_TYPE = ["image/jpeg", "image/jpg", "image/png"];

  const VID_TYPE = ["video/quicktime", "video/mp4"];

  const assetInfo = {};

  const IMG_MB = 5;
  const VID_MB = 100;
  const GIF_MB = 15;

  // sizes limits
  const MAX_IMG_SIZE = 1024 * 1024 * IMG_MB; // 15 MB
  const MAX_VID_SIZE = 1024 * 1024 * VID_MB; // 100 MB
  const MAX_GIF_SIZE = 1024 * 1024 * GIF_MB; // 15 MB

  if (IMG_TYPE.includes(type)) {
    if (size <= MAX_IMG_SIZE) {
      assetInfo.isAssetValid = true;
      assetInfo.type = "image";

      return assetInfo;
    }

    return { error: `Images must be less than ${IMG_MB} MB.` };
  } else if (VID_TYPE.includes(type)) {
    if (size <= MAX_VID_SIZE) {
      assetInfo.isAssetValid = true;
      assetInfo.type = "video";

      return assetInfo;
    }

    return { error: `Videos must be less than ${VID_MB} MB.` };
  } else if (type === "image/gif") {
    // if (size <= MAX_GIF_SIZE) {
    //   assetInfo.isAssetValid = true;
    //   assetInfo.type = "gif";

    //   return assetInfo;
    // }

    // return { error: `GIFs must be less than ${GIF_MB} MB.` };

    return { error: `Cannot schedule GIF posts!` };
  } else {
    // invalid
    return { error: "Only images, videos and GIFs allowed." };
  }
};

module.exports = validatePostAsset;
