import { useState, useEffect } from "react";
import {
  Drawer,
  IconButton,
  MenuItem,
  Button,
  withStyles,
} from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";

import {
  addStrategy,
  updateStrategy,
} from "Store/calendarDashboard/strategy/strategySlice";
import leftArrow from "assets/icons/leftArrow.svg";
import {
  CustomInputField,
  CustomTextFieldFilled,
} from "../../../UI/CustomTextField/CustomTextField";

// request helpers imports
import { CreateNewPostStrategy, EditPostStrategy } from "api/PostStrategy";

// contexts
import { useToastMessage } from "contexts/ToastMessageContext";

// styles
import useStyles from "../styles/muiStyles";

// component imports
import PlatformIcons from "../PlatformIcons";
import { cpuUsage } from "process";

export default function StrategyCreate({
  open,
  onClose,
  isPopulated,
  populatingData,
  strategyID,
}) {
  console.log("populatingData", populatingData);
  const classes = useStyles();
  const dispatch = useDispatch();
  const defaultData = isPopulated
    ? populatingData
    : {
        name: "",
        type: "",
        desc: "",
        color: {
          name: "",
          value: "",
        },
      };
  const strategyState = useSelector((state) => state.strategyState);
  const [strategyData, setStrategyData] = useState(defaultData);

  const [showColorDropDown, hideColorDropDown] = useState(false);
  const [creating, setCreating] = useState(false);
  const { setErrorMessage, setSuccessMessage } = useToastMessage();
  // const [showFB, setShowFB] = useState(false);
  // const [showIG, setShowIG] = useState(false);
  // const [showTW, setShowTW] = useState(false);
  // const [showLI, setShowLI] = useState(false);
  const [showIconsTitle, setShowIconsTitle] = useState(false);

  // styles
  const FieldOption = withStyles({
    root: {
      alignItems: "center",
      columnGap: "10px",
      display: "flex",
      justifyContent: "flex-start",

      "& .color": {
        borderRadius: "50%",
        display: "flex",
        height: "13px",
        width: "13px",
      },

      "& .label": {
        color: "#24272C",
        fontSize: "14px",
        fontWeight: 400,
        lineHeight: "21px",
      },
    },
  })(MenuItem);

  // component effects
  useEffect(() => {
    if (strategyData.type) {
      setShowIconsTitle(true);
    }
  }, [strategyData.type]);

  useEffect(() => {
    if (!open) {
      setStrategyData({ ...strategyData, type: "" });
      setShowIconsTitle(false);
    }
    // eslint-disable-next-line
  }, [open]);

  const handleInputChange = (field, value) => {
    if (field === "color") {
      const newVal = strategyState.colorAvailable.find(
        (data) => data.hexCode === value
      );
      setStrategyData((prevState) => ({
        ...prevState,
        color: { name: newVal.name, value: newVal.hexCode },
      }));
    } else {
      setStrategyData((prevState) => ({
        ...prevState,
        [field]: value,
      }));
    }
  };

  // eslint-disable-next-line no-unused-vars
  const handleSelectedColor = ({ name, hexCode }) => {
    setStrategyData({
      ...strategyData,
      color: {
        name: name,
        value: hexCode,
      },
    });
    hideColorDropDown(false);
  };

  const handleClearState = () => {
    setStrategyData({
      name: "",
      desc: "",
      color: {
        name: "",
        value: "",
      },
    });

    hideColorDropDown(false);
  };

  const submitStrategyHandler = async () => {
    if (
      strategyData.name === "" ||
      strategyData.desc === "" ||
      strategyData.color.name === ""
    ) {
      setErrorMessage("All fields are required");
    } else {
      if (isPopulated) {
        // TODO: Send request to update strategy data
        const requestStrategyData = {
          name: strategyData.name,
          description: strategyData.desc,
          color: strategyData.color,
          strategy_type: strategyData.type,
        };
        try {
          setCreating(true);
          // const editStrategyData = await EditPostStrategy(
          //   businessID,
          //   requestStrategyData
          // );
          const res = await EditPostStrategy(requestStrategyData, strategyID);
          if (!res.success) {
            setErrorMessage(
              res.messages ? res.messages.errors[0].message : res.message
            );
          } else {
            dispatch(
              updateStrategy({
                name: res.payload.name,
                description: res.payload.description,
                desc: res.payload.description,
                color: res.payload.color,
                strategyID: res.payload.id,
                businessID: res.payload.business_id,
                type: res.payload.strategy_type,
                isDefault: false,
              })
            );

            setShowIconsTitle(false);
            setSuccessMessage("Strategy edited!");
          }
        } catch (error) {
          setErrorMessage(error.message);
        } finally {
          setCreating(false);
        }
      } else {
        // TODO: Send request to add strategy data
        const requestStrategyData = {
          name: strategyData.name,
          description: strategyData.desc,
          color: strategyData.color,
          strategy_type: strategyData.type,
        };

        try {
          setCreating(true);
          const res = await CreateNewPostStrategy(requestStrategyData);
          if (!res.success) {
            return setErrorMessage(res.message);
          }
          const { payload } = res;
          dispatch(
            addStrategy({
              name: payload.name,
              description: payload.description,
              desc: payload.description,
              color: payload.color,
              strategyID: payload.id,
              type: payload.strategy_type,
              isDefault: false,
            })
          );
          setShowIconsTitle(false);
          setSuccessMessage(res.message);
        } catch (error) {
          setErrorMessage(error.message);
        } finally {
          setCreating(false);
        }
      }
      onClose();
      handleClearState();
    }
  };

  return (
    <Drawer
      anchor={"right"}
      hideBackdrop={true}
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") && onClose()
      }
      className={classes["new-strategy"]}
    >
      <div className="wrapper">
        <div className="header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            onClick={() => {
              onClose();
            }}
            style={{ color: "transparent", padding: 0 }}
            disableFocusRipple={true}
            disableRipple={true}
          >
            <img alt="back" src={leftArrow} style={{ margin: "7.5px 10px" }} />
          </IconButton>
          <h1 className="title">
            {isPopulated ? "Edit Strategy" : "Create a strategy"}
          </h1>
        </div>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            submitStrategyHandler();
          }}
        >
          <div className="field">
            <CustomTextFieldFilled
              type="text"
              className={classes["form-input"]}
              placeholder="Strategy Name"
              label="Strategy Name"
              value={strategyData.name}
              onChange={(e) => handleInputChange("name", e.target.value)}
            />
          </div>
          <div className="field">
            <CustomTextFieldFilled
              select
              label="Strategy Type"
              placeholder="Strategy Type"
              value={strategyData.type}
              onChange={({ target: { value } }) =>
                handleInputChange("type", value)
              }
              variant="filled"
              type="select"
              inputProps={{ "aria-label": "Without label" }}
              fullWidth
            >
              <MenuItem value={""} disabled>
                {"Select Strategy Type"}
              </MenuItem>
              {[
                { label: "Image or Video Content", value: "image_video" },
                { label: "Text Post", value: "text" },
                { label: "Album", value: "album" },
                { label: "Facebook Carousel", value: "facebook_carousel" },
                { label: "Cover Picture", value: "cover_picture" },
              ].map(({ label, value }, index) => (
                <MenuItem key={index} value={value}>
                  {label}
                </MenuItem>
              ))}
            </CustomTextFieldFilled>
          </div>
          <div className="field">
            <PlatformIcons
              strategyType={strategyData.type}
              showTitle={showIconsTitle}
            />
          </div>
          <div className="field">
            <CustomTextFieldFilled
              select
              label="Strategy Color"
              placeholder="Strategy Color"
              value={strategyData.color.value}
              onChange={({ target: { value } }) =>
                handleInputChange("color", value)
              }
              variant="filled"
              type="select"
              className="color-select"
              inputProps={{ "aria-label": "Without label" }}
              fullWidth
            >
              <FieldOption value={""} disabled>
                <span className="label">{"Select Strategy Color"}</span>
              </FieldOption>
              {strategyState.colorAvailable.map(({ name, hexCode }, index) => (
                <FieldOption key={index} value={hexCode}>
                  <span
                    className="color"
                    style={{ backgroundColor: hexCode }}
                  />
                  <span className="label">{name}</span>
                </FieldOption>
              ))}
            </CustomTextFieldFilled>
          </div>
          <div className="field">
            <CustomInputField
              multiline
              placeholder="Description"
              rows={5}
              value={strategyData.description ?? strategyData.desc}
              onChange={(e) => handleInputChange("desc", e.target.value)}
            />
          </div>
          <div className="actions">
            <Button variant="contained" type="submit" disabled={creating}>
              {"Save"}
            </Button>
          </div>
        </form>
      </div>
    </Drawer>
  );
}
