// react imports
import { FC, Fragment, useState, useEffect } from "react";

// icons assets imports
import Facebook from "../../assets/fb.svg";
import Instagram from "../../assets/insta.svg";
import LinkedIn from "../../assets/LIn.svg";
import Twitter from "../../assets/tweet.svg";

// interface imports
import { IPlatformIconsProps } from "./interfaces";

const PlatformIcons: FC<IPlatformIconsProps> = ({
  strategyType,
  showTitle,
}: IPlatformIconsProps): JSX.Element => {
  // component states
  const [showFB, setShowFB] = useState<boolean>(false);
  const [showIG, setShowIG] = useState<boolean>(false);
  const [showTW, setShowTW] = useState<boolean>(false);
  const [showLI, setShowLI] = useState<boolean>(false);

  // component effects
  useEffect(() => {
    switch (strategyType) {
      case "image_video":
        setShowFB(true);
        setShowIG(true);
        setShowTW(true);
        setShowLI(true);
        break;

      case "text":
        setShowFB(true);
        setShowIG(false);
        setShowTW(true);
        setShowLI(true);
        break;

      case "album":
        setShowFB(true);
        setShowIG(false);
        setShowTW(true);
        setShowLI(false);
        break;

      case "facebook_carousel":
        setShowFB(true);
        setShowIG(false);
        setShowTW(false);
        setShowLI(false);
        break;

      case "cover_picture":
        setShowFB(true);
        setShowIG(false);
        setShowTW(true);
        setShowLI(false);
        break;

      default:
        setShowFB(false);
        setShowIG(false);
        setShowTW(false);
        setShowLI(false);
    }
  }, [strategyType]);

  return (
    <Fragment>
      {showTitle && <h5 className="field-title dark">{"Allowed Platforms"}</h5>}
      <div className="social-platform">
        {showFB && (
          <span className="platform">
            <img src={Facebook} className="social-icon" alt="Facebook" />
          </span>
        )}
        {showIG && (
          <span className="platform">
            <img src={Instagram} className="social-icon" alt="Instagram" />
          </span>
        )}
        {showTW && (
          <span className="platform">
            <img src={Twitter} className="social-icon" alt="Twitter" />
          </span>
        )}
        {showLI && (
          <span className="platform">
            <img src={LinkedIn} className="social-icon" alt="LinkedIn" />
          </span>
        )}
      </div>
    </Fragment>
  );
};

export default PlatformIcons;
