import React, { Fragment, useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Checkbox, Link, ListItem, ListItemIcon } from "@mui/material";
import {
  updateCheckBox,
  deleteStrategy,
} from "Store/calendarDashboard/strategy/strategySlice";
import StrategyCreate from "../StrategyCreate";

// request helpers imports
import { DeletePostStrategy } from "api/PostStrategy";

// contexts imports
import { useToastMessage } from "contexts/ToastMessageContext";

// component imports
import PlatformIcons from "../PlatformIcons";

// allowed strategy types
const strategyTypes = [
  { label: "Image or Video Content", value: "image_video" },
  { label: "Text Post", value: "text" },
  { label: "Album", value: "album" },
  { label: "Facebook Carousel", value: "facebook_carousel" },
  { label: "Cover Picture", value: "cover_picture" },
];

export default function StrategyCard({
  key,
  name,
  desc,
  isDefault,
  color,
  strategyID,
  strategyDetails,
}) {
  
  const [editStrategy, setEditStrategy] = useState(false);
  const [strategyTypeLabel, setStrategyTypeLabel] = useState("");
  const strategyState = useSelector((state) => state.strategyState);
  const dispatch = useDispatch();

  const { setErrorMessage, setSuccessMessage } = useToastMessage();

  const handleStrategyCheck = (e, name) => {
    const { checked } = e.target;
    dispatch(updateCheckBox({ name, value: checked }));
  };

  const data = { ...strategyDetails };

  // component effects
  useEffect(() => {
    const selectedStrategy = strategyTypes.find(
      (strategy) => strategy.value === strategyDetails.type
    );

    if (selectedStrategy) {
      setStrategyTypeLabel(selectedStrategy.label);
    }
  }, [strategyDetails]);

  const handleStrategyDelete = async () => {
    try {
      if (!window?.confirm("Are you sure you want to delete?")) {
        return;
      }
      const res = await DeletePostStrategy(strategyID);
      if (!res.success) {
        setErrorMessage(res.message);
      } else {
        dispatch(deleteStrategy({ strategyID: strategyID, color }));
        setSuccessMessage(res.message);
      }
    } catch (error) {
      setErrorMessage(error.message);
    }
  };

  return (
    <Fragment>
      <ListItem disablePadding={true} sx={{ p: 0, alignItems: "flex-start" }}>
        <ListItemIcon>
          <Checkbox
            checked={strategyState.strategyStatus[name]}
            onChange={(e) => handleStrategyCheck(e, name)}
            inputProps={{ "aria-label": "controlled" }}
            sx={{
              color: "#12B0EE",

              "&.Mui-checked": {
                color: "#12B0EE",
              },
            }}
          />
        </ListItemIcon>
        <div className="info">
          <h4
            className="strategy-name"
            style={{ borderLeftColor: color.value }}
          >
            {name}
          </h4>
          <div className="details">
            {/* <h5 className="type">{"Image / Video Post"}</h5> */}
            <h5 className="type">{strategyTypeLabel}</h5>
            <p className="desc">{desc}</p>
            <PlatformIcons strategyType={strategyDetails.type} />
          </div>
        </div>
        {!isDefault && (
          <div className="actions">
            <Link
              onClick={() => setEditStrategy(true)}
              underline="always"
              component="button"
            >
              {"Edit"}
            </Link>
            <Link
              onClick={() => handleStrategyDelete(color)}
              underline="always"
              component="button"
            >
              {"Delete"}
            </Link>
          </div>
        )}
      </ListItem>
      {editStrategy && (
        <StrategyCreate
          open={editStrategy}
          onClose={() => setEditStrategy(false)}
          isPopulated
          populatingData={data}
          strategyID={strategyID}
        />
      )}
    </Fragment>
  );
}
