import { Fragment, useState, useEffect } from "react";
import {
  Autocomplete,
  Button,
  Drawer,
  Popper,
  IconButton,
  Link,
  MenuItem,
} from "@mui/material";
import { Switch } from "@material-ui/core";
import { styled } from "@mui/material/styles";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import { DatePicker, TimePicker, LocalizationProvider } from "@mui/lab";
import moment from "moment";
import { useSelector } from "react-redux";
import Triangle from "assets/icons/Triangle.svg";
import Facebook from "../assets/fb.svg";
import Instagram from "../assets/insta.svg";
import Linkedin from "../assets/LIn.svg";
import Twitter from "../assets/tweet.svg";
import { addPost, updatePost } from "Store/calendarDashboard/posts/postSlice";
import { useDispatch } from "react-redux";
import AdvanceSettingCreate from "../AdvanceSettingCreate/advanceSettingCreate";
import Warn from "assets/icons/warn.svg";
import { formatDate } from "utils/calendarDashboard";
import Drag from "./Drag/drag";
import { v4 as uuid } from "uuid";
import CustomIcon from "shared-components/CustomIcon/CustomIcon";
import {
  CustomInputField,
  CustomTextFieldFilled,
} from "../../UI/CustomTextField/CustomTextField";

// request helper imports
import { FetchBusinessStrategiesAll } from "api/PostStrategy";

// reducer imports
import { fetchStrategies } from "Store/calendarDashboard/strategy/strategySlice";
import { useToastMessage } from "contexts/ToastMessageContext";
import { useBusiness } from "contexts/BusinessProvider";

// posts request helpers
import {
  UpdateScheduledAssetPost,
  ScheduleNewImagePost,
  ScheduleNewVideoPost,
  ScheduleNewGifPost,
} from "api/Posts";

// utils imports
import validatePostAsset from "./utils/ValidatePostAsset";
import getBusinessPostTime from "./utils/GetBusinessPostTime";
import validatePostDateTime from "./utils/ValidatePostDateTime";
// styles
import useStyles from "./styles/muiStyles";
import PlatformIcons from "../Strategy/PlatformIcons";

const PostCreate = ({
  open,
  onClose,
  hideBackdrop = false,
  isEdit = false,
  isPopulated = false,
  eventImage,
  currentPostID,
  populatingData = {},
  strategy = "",
}) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [advanceSetting, setAdvanceSetting] = useState(false);
  const [, setShowDropDown] = useState(false);
  const strategyState = useSelector((state) => state.strategyState);
  const [postDate, setPostDate] = useState(moment().toDate());
  const [postTime, setPostTime] = useState(
    moment().add(16, "minutes").toDate()
  );
  const [addBriefing, toggleBriefing] = useState(false);
  const [refURLCount, setRefURLCount] = useState(0);

  const [showTwitterInputBox, setShowTwitterInputBox] = useState(false);

  const initialStrategy = strategy ? strategyState.strategyList[0] : "";

  const [selectedStrategy, setSelectedStrategy] = useState(initialStrategy);

  const [defaultStrategy, setDefaultStrategy] = useState(null);

  const [allStrategies, setAllStrategies] = useState([]);
  const [submitDisable, setSubmitDisable] = useState(false);

  // const [selectedStrategy, setSelectedStrategy] = useState(null);

  const [assetType, setAssetType] = useState(null);

  const [files, setFiles] = useState([]);

  const { setErrorMessage, setSuccessMessage } = useToastMessage();

  const { selectedBusiness } = useBusiness();

  const initialData = isPopulated
    ? { ...populatingData }
    : {
        postInstruction: "",
        postDesc: "",
        twitterDesc: "",
        postDate: "",
        postTime: "",
        isAdvanceActivated: advanceSetting,
        facebookDate: "",
        facebookTime: "",
        instagramTime: "",
        instagramDate: "",
        twitterTime: "",
        twitterDate: "",
        linkedinDate: "",
        ctaType: "",
        ctaURL: "",
        tagPeople: "",
        tagPlace: "",
        linkedinTime: "",
        postID: "",
        postType: "",
      };
  const [postData, setPostData] = useState({
    ...initialData,
  });

  const StyledPopper = styled(Popper)({
    [`& .MuiAutocomplete-listbox`]: {
      "& .MuiMenuItem-root": {
        alignItems: "flex-start",
        flexDirection: "column",
        padding: "15px 30px",
        whiteSpace: "initial",
        rowGap: "4px",

        "&:not(:last-child)": {
          borderBottom: "1px solid #0000001A",
        },

        "& .option-label": {
          color: "#24272C",
          fontSize: "15px",
          fontWeight: 500,
          lineHeight: "18px",
        },

        "& .option-attr": {
          alignItems: "flex-start",
          display: "flex",
          width: "100%",

          "& .option-sub-label": {
            color: "#00000080",
            fontSize: "12px",
            fontWeight: 700,
            lineHeight: "16px",
            flex: "1 1 0",
          },

          "& .option-platforms": {
            display: "flex",
            flex: "0 1 0",

            "& .social-platform": {
              display: "flex",
              gap: "9px",
            },

            "& .platform": {
              lineHeight: "16px",

              "&:not(:last-child)": {
                "& img": {
                  marginRight: "6px",
                },
              },
            },
          },
        },

        "& .option-desc": {
          color: "#00000080",
          fontSize: "12px",
          fontWeight: 400,
          lineHeight: "16px",
        },
      },
    },
  });

  const showTwitterButtonHandler = () => {
    const text = postData.postDesc.substr(0, 180);
    setPostData({
      ...postData,
      twitterDesc: text,
    });
    setShowTwitterInputBox(true);
  };

  // fetch all the strategies

  useEffect(() => {
    if (open) {
      setSubmitDisable(false);
    }
  }, [open]);

  useEffect(() => {
    if (open && selectedBusiness && selectedBusiness.id) {
      FetchBusinessStrategiesAll()
        .then((res) => {
          if (!res.success) {
            return setErrorMessage("Fetch strategy err: ", res.message);
          }

          const toDispatchStrategies = res.payload.map((strategy) => {
            return {
              id: strategy.id,
              color: strategy.color,
              label: strategy.name,
              name: strategy.name,
              type: strategy.strategy_type,
              description: strategy.description,
              businessID: strategy.business_id,
              isDefault: strategy.is_default || false,
            };
          });

          /*           const eventStrategy = toDispatchStrategies.find(
            (s) => s.label === "Event"
          );

          setDefaultStrategy(eventStrategy); */

          setAllStrategies(toDispatchStrategies);

          dispatch(fetchStrategies({ data: toDispatchStrategies }));
        })
        .catch((err) => {
          return setErrorMessage("Fetch strategy err: ", err.message);
        });
    }
  }, [open, selectedBusiness]);

  useEffect(() => {
    let [month, day, year] = new Date().toLocaleDateString().split("/");

    month = month.length < 2 ? `0${month}` : month;
    day = day.length < 2 ? `0${day}` : day;

    const requiredDate = `${year}-${month}-${day}`;

    let [hour, min, sec] = new Date(Date.now() + 1000 * 60 * 10)
      .toLocaleTimeString()
      .split(":");

    const meridian = sec.split(" ").pop();

    // hour = meridian === "PM" ? parseInt(hour) + 12 + "" : hour;

    if (meridian === "PM") {
      if (parseInt(hour) !== 12) {
        hour = parseInt(hour) + 12 + "";
      }
    }

    hour = hour.length < 2 ? `0${hour}` : hour;
    min = min.length < 2 ? `0${min}` : min;

    const requiredTime = `${hour}:${min}`;

    setPostData({
      ...postData,
      postDate: requiredDate,
      postTime: requiredTime,
    });

    // }, [open, postData]);
    // eslint-disable-next-line
  }, [open]);

  const handleFinalSubmit = async (event, action) => {
    event.preventDefault();

    setSubmitDisable(true);

    const businessID = localStorage.getItem("businessId");

    // const businessID = "6064538f2f94727c34a4d6a9";

    if (
      postData.postDate === "" ||
      postData.postTime === "" ||
      postData.postDesc === ""
      // Object.keys(selectedStrategy).length === 0
      // selectedStrategy.length === 0
    ) {
      setSubmitDisable(false);
      setErrorMessage("fields can not be empty");
    } else {
      if (isEdit) {
        const fbScheduleDateTime =
          postData.facebookDate && postData.facebookTime
            ? `${postData.facebookDate}T${postData.facebookTime}:00`
            : "";

        const igScheduleDateTime =
          postData.instagramDate && postData.instagramTime
            ? `${postData.instagramDate}T${postData.instagramTime}:00`
            : "";

        const twScheduleDateTime =
          postData.twitterDate && postData.twitterTime
            ? `${postData.twitterDate}T${postData.twitterTime}:00`
            : "";

        const liScheduleDateTime =
          postData.linkedinDate && postData.linkedinTime
            ? `${postData.linkedinDate}T${postData.linkedinTime}:00`
            : "";

        const formData = new FormData();

        formData.append("asset", files[0]);
        formData.append("postID", currentPostID);
        formData.append("action", "update");
        formData.append("businessID", businessID);
        formData.append("strategyID", "617bb84fda289f37b08a995a");
        formData.append("instructions", postData.postInstruction);
        formData.append("content", postData.postDesc);
        formData.append(
          "scheduleDateTime",
          `${postData.postDate}T${postData.postTime}:00`
        );
        formData.append("fbScheduleDateTime", fbScheduleDateTime);
        formData.append("igScheduleDateTime", igScheduleDateTime);
        formData.append("twScheduleDateTime", twScheduleDateTime);
        formData.append("liScheduleDateTime", liScheduleDateTime);
        formData.append("cta_type", postData.ctaType);
        formData.append("cta_value", postData.ctaURL);
        formData.append("igUsernames", postData.tagPeople);

        try {
          const { UpdatePostData } = await UpdateScheduledAssetPost(formData);

          setSuccessMessage(`Scheduled post updated!`);
        } catch (error) {
          console.log("Update post error: ", error);

          const { message, error: err } = error;

          if (message) {
            setErrorMessage(message);
          } else if (err) {
            setErrorMessage(err);
          } else {
            setErrorMessage(error);
          }
        }

        dispatch(
          updatePost({
            postID: currentPostID,
            postDetails: { ...postData, postImage: files[0] },
            event: {
              day: formatDate(postData.postDate),
              strategy: selectedStrategy,
              isDraft: false,
            },
          })
        );

        setSubmitDisable(false);
      } else {
        // Send create post request

        const fbScheduleDateTime =
          postData.facebookDate && postData.facebookTime
            ? `${postData.facebookDate}T${postData.facebookTime}:00`
            : "";

        const igScheduleDateTime =
          postData.instagramDate && postData.instagramTime
            ? `${postData.instagramDate}T${postData.instagramTime}:00`
            : "";

        const twScheduleDateTime =
          postData.twitterDate && postData.twitterTime
            ? `${postData.twitterDate}T${postData.twitterTime}:00`
            : "";

        const liScheduleDateTime =
          postData.linkedinDate && postData.linkedinTime
            ? `${postData.linkedinDate}T${postData.linkedinTime}:00`
            : "";

        const formData = new FormData();

        // console.log('postTime', postTime);

        // return false;

        const scheduleDateTime = `${moment(postDate).format(
          "YYYY-MM-DD"
        )}T${moment(postTime).format("HH:mm")}:00`;

        // convert to business post time
        let scheduledFormattedTime;
        let fbFormattedTime;
        let igFormattedTime;
        let twFormattedTime;
        let liFormattedTime;

        const timeConversionArray = [];

        timeConversionArray.push(
          getBusinessPostTime(scheduleDateTime)
            .then((scheduledTimeData) => {
              scheduledFormattedTime = scheduledTimeData.formattedPostTime;
            })
            .catch((error) => {
              console.log("Business post time format error: ", error);
            })
        );

        if (fbScheduleDateTime) {
          timeConversionArray.push(
            getBusinessPostTime(fbScheduleDateTime)
              .then((scheduledTimeData) => {
                fbFormattedTime = scheduledTimeData.formattedPostTime;
              })
              .catch((error) => {
                console.log("Business post time format error: ", error);
              })
          );
        }

        if (igScheduleDateTime) {
          timeConversionArray.push(
            getBusinessPostTime(igScheduleDateTime)
              .then((scheduledTimeData) => {
                igFormattedTime = scheduledTimeData.formattedPostTime;
              })
              .catch((error) => {
                console.log("Business post time format error: ", error);
              })
          );
        }

        if (twScheduleDateTime) {
          timeConversionArray.push(
            getBusinessPostTime(twScheduleDateTime)
              .then((scheduledTimeData) => {
                twFormattedTime = scheduledTimeData.formattedPostTime;
              })
              .catch((error) => {
                console.log("Business post time format error: ", error);
              })
          );
        }

        if (liScheduleDateTime) {
          timeConversionArray.push(
            getBusinessPostTime(liScheduleDateTime)
              .then((scheduledTimeData) => {
                liFormattedTime = scheduledTimeData.formattedPostTime;
              })
              .catch((error) => {
                console.log("Business post time format error: ", error);
              })
          );
        }

        try {
          await Promise.all(timeConversionArray);
        } catch (error) {
          console.log("Timezone formatting error: ", error);
        }
        // validate times
        if (
          scheduledFormattedTime &&
          !validatePostDateTime(scheduledFormattedTime)
        ) {
          setSubmitDisable(false);
          return setErrorMessage(
            `Post schedule time should be at least 15 mins from now!`
          );
        }

        if (fbFormattedTime && !validatePostDateTime(fbFormattedTime)) {
          setSubmitDisable(false);
          return setErrorMessage(
            `Facebook schedule time should be at least 10 mins from now!`
          );
        }

        if (igFormattedTime && !validatePostDateTime(igFormattedTime)) {
          setSubmitDisable(false);
          return setErrorMessage(
            `Instagram schedule time should be at least 10 mins from now!`
          );
        }

        if (twFormattedTime && !validatePostDateTime(twFormattedTime)) {
          setSubmitDisable(false);
          return setErrorMessage(
            `Twitter schedule time should be at least 10 mins from now!`
          );
        }

        if (liFormattedTime && !validatePostDateTime(liFormattedTime)) {
          setSubmitDisable(false);
          return setErrorMessage(
            `LinkedIn schedule time should be at least 10 mins from now!`
          );
        }

        formData.append("asset", files[0]);
        formData.append("strategy_id", selectedStrategy.id);

        //formData.append("strategyID", "621f40b2f004b8397418c407");
        // formData.append("action", "create");
        formData.append("action", action);
        formData.append("instructions", postData.postInstruction);
        formData.append("content", postData.postDesc);

        formData.append("schedule_date_time", scheduledFormattedTime);

        if (fbFormattedTime) {
          formData.append("fbScheduleDateTime", fbFormattedTime);
        }

        if (igFormattedTime) {
          formData.append("igScheduleDateTime", igFormattedTime);
        }

        if (twFormattedTime) {
          formData.append("twScheduleDateTime", twFormattedTime);
        }

        if (liFormattedTime) {
          formData.append("liScheduleDateTime", liFormattedTime);
        }

        formData.append("cta_type", postData.ctaType);
        formData.append("cta_value", postData.ctaURL);
        formData.append("igUsernames", postData.tagPeople);

        formData.append("platforms", "facebook");
        formData.append("platforms", "instagram");
        formData.append("platforms", "twitter");
        formData.append("platforms", "linkedin");

        try {
          let schedulePostData;

          if (assetType && assetType === "image") {
            const res = await ScheduleNewImagePost(formData);
            if (res.errors) {
              return setErrorMessage(res.errors[0].message);
            }
            schedulePostData = res.payload;
          } else if (assetType && assetType === "video") {
            const { AssetPostData } = await ScheduleNewVideoPost(formData);
            schedulePostData = AssetPostData;
          } else if (assetType && assetType === "gif") {
            const { AssetPostData } = await ScheduleNewGifPost(formData);
            schedulePostData = AssetPostData;
          } else {
            setSubmitDisable(false);
            if (files.length === 0) {
              return setErrorMessage("Please provide a post asset!");
            }

            return setErrorMessage("Invalid asset type!");
          }

          const {
            error,
            PSQDocs,
            postDraft,
            NewScheduledAssetPost,
            scheduled_post,
          } = schedulePostData;

          if (error) {
            setSubmitDisable(false);
            return setErrorMessage(error);
          }

          let addPostData = {};

          if (action === "draft" && postDraft) {
            const dt = new Date(postDraft.scheduleDateTime);

            const [postTime] = dt
              .toLocaleTimeString("en-US", {
                hour: "2-digit",
                minute: "2-digit",
              })
              .split(" ");

            let postSubtitle = `Drafted for ${postTime}`;

            // find the strategy data using strategy ID
            // const strategy = allStrategies.find(
            //   (s) => s.strategyID === selectedStrategy
            // );

            addPostData = {
              postID: postDraft._id,
              postDetails: {
                ...postData,
                postImage: postDraft.thumbnailURL || postDraft.assetURL,
                postSubtitle,
                showSuccessIcon: false,
              },
              // event: {
              //   day: formatDate(postData.postDate),
              //   strategy: selectedStrategy,
              //   isDraft: false,
              // },
              event: {
                day: formatDate(postData.postDate),
                // strategy: {
                //   strategyID: selectedStrategy,
                // },
                strategy: defaultStrategy,
                isDraft: true,
              },
              post: {
                _id: postDraft._id,
              },
            };
          }

          if (action === "create" && scheduled_post) {
            // const dt = new Date(schedulePostData.PSQDocs[0].scheduleDateTime);
            const dt = new Date(scheduled_post.schedule_date_time);

            const [postDate] = scheduled_post.schedule_date_time.split("T");

            const [postTime] = dt
              .toLocaleTimeString("en-US", {
                hour: "2-digit",
                minute: "2-digit",
              })
              .split(" ");

            let postSubtitle = `Scheduled for ${postTime}`;

            // find the strategy data using strategy ID
            // const strategy = allStrategies.find(
            //   (s) => s.strategyID === selectedStrategy
            // );

            const postStrategy = allStrategies.find(
              (s) => s.id === selectedStrategy.id
            );

            addPostData = {
              // postID: schedulePostData.PSQDocs[0].esPostID,
              postID: scheduled_post.id,
              postDetails: {
                ...scheduled_post,
                postImage: scheduled_post.asset_url,
                //schedulePostData.NewScheduledAssetPost.assetURL,
                postSubtitle,
                showSuccessIcon: false,
              },
              // event: {
              //   day: formatDate(postData.postDate),
              //   strategy: selectedStrategy,
              //   isDraft: false,
              // },
              event: {
                day: formatDate(moment(postDate).format("YYYY-MM-DD")),
                // strategy: {
                //   strategyID: selectedStrategy,
                // },
                strategy: postStrategy,
                isDraft: false,
              },
             // post: {
              //  _id: scheduled_post.id,
             // },
             post: {...scheduled_post}
            };

            // return false;

            // if (action === "create" && schedulePostData.PSQDocs.length) {
            if (action === "create" && schedulePostData.scheduled_post) {
              setSuccessMessage(`New ${assetType} post scheduled!`);
            }

            if (action === "draft" && postDraft) {
              setSuccessMessage(`New ${assetType} post drafted!`);
            }
            dispatch(addPost({ type: "ADD", post: addPostData }));

            setSubmitDisable(false);
          }
        } catch (error) {
          const { message, error: err } = error;

          if (message) {
            setErrorMessage(message);
          } else if (err) {
            setErrorMessage(err);
          } else {
            setErrorMessage("Something went wrong. Please try again later.");
          }

          setSubmitDisable(false);
        }
      }
      onClose();
      handleClearState();
    }
  };

  const handleClearState = () => {
    setPostData({
      postInstruction: "",
      postDesc: "",
      twitterDesc: "",
      postDate: "",
      postTime: "",
      isAdvanceActivated: advanceSetting,
      facebookDate: "",
      facebookTime: "",
      instagramTime: "",
      instagramDate: "",
      twitterTime: "",
      twitterDate: "",
      linkedinDate: "",
      ctaType: "",
      ctaURL: "",
      tagPeople: "",
      tagPlace: "",
      linkedinTime: "",
    });
    setSelectedStrategy("");
    setFiles([]);
    setSubmitDisable(false);
  };
  const handleSaveAsDraft = (event) => {
    event.preventDefault();

    if (
      postData.postDate === "" ||
      postData.postTime === "" ||
      Object.keys(selectedStrategy).length === 0
    ) {
      setErrorMessage("fields can not be empty");
    } else {
      dispatch(
        addPost({
          postID: uuid(),
          postDetails: {
            postDate: postData.postDate,
            postTime: postData.postTime,
            postDesc: postData.postDesc,
            postImage: files[0],
          },

          event: {
            day: formatDate(postData.postDate),
            strategy: selectedStrategy,
            isDraft: true,
          },
        })
      );
    }
    handleClearState();
    onClose();
  };
  const handleInputChange = (field, value) => {
    if (field === "postTime") {
      return setPostData((prevState) => ({
        ...prevState,
        [field]: moment(value).format("HH:mm"),
      }));
    }
    return setPostData((prevState) => ({
      ...prevState,
      [field]: value,
    }));
  };

  const handleStrategySelect = async (s) => {
    const { label, color, description, id } = s;
    setSelectedStrategy({
      label,
      color,
      desc: description,
      id: id,
    });

    //setSelectedStrategy(id)

    setShowDropDown(false);
  };

  const handleInputChangeSpecial = (field, value) => {
    return setPostData((prevState) => ({
      ...prevState,
      [field]: value,
    }));
  };

  const handleClose = () => {
    onClose();
    handleClearState();
  };

  useEffect(() => {
    if (files.length) {
      // validate post asset
      const { error, isAssetValid, type } = validatePostAsset(files[0]);

      if (error) {
        setSubmitDisable(false);
        // handleClose();
        setFiles([]);
        return setErrorMessage(error);
      } else if (isAssetValid) {
        setSubmitDisable(false);
        setAssetType(type);
      }
    }
  }, [files]);

  const newHandleStrategySelect = async (id) => {
    setSelectedStrategy(id);
  };

  return (
    <Drawer
      anchor={"right"}
      hideBackdrop={hideBackdrop}
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") && onClose()
      }
      className={classes["new-post"]}
      sx={{ zIndex: "calc(1200 + 100)" }}
    >
      <div className="wrapper">
        <div className="header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            onClick={handleClose}
            style={{ color: "transparent", padding: 0 }}
            disableFocusRipple={true}
            disableRipple={true}
          >
            <CustomIcon.BackBtnIcon />
          </IconButton>
          <h1 className="title">{isEdit ? "Edit Post" : "Create a Post"}</h1>
        </div>
        <form onSubmit={(event) => handleFinalSubmit(event, "create")}>
          <div className="field">
            <Autocomplete
              disableClearable={true}
              PopperComponent={StyledPopper}
              options={allStrategies}
              value={selectedStrategy.label}
              inputValue={selectedStrategy.label}
              onChange={(event, value) => handleStrategySelect(value)}
              renderInput={(params) => (
                <CustomTextFieldFilled
                  {...params}
                  label="Select Strategy"
                  type="text"
                  name="postTime"
                />
              )}
              renderOption={(props, option) => (
                <MenuItem {...props} value={selectedStrategy.label}>
                  <h5
                    className="option-label"
                    style={{
                      ...(option.color
                        ? {
                            borderLeft: `4px solid ${option.color}`,
                            paddingLeft: "4px",
                          }
                        : {}),
                    }}
                  >
                    {option.label}
                  </h5>
                  <div className="option-attr">
                    {option.type && (
                      <h6 className="option-sub-label">{option.type}</h6>
                    )}
                    {/* {option.platforms && ( */}
                    <div className="option-platforms">
                      <PlatformIcons strategyType={option.type} />
                    </div>
                    {/*                     )}
                     */}{" "}
                  </div>
                  {option.description && (
                    <p className="option-desc">{option.description}</p>
                  )}
                </MenuItem>
              )}
            />
          </div>
          <div className="field">
            <CustomTextFieldFilled
              select
              label="Post Type"
              placeholder="Post Type"
              value={postData.postType}
              onChange={({ target: { value } }) =>
                handleInputChange("postType", value)
              }
              variant="filled"
              type="select"
              inputProps={{ "aria-label": "Without label" }}
              fullWidth
            >
              <MenuItem value={""} disabled>
                {"Select Strategy Type"}
              </MenuItem>
              {[
                { label: "Image or Video Creative", value: "image_video" },
                { label: "Text Post", value: "text" },
                { label: "Album", value: "album" },
                { label: "Facebook Carousel", value: "facebook_carousel" },
                { label: "Cover Picture", value: "cover_picture" },
              ].map(({ label, value }, index) => (
                <MenuItem key={index} value={value}>
                  {label}
                </MenuItem>
              ))}
            </CustomTextFieldFilled>
            {postData.postType !== "" && (
              <div className="append">
                <h5 className="label">{"can be scheduled on"}</h5>
                <ul className="platforms">
                  <li className="platform">
                    <img src={Facebook} alt="facebook icon" />
                  </li>
                  <li className="platform">
                    <img src={Instagram} alt="instagram-icon" />
                  </li>
                  <li className="platform">
                    <img src={Twitter} alt="twitter-icon" />
                  </li>

                  <li className="platform">
                    <img src={Linkedin} alt="linkedin-icon" />
                  </li>
                </ul>
              </div>
            )}
          </div>
          <div className="field flex">
            {/* <h4 className="field-title">{"Add a briefing"}</h4> 
            <Switch
              classes={{
                root: classes.root,
                switchBase: classes.switchBase,
                thumb: classes.thumb,
                track: classes.track,
                checked: classes.checked,
              }}
              onChange={() => toggleBriefing(!addBriefing)}
              checked={addBriefing}
            />
            */}
          </div>
          {addBriefing && (
            <div className="field">
              <CustomInputField
                label="Concept, Design and Content Brief"
                placeholder="Concept, Design and Content Brief"
                rows={3}
                multiline
              />
            </div>
          )}
          {addBriefing && (
            <div className="field">
              <CustomInputField
                label="Image Copy Ideation"
                placeholder="Image Copy Ideation"
                rows={3}
                multiline
              />
            </div>
          )}
          {addBriefing && (
            <div className="field">
              <CustomInputField
                label="Post Copy Ideation"
                placeholder="Post Copy Ideation"
                rows={3}
                multiline
              />
            </div>
          )}
          {addBriefing && (
            <div className="field flex reverse">
              <Link
                component="button"
                type="button"
                underline="always"
                onClick={() => setRefURLCount((pC) => (pC < 5 ? pC + 1 : pC))}
                disabled={refURLCount === 5}
              >
                {"Add Reference URL"}
              </Link>
            </div>
          )}
          {addBriefing && refURLCount > 0 && (
            <Fragment>
              {Array.from({ length: refURLCount }, (_, i) => i + 1).map(
                (val) => (
                  <div className="field" key={val}>
                    <CustomInputField
                      label={`Reference URL ${val}`}
                      placeholder={`Reference URL ${val}`}
                    />
                  </div>
                )
              )}
            </Fragment>
          )}
          {!addBriefing && (
            <div className="field">
              <Drag files={files} setFiles={setFiles} />
            </div>
          )}
          {!addBriefing && (
            <div className="field">
              <CustomInputField
                label="Add Description"
                placeholder="Add Description"
                rows={3}
                value={postData.postDesc}
                onChange={(e) => handleInputChange("postDesc", e.target.value)}
                multiline
                required
              />
              {/* Twitter text limit error */}
              {postData.postDesc?.length >= 280 && (
                <div className="append">
                  <div className="label-error">
                    <img src={Warn} alt="warn" />
                    <h5 className="label">{`Word limit ${postData.postDesc.length}/280`}</h5>
                  </div>
                  <Link
                    component="button"
                    underline="always"
                    className="edit-twitter"
                    onClick={showTwitterButtonHandler}
                  >
                    {"Edit Twitter Text"}
                  </Link>
                </div>
              )}
            </div>
          )}
          {/* If twitter text limit reached */}
          {showTwitterInputBox && (
            <div className="field">
              <CustomInputField
                label="Twitter Description"
                placeholder="Twitter Description"
                rows={3}
                multiline
                value={postData.twitterDesc}
                onChange={(e) =>
                  handleInputChangeSpecial("twitterDesc", e.target.value)
                }
              />
            </div>
          )}
{/*           <h6 className="posted-at">
            {"To be Posted at:"} {JSON.stringify(postDate)}
          </h6> */}
          <div className="field">
            <LocalizationProvider dateAdapter={AdapterDateFns}>
              <DatePicker
                label="Schedule Date"
                mask="__-__-____"
                inputFormat="dd-MM-yyyy"
                renderInput={(props) => (
                  <CustomTextFieldFilled
                    type="text"
                    name="postDate"
                    {...props}
                  />
                )}
                value={postDate}
                minDate={moment().toDate()}
                onChange={(value) => setPostDate(value)}
              />
            </LocalizationProvider>
          </div>
          <div className="field">
            <LocalizationProvider dateAdapter={AdapterDateFns}>
              <TimePicker
                label="Schedule Time"
                renderInput={(props) => (
                  <CustomTextFieldFilled
                    type="text"
                    name="postTime"
                    {...props}
                  />
                )}
                minTime={moment().toDate()}
                value={postTime}
                onChange={(value) => setPostTime(value)}
              />
            </LocalizationProvider>
          </div>
          {false && !addBriefing && (
            <div className="field">
              <div className="advanceSettingWrapper">
                <div
                  className="advanceTitleWrap"
                  onClick={() => setAdvanceSetting(true)}
                >
                  <p className="advanceTitle">Advance Social media settings</p>
                  <img
                    src={Triangle}
                    alt="triangle"
                    className="advance-triangle"
                  />
                </div>
                {(postData.facebookDate ||
                  postData.facebookTime ||
                  postData.instagramDate ||
                  postData.instagramTime ||
                  postData.twitterDate ||
                  postData.twitterTime) && (
                  <div className="custom-title">
                    Custom settings applied on:
                  </div>
                )}
                <div className="social-media-icons">
                  {(postData.facebookDate || postData.facebookTime) && (
                    <img src={Facebook} alt="facebook icon" />
                  )}
                  {(postData.instagramDate || postData.instagramTime) && (
                    <img src={Instagram} alt="instagram-icon" />
                  )}
                  {(postData.twitterDate || postData.twitterTime) && (
                    <img src={Twitter} alt="twitter-icon" />
                  )}
                  {(postData.linkedinDate || postData.linkedinTime) && (
                    <img
                      src={Linkedin}
                      alt="linkedin-icon"
                      style={{ width: "5%" }}
                    />
                  )}
                </div>
              </div>
            </div>
          )}
          {!addBriefing && (
            <div className="actions">
              <Button
                variant="contained"
                type="submit"
                disabled={submitDisable}
              >
                {"Submit the post"}
              </Button>
            </div>
          )}
          {addBriefing && (
            <div className="actions">
              <Button variant="outlined" type="button">
                {"Continue"}
              </Button>
              <Button variant="contained" type="button">
                {"Save for later"}
              </Button>
            </div>
          )}
        </form>
      </div>
      <AdvanceSettingCreate
        open={advanceSetting}
        onClose={() => setAdvanceSetting(false)}
        handleInputChange={handleInputChange}
        postData={postData}
        setPostData={setPostData}
      />
    </Drawer>
  );
};

export default PostCreate;
