import React from "react";
import Slide from "@material-ui/core/Slide";
import { useDispatch } from "react-redux";
import Dialog from "@material-ui/core/Dialog";

import DialogContent from "@material-ui/core/DialogContent";
import Facebook from "../../../../../../assets/icons/facebook.svg";
import Instagram from "../../../../../../assets/icons/instagram.svg";
import Twitter from "../../../../../../assets/icons/twitter.svg";

import { deletePost } from "../../../../../../Store/calendarDashboard/posts/postSlice";

// styles
import useStyles from "../../../styles/muiStyles";

// request helpers imports
import {
  DeleteScheduledPost,
  deletePlatformPosts,
  isPlatformPosted,
} from "../../../../../../api/Posts";

// contexts
import { useToastMessage } from "../../../../../../contexts/ToastMessageContext";

const Transition = React.forwardRef(function Transition (props, ref) {
  return <Slide direction='up' ref={ref} {...props} />;
});

export default function PostDelete ({
  postID,
  eventImage,
  open,
  onClose,
  day,
  postDetails,
  eventDesc,
  isFacebook,
  isInstagram,
  isLinkedin,
  isTwitter,
  imgURL,
}) {
  const classes = useStyles();
  const eventDescToDisplay =
    eventDesc.length > 18 ? `${eventDesc.substr(0, 18)} ...` : eventDesc;

  const dispatch = useDispatch();

  const { setErrorMessage, setSuccessMessage } = useToastMessage();

  const handlePostDelete = async () => {
    // try {
    //const { isPosted } = await isPlatformPosted(postID);
    // if (!isPosted) {
    // const businessID = "6064538f2f94727c34a4d6a9";
    // await DeleteScheduledPost(businessID, postID);
    try {
      const res = await DeleteScheduledPost(postID);
      if (!res.success) {
        setErrorMessage(res.message);
      } else {
        dispatch(
          deletePost({
            postID: postID,
          })
        );
        setSuccessMessage(res.message); 
      }
    } catch (err) {
      setErrorMessage(err.message);
    }
    // } else {
    //   // delete platform post
    //   await deletePlatformPosts(postID, ["fb", "tw", "li"]);

    //   setSuccessMessage("Post deleted from platforms!");
    // }
    // } catch (error) {
    //   console.log("Delete post error: ", error);

    //   const { message, error: err } = error;

    //   if (message) {
    //     setErrorMessage(message);
    //   } else if (err) {
    //     setErrorMessage(err);
    //   } else {
    //     setErrorMessage(error);
    //   }
    // }
  };

  return (
    <div>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={onClose}
        aria-labelledby='alert-dialog-slide-title'
        aria-describedby='alert-dialog-slide-description'
      >
        <DialogContent className={classes.deletePostWrapper}>
          <div className='cardImage'>
            {imgURL && <img src={imgURL} alt='post' className='postImage' />}
          </div>
          <div className='cardDescription'>
            <div className='cardTitle'>{eventDescToDisplay}</div>
            <div className='cardSubTitle'>
              {day}
              {" @ "}
              {postDetails.postTime}
            </div>
          </div>

          <div className='cardSocialWrapper'>
            <div className='social-icons-posted'>
              {isFacebook && (
                <img
                  src={Facebook}
                  className='socialIcons'
                  alt='social-icons'
                />
              )}
              {isInstagram && (
                <img
                  src={Instagram}
                  className='socialIcons'
                  alt='social-icons'
                />
              )}
              {isTwitter && (
                <img src={Twitter} className='socialIcons' alt='social-icons' />
              )}
            </div>
            <div>
              {isFacebook && isInstagram && isTwitter && isLinkedin && (
                <div className='plusIconText'>+1</div>
              )}
            </div>
          </div>
          <hr />

          <div className='footer'>
            <div className='footerText'>
              {"Are you sure,you want to delete the post?"}
            </div>
          </div>
          <div className='footerButton'>
            <button className='deletePostButton' onClick={handlePostDelete}>
              {"Delete Now"}
            </button>
            <button className='cancelPostButton' onClick={onClose}>
              {"Cancel"}
            </button>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
}
