import clsx from "clsx";
import React from "react";
import { useHistory } from "react-router-dom";
import { Backdrop } from "../../../../Components/exports";
import { useSidebar } from "../../../../contexts/SidebarProvider/SidebarProvider";
import Header from "./Header/Header";
import classes from "./Sidebar.module.css";
import parse from "html-react-parser";
import Footer from "./Footer/Footer";
import CheckSvg from "../../../../assets/navbar/check.svg";

const Sidebar = ({ closed, open, selectedBusiness }) => {
  const history = useHistory();
  let attachedClasses = [classes.Sidebar, classes.Close];

  if (open) {
    attachedClasses = [classes.Sidebar, classes.Open];
  }

  const { activePlans, menuCategory, businessId } = useSidebar();

  const urlSplit = window.location.pathname.split("/");

  const productName = urlSplit[1];

  const planCategory = activePlans.map(({ product }) => product);

  const switchWorkSpace = (catName) => {
    if (catName !== productName) {
      let switchPath;
      if (catName === "engage") {
        switchPath = "/engage/chat";
      }
      if (catName === "curate") {
        switchPath = "/curate/calendar";
      }
      history.push({
        pathname: switchPath,
      });
    }
  };

  return (
    <>
      <Backdrop zIndex="111" show={open} clicked={closed} />
      <div className={attachedClasses.join(" ")}>
        <Header selectedBusiness={selectedBusiness} />

        <div className={classes.innerContent}>
          <h2>Choose Workspace</h2>

          {menuCategory &&
            planCategory &&
            menuCategory.map((cat, index) =>
              planCategory.includes(cat.name) ? (
                <div
                  key={index}
                  className={clsx(
                    classes.workspaceBtn,
                    productName === cat.productName && classes.active
                  )}
                  onClick={() => switchWorkSpace(cat.productName)}
                >
                  <div className={classes.workspaceLink}>
                    {parse(cat.icon)}

                    <label style={{ color: cat.color }}>
                      {cat.productName}
                    </label>

                    {productName === cat.productName && (
                      <img src={CheckSvg} alt="check" />
                    )}
                  </div>
                  <p>{cat.description}</p>
                </div>
              ) : null
            )}
        </div>

        <Footer />
      </div>
    </>
  );
};

export default Sidebar;
