import React, { useState, useEffect } from "react";
import {
  Button,
  Drawer,
  IconButton,
  List,
  FormControlLabel,
  Checkbox,
} from "@mui/material";
import { useSelector } from "react-redux";

import StrategyCard from "./StrategyCard";
import StrategyCreate from "./StrategyCreate";

import leftArrow from "../../../assets/icons/leftArrow.svg";

// styles
import useStyles from "./styles/muiStyles";

// react-redux imports
import { useDispatch } from "react-redux";

// slice imports
import { updateCheckBoxAll } from "../../../Store/calendarDashboard/strategy/strategySlice.js";

const Strategy = ({ open, onClose }) => {
  const classes = useStyles();
  const { strategyList } = useSelector((state) => state.strategyState);
  const [createStrategy, toggleCreateStrategy] = React.useState(false);
  const [toggleAll, setToggleAll] = useState(true);

  const dispatch = useDispatch();

  const createStrategyHandler = () => {
    toggleCreateStrategy(true);
  };


  useEffect(() => {
    dispatch(updateCheckBoxAll({ check: toggleAll }));
  }, [toggleAll, dispatch]);

  return (
    <Drawer
      anchor={"right"}
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") && onClose()
      }
      className={classes.strategy}
    >
      <div className="wrapper">
        <div className="header">
          <div className="left">
            <IconButton
              aria-label="Back"
              className="back-btn"
              onClick={() => {
                onClose();
              }}
              style={{ color: "transparent", padding: 0 }}
              disableFocusRipple={true}
              disableRipple={true}
            >
              <img
                alt="back"
                src={leftArrow}
                style={{ margin: "7.5px 10px" }}
              />
            </IconButton>
            <h1 className="title">{"Strategies"}</h1>
          </div>
          <div className="right">
            <Button
              aria-label="New strategy"
              variant="outlined"
              onClick={createStrategyHandler}
            >
              {"New strategy"}
            </Button>
          </div>
        </div>
        <div className="body">
          <div className="strategy-head">
            <FormControlLabel
              control={
                <Checkbox
                  sx={{
                    color: "#12B0EE",

                    "&.Mui-checked": {
                      color: "#12B0EE",
                    },
                  }}
                  checked={toggleAll}
                  onClick={() => setToggleAll(!toggleAll)}
                />
              }
              label="Show all on calendar"
              sx={{ ml: 0 }}
            />
          </div>
          <div className="strategy-body">
            <List>
              {strategyList.map((s, index) => (
                <StrategyCard
                  key={index}
                  name={s.name}
                  desc={s.desc}
                  color={s.color}
                  strategyID={s.strategyID || s.id}
                  isDefault={s.isDefault}
                  strategyDetails={s}
                />
              ))}
            </List>
          </div>
        </div>
      </div>
      <StrategyCreate
        open={createStrategy}
        onClose={() => toggleCreateStrategy(false)}
      />
    </Drawer>
  );
};

export default Strategy;
