import React, { useState, useEffect } from "react";
import { Link, Button, IconButton } from "@mui/material";
import { useSelector } from "react-redux";

import LeftArrow from "../../../../../assets/icons/leftArrow.svg";
import RightArrow from "../../../../../assets/icons/rightArrow.svg";
import EyeIcon from "../../../../../assets/icons/eye.svg";
import EyeOffIcon from "../../../../../assets/icons/eyeoff.svg";
import StarIcon from "../../../../../assets/icons/GrayStar.svg";
import StrategyIcon from "../../../assets/strategy-icon.svg";
import EventIcon from "../../../assets/events-icon.svg";

import Strategy from "../../../Strategy";
import ShowPost from "../../../ShowPost";
import ViewEvent from "../../../ViewEvent";

const Header = ({ onNext, onBack, dateDisplay, setCreatePost }) => {
  const [showEvents, setShowEvents] = useState(false);
  const [showStrategy, setShowStrategy] = useState(false);
  const [eyeOpenIcon, setEyeOpenIcon] = useState(true);
  const strategyState = useSelector((state) => state.strategyState);
  const [showPost, setShowPost] = useState(false);

  useEffect(() => {
    let allSetToTrue = true;
    if (strategyState.strategyStatus) {
      for (const value of Object.keys(strategyState.strategyStatus)) {
        if (!strategyState.strategyStatus[value]) {
          allSetToTrue = false;
        }
      }
    }
    setEyeOpenIcon(allSetToTrue);
  }, [strategyState]);

  return (
    <div className="date-bar">
      <div className="header">
        <IconButton
          onClick={onBack}
          disableFocusRipple={true}
          disableRipple={true}
        >
          <img src={LeftArrow} alt="left" />
        </IconButton>
        <h3 className="date-title">{dateDisplay}</h3>
        <IconButton
          onClick={onNext}
          disableFocusRipple={true}
          disableRipple={true}
        >
          <img src={RightArrow} alt="right" />
        </IconButton>
      </div>
      <div className="actions">
        {!false && (
          <Link
            component="button"
            underline="none"
            onClick={() => setShowStrategy(true)}
          >
            <img src={StrategyIcon} alt="strategy-icon" className="icon" />
            <span className="label">{"Strategy"}</span>
          </Link>
        )}
        {!false && (
          <Link
            component="button"
            underline="none"
            onClick={() => setShowEvents(true)}
          >
            <img src={EventIcon} alt="event-icon" className="icon" />
            <span className="label">{"Events"}</span>
          </Link>
        )}
        <Button
          type="button"
          variant="outlined"
          onClick={() => setCreatePost(true)}
        >
          {"Create Post"}
        </Button>
      </div>
      <Strategy open={showStrategy} onClose={() => setShowStrategy(false)} />
      <ViewEvent open={showEvents} onClose={() => setShowEvents(false)} />
      <ShowPost open={showPost} onClose={() => setShowPost(false)} />
    </div>
  );
};

export default Header;
