import classes from "./calendarBody.module.css";
import GrayStar from "../../../../assets/icons/GrayStar.svg";
import GoldStar from "../../../../assets/icons/star.svg";
import { useSelector } from "react-redux";
import Post from "../../Post/post";

export default function CalendarBody({ days }) {
  const strategyState = useSelector((state) => state.strategyState);

  let countDays = 0;
  const weekDay = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];

  return (
    <div>
      {days.map((d) => {
        let dayValue;

        if (d.value) {
          if (String(d.value).length === 1) {
            dayValue = "0" + d.value;
          } else if (String(d.value).length > 1) {
            dayValue = d.value;
          } else {
            dayValue = "";
          }
        }

        if (countDays === 6) {
          countDays = 0;
        }
        countDays++;
        if (d.isPadding) {
          return null;
        } else {
          return (
            <div className={classes.calendarDayWrapper}>
              <div
                className={
                  d.isPadding
                    ? classes.weekDayWrapperDeactivate
                    : classes.weekDayWrapper
                }
              >
                <div>
                  {d.globalEvent.length > 0 && d.globalEvent[0].isGlobal && (
                    <img src={GrayStar} alt="global event indicator" />
                  )}

                  {d.globalEvent.length > 0 && d.globalEvent[0].isManual && (
                    <img src={GoldStar} alt="User important event indicator" />
                  )}
                </div>
                <div>{dayValue}</div>
                <div> {weekDay[countDays - 1]}</div>
              </div>
              <div className={classes.postAreaWrapper}>
                {d.event.length > 0 &&
                  d.event.map(
                    (e) =>
                      strategyState.strategyStatus[e.event.strategy.name] && (
                        <Post
                          isMobile
                          postID={e.postID}
                          eventTitle={e.event.strategy.name}
                          eventColor={e.event.strategy.color}
                          eventImage={e.postDetails.postImage}
                          isDraft={e.event.isDraft}
                          eventDesc={e.postDetails.postDesc}
                          isLinkedin={
                            e.postDetails.linkedinDate ||
                            e.postDetails.linkedinTime
                          }
                          isFacebook={
                            e.postDetails.facebookDate ||
                            e.postDetails.facebookTime
                          }
                          isInstagram={
                            e.postDetails.instagramDate ||
                            e.postDetails.instagramTime
                          }
                          isTwitter={
                            e.postDetails.twitterDate ||
                            e.postDetails.twitterTime
                          }
                          postDetails={e.postDetails}
                          eventSubTitle={
                            e.event.isDraft
                              ? `Drafted @ ${e.postDetails.postTime}`
                              : `Posted @ ${e.postDetails.postTime}`
                          }
                        />
                      )
                  )}
              </div>
            </div>
          );
        }
      })}
    </div>
  );
}
