import React, { useState, useRef } from "react";
import { Menu, MenuItem, IconButton } from "@mui/material";
import { MoreVert } from "@mui/icons-material";

import Facebook from "../../../assets/fb.svg";
import Instagram from "../../../assets/insta.svg";
import InfoIconLocal from "../../../../../assets/icons/infoIcon.svg";
import LinkedIn from "../../../assets/LIn.svg";
import Twitter from "../../../assets/tweet.svg";
import {
  Edit,
  DeleteOutlineOutlined,
  MailOutline,
  VisibilityOutlined,
} from "@mui/icons-material";
//import PostCreate from "../../../PostCreate/postCreate";
import PostCreate from "../../../PostCreate";
import ViewPost from "../../../ViewPost/viewPost";
import DeletePost from "./PostDelete";
import DoneIcon from "../../../assets/done-icon.svg";
import ScheduleIcon from "../../../assets/schedule-icon.svg";
// styles
import useStyles from "../../styles/muiStyles";
import { current } from "@reduxjs/toolkit";
import moment from "moment";

export default function Posts({
  isMobile = false,
  day,
  postID,
  eventSubTitle,
  eventTitle,
  eventDesc = "",
  eventColor,
  eventImage = [],
  isFacebook,
  isTwitter,
  isInstagram,
  isLinkedin,
  isDraft,
  postDetails,
  imgURL,
  post,
}) {

 
  const classes = useStyles();
  const [editPost, setEditPost] = useState(false);
  const [viewPost, setViewPost] = useState(false);
  const [deletePost, setDeletePost] = useState(false);
  const [mouseOver, setMouseOver] = useState(false);
  const wrapperRef = useRef();

  // 3dot menu state
  const [anchorEl, setAnchorEl] = useState(null);
  //function for opening 3 dot menu
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  //function for closing 3 dot menu
  const handleClose = () => {
    setAnchorEl(null);
  };
  const HoverComponent = () => {
    return (
      <div className="hover-container" onMouseLeave={() => setMouseOver(false)}>
        <div className="hover-buttons">
          <MailOutline />
          <div className="hover-button-text">{"New Message"}</div>
        </div>
      </div>
    );
  };
  const eventDescToDisplay =
    eventDesc.length > 18 ? `${eventDesc.substr(0, 18)} ...` : eventDesc;
  const data = {
    ...postDetails,
  };

  return (
    <div
      className="eventParentWrapper"
      ref={wrapperRef}
      onMouseEnter={() => {
        setMouseOver(true);
        wrapperRef.current.style.boxShadow = `1px 1px 8px ${eventColor.value}80`;
      }}
      onMouseLeave={() => {
        setMouseOver(false);
        wrapperRef.current.style.boxShadow = `none`;
      }}
      onClick={() => setMouseOver(true)}
      style={{
        borderLeftColor: eventColor.value,
      }}
    >
      <div className="eventWrapper">
        {!isMobile && (
          <div className="eventTitleWrapper">
            <h6 className={isMobile ? "eventTitleMobile" : "eventTitle"}>
              {eventTitle}
            </h6>
            {isDraft && <img src={InfoIconLocal} alt="info" />}
            <IconButton
              onClick={handleClick}
              disableFocusRipple={true}
              disableRipple={true}
              sx={{ p: 0 }}
            >
              <MoreVert />
            </IconButton>
            {Boolean(anchorEl) && (
              <Menu
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={handleClose}
                keepMounted
                MenuListProps={{
                  "aria-labelledby": "Options",
                }}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                className={classes["item-options"]}
              >
                {isDraft && (
                  <MenuItem
                    onClick={() => {
                      setEditPost(true);
                      setMouseOver(false);
                      handleClose();
                    }}
                    style={{ alignItems: "flex-start", gap: "5px" }}
                  >
                    <Edit style={{ fontSize: "1.25rem" }} />
                    <span className="label">{"Edit"}</span>
                  </MenuItem>
                )}
                <MenuItem
                  onClick={() => {
                    setViewPost(true);
                    setMouseOver(false);
                    handleClose();
                  }}
                  style={{ alignItems: "flex-start", gap: "5px" }}
                >
                  <VisibilityOutlined style={{ fontSize: "1.25rem" }} />
                  <span className="label">{"View"}</span>
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    setDeletePost(true);
                    setMouseOver(false);
                    handleClose();
                  }}
                  style={{ alignItems: "flex-start", gap: "5px" }}
                >
                  <DeleteOutlineOutlined style={{ fontSize: "1.25rem" }} />
                  <span className="label">{"Delete"}</span>
                </MenuItem>
              </Menu>
            )}
          </div>
        )}
        <div className="post-desc">{postDetails.postDesc ?? postDetails.content}</div>
        <div
          className={
            Array.isArray(eventImage)
              ? !isDraft
                ? "post-wrapper-with-gap"
                : "post-wrapper-without-gap"
              : isMobile
              ? "post-image-wrapper-expand"
              : "post-image-wrapper"
          }
        >
          <div className="post-image">
            {imgURL && (
              <img
                src={imgURL}
                alt="event"
                className={isMobile ? "event-image" : "event-image-md"}
              />
            )}
          </div>
          {/* {mouseOver && <HoverComponent />} */}
          {false && (
            <div className="post-right-part-wrapper">
              {isMobile && (
                <div
                  className="eventTitleWrapper"
                  style={{ marginBottom: "5px" }}
                >
                  <h6 className="eventTitle">{eventTitle}</h6>
                  {isDraft && <img src={InfoIconLocal} alt="info icon" />}
                </div>
              )}
              {isMobile && (
                <div
                  className="eventSubTitleWrapper"
                  style={{ marginTop: "2px" }}
                >
                  <div className="eventSubTitle">{eventSubTitle}</div>

                  {!isDraft && <img src={DoneIcon} alt="DoneIcon" />}
                </div>
              )}
            </div>
          )}
        </div>
        {!isMobile && (
          <div className="eventSubTitleWrapper">
            <div className="social-available">
              <div className="social-icons-posted">
                {isFacebook && (
                  <img
                    src={Facebook}
                    className="social-av-icons"
                    alt="social-icons"
                  />
                )}
                {isInstagram && (
                  <img
                    src={Instagram}
                    className="social-av-icons"
                    alt="social-icons"
                  />
                )}
                {isTwitter && (
                  <img
                    src={Twitter}
                    className="social-av-icons"
                    alt="social-icons"
                  />
                )}
                {isLinkedin && (
                  <img
                    src={LinkedIn}
                    className="social-av-icons"
                    alt="social-icons"
                  />
                )}
              </div>
            </div>
            <div
              className={`${
                postDetails.showSuccessIcon
                  ? "eventSubTitle"
                  : "eventSubTitle half-tone"
              }`}
            >
              {moment(post.schedule_date_time).local().format("hh:mm A")}
            </div>
            {/* {postDetails.showSuccessIcon ? (
              <img src={DoneIcon} alt="DoneIcon" />
            ) : (
              <img src={ScheduleIcon} alt="ScheduleIcon" />
            )} */}
          </div>
        )}
      </div>
      <PostCreate
        strategy={eventTitle}
        isPopulated
        isEdit
        eventImage={eventImage}
        currentPostID={postID}
        populatingData={data}
        open={editPost}
        onClose={() => setEditPost(false)}
      />
      <ViewPost
        open={viewPost}
        onClose={() => setViewPost(false)}
        day={day}
        eventImage={eventImage}
        isFacebook={isFacebook}
        isInstagram={isInstagram}
        isTwitter={isTwitter}
        isLinkedin={isLinkedin}
        postDetails={postDetails}
        imgURL={imgURL}
        post={post}
      />
      <DeletePost
        open={deletePost}
        postID={postID}
        onClose={() => setDeletePost(false)}
        day={day}
        eventImage={eventImage}
        postDetails={postDetails}
        eventDesc={eventDesc}
        isFacebook={isFacebook}
        isInstagram={isInstagram}
        isTwitter={isTwitter}
        isLinkedin={isLinkedin}
        imgURL={imgURL}
      />
    </div>
  );
}
