import { useMemo } from "react";
import classes from "./drag.module.css";
import { useDropzone } from "react-dropzone";

const baseStyle = {
  flex: 1,
  display: "flex",
  boxSizing: "borderBox",
  borderRadius: "8px",
  margin: "0 auto 16px",
  alignItems: "center",
  textAlign: "center",
  cursor: "pointer",
  height: "244px",
  verticalHeight: "244px",
  width: "244px",
  padding: "20px",

  borderColor: "#B8BAC2",
  borderStyle: "dashed",
  backgroundColor: " #F2F3F7",
  color: "#000000",

  opacity: "0.5",
  outline: "none",
  transition: "border .24s ease-in-out",
};

const activeStyle = {
  borderColor: "#2196f3",
};

const acceptStyle = {
  borderColor: "#00e676",
};

const rejectStyle = {
  borderColor: "#ff1744",
};

// const img = {
//   display: "block",
//   width: "244px",
//   marginLeft: "10px",
//   height: "244px",
// };

function StyledDropzone({ files, setFiles }) {
  const {
    getRootProps,
    getInputProps,
    isDragActive,
    isDragAccept,
    isDragReject,
    open,
  } = useDropzone({
    // accept: "image/*",
    noClick: true,
    noKeyboard: true,
    onDrop: (acceptedFiles) => {
      setFiles(
        acceptedFiles.map((file) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        )
      );
    },
  });

  const style = useMemo(
    () => ({
      ...baseStyle,
      ...(isDragActive ? activeStyle : {}),
      ...(isDragAccept ? acceptStyle : {}),
      ...(isDragReject ? rejectStyle : {}),
    }),
    [isDragAccept, isDragActive, isDragReject]
  );

  // const filepath = acceptedFiles.map((file) => (
  //   <li key={file.path}>
  //     {file.path} - {file.size} bytes
  //   </li>
  // ));

  return (
    <>
      {files.length === 0 ? (
        <div className="container" onClick={open}>
          <div {...getRootProps({ style })}>
            <input {...getInputProps()} />
            <div className={classes["input-text"]}>Upload social post</div>
          </div>
        </div>
      ) : (
        <>
          {files.map((file) => {
            const type = file.type.split("/").shift();

            if (type === "image") {
              return (
                <img
                  src={file.preview}
                  className={classes["image-preview"]}
                  alt="preview"
                />
              );
            }

            if (type === "video") {
              return (
                <div style={{ padding: 10 }}>
                  <video autoPlay controls style={{ maxWidth: "100%" }}>
                    <source src={file.preview} type="video/mp4" />
                  </video>
                </div>
              );
            }
          })}
          {/* {files.map((file) => (
            <img
              src={file.preview}
              className={classes["image-preview"]}
              alt="preview"
            />
          ))} */}
        </>
      )}
    </>
  );
}

export default StyledDropzone;
